
import React from 'react'
const OnsiteLogo = (props) => {
    const { wall } = props
    return wall.UgcSettings && wall.UgcSettings.onsite_logo && wall.UgcSettings.onsite_logo != "" ? <div className='popup_logo'>
        <img src={wall.UgcSettings.onsite_logo} className='popup_logo_img' width="200" height="45" alt='logo' />
    </div> : null
}

export default OnsiteLogo