import React, { PureComponent, Fragment, Suspense, memo, useRef, useState } from "react"

import './assets/css/falling-columns-style.css'
import { CONVERT_OBJ_TO_ARRAY } from '../../../utils'

import SwiperFalling from './SwiperFalling'

class FallingColumnsSwiper extends PureComponent {
    state = {
        intialize: false,
        columns: [],
        activeID: null,
        data: []
    }
    componentWillMount() {
        this.onLoadData()
    }
    onLoadData = () => {
        let { newApi, newItems } = this.props;
        let columns = [];
        if (newItems && newItems.length > 0) {
            newItems.map((item, index) => {
                columns.push({ id: index, referenceData: null });
            })
            this.setState({ columns, data: newItems })
        }
    }
    componentDidMount = () => {
        setInterval(() => this.onUpdateTimeInterVal(), 5000);
    }
    onUpdateTimeInterVal = () => {
        const { activeID, columns } = this.state;
        if (activeID || activeID == 0) {
            let ID = parseInt(activeID) + 1;
            if (ID >= columns.length) {
                ID = 0
            }
            if (columns[ID]) {
                this.setState({ activeID: ID })
            }
        }
        else {
            let ID = 0;
            if (columns[ID]) {
                this.setState({ activeID: ID })
            }
        }



    }
    onUpdateRef = (index, refeData) => {
        let { columns } = this.state;
        if (columns && columns.length > 0 && columns[index])
            columns[index].referenceData = refeData;

        this.setState({ columns })
    }
    componentDidUpdate(prevProps, prevState) {
        if (this.props.newItems !== prevState.data) {
            this.onLoadData()
            setTimeout(() => window.dispatchEvent(new Event('resize')), 1000)
        }
    }

    render() {
        let { newApi, newItems } = this.props;
        const { intialize, columns, activeID } = this.state;
        //console.log("test faaling",newItems)
        return (
            <Fragment>
                <div id="falling-container" className="tb_wall_wrap__">
                    {
                        (newItems && newItems.length > 0) ? newItems.map((pItem, pIndex) => {
                            return <SwiperFalling key={`update_${pIndex}`} pItem={CONVERT_OBJ_TO_ARRAY(pItem)} pIndex={pIndex} newApi={newApi} onUpdateRef={this.onUpdateRef} activeID={activeID} />
                        }
                        ) : null
                    }
                </div>
            </Fragment>
        )
    }

}

export default memo(FallingColumnsSwiper)