import React, { PureComponent, Fragment, Suspense, memo } from "react"
import { Helmet } from "react-helmet";
import { SERVER_PATH, DEFAULT_FINAL_PATH, DEFAULT_LOGO } from '../../../constants'
import ScrollToBottom from 'react-scroll-to-bottom'
import styled from 'styled-components'
import $, { data } from 'jquery';
//Swiper JS
import "swiper/swiper.min.css";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Pagination, Autoplay } from 'swiper/core';

import './assets/css/conversation-style.css'
import LeftChatCard from './elements/LeftChatCard'
import RightChatCard from './elements/RightChatCard'


//Swiper JS
SwiperCore.use([Pagination]);
SwiperCore.use([Autoplay]);

const timeIntervalSlack = 2000;
const uniqueDataForSlack = (data) => {
    let updateData = []; let updateKeys = [];
    if (data && data.length > 0) {
        data.map((item) => {
            if (!updateKeys.includes(item.id)) {
                updateData.push(item)
                updateKeys.push(item.id)
            }
        })
    }
    return updateData;

}

class Conversation extends PureComponent {
    state = {
        isPopUpHide: true,
        isDataCount: -1,
        slackThemeData: []
    }
    loadPopUP = () => {
        const { newItems } = this.props;
        const { isDataCount, slackThemeData } = this.state;
        if (isDataCount >= newItems.length - 1) {
            slackThemeData.length = 0;
            //slackThemeData.push(newItems[0]);
            this.setState({ isDataCount: -1, isPopUpHide: false, slackThemeData: uniqueDataForSlack(slackThemeData) }, () => this.loadPopUpWithTimer())
        }
        else {
            slackThemeData.push(newItems[isDataCount + 1]);
            this.setState({ isDataCount: isDataCount + 1, isPopUpHide: false, slackThemeData: uniqueDataForSlack(slackThemeData) }, () => this.loadPopUpWithTimer())
        }
    }



    loadPopUpWithTimer = () => {
        const { newApi } = this.props;
        setTimeout(() => {
            const timer = timeIntervalSlack;
            this.setState({ isPopUpHide: true }, () => setTimeout(() => this.onLoadedPopUp(), timer))
        },
            timeIntervalSlack)
    }

    extendsLoadPopUpWithTimer = () => {
        const { newApi } = this.props;
        setTimeout(() => {
            const { newItems } = this.props;
            const { isDataCount, slackThemeData } = this.state;
            if (isDataCount >= newItems.length - 1) {
                slackThemeData.length = 0;
                //slackThemeData.push(newItems[0]);
                this.setState({ isDataCount: -1, isPopUpHide: false, slackThemeData: uniqueDataForSlack(slackThemeData) }, () => this.loadPopUpWithTimer())
            }
            else {
                slackThemeData.push(newItems[isDataCount + 1]);
                this.setState({ isDataCount: isDataCount + 1, isPopUpHide: false, slackThemeData: uniqueDataForSlack(slackThemeData) }, () => this.loadPopUpWithTimer())
            }
        },
            timeIntervalSlack);
    }
    onLoadedPopUp = () => {
        const { newApi, newItems } = this.props;
        const { isDataCount, slackThemeData } = this.state;
        if (isDataCount >= (newItems.length - 1)) {
            slackThemeData.length = 0;
            //slackThemeData.push(newItems[0]);
            this.setState({ isDataCount: -1, isPopUpHide: false, slackThemeData: uniqueDataForSlack(slackThemeData) }, () => this.loadPopUpWithTimer())
        }
        else {

            slackThemeData.push(newItems[isDataCount + 1]);
            this.setState({ isDataCount: isDataCount + 1, isPopUpHide: false, slackThemeData: uniqueDataForSlack(slackThemeData) }, () => this.loadPopUpWithTimer())
        }
    }

    componentDidMount() {
        const { newApi, newItems } = this.props;
        this.loadPopUpWithTimer();


    }

    onScrollToBottom = () => {

        // var container = document.querySelector('#tb_con_container')
        // console.log("container", container)
        // container.maxScrollTop = container.scrollHeight - container.offsetHeight
        // if (container.maxScrollTop - container.scrollTop <= container.offsetHeight) {
        //     container.scrollTop = container.scrollHeight
        // }

    }



    render() {
        let { newItems, newApi } = this.props;
        const { slackThemeData } = this.state;
        const style = {
            backgroundColor: newApi.wall.ThemeRule.transparent == 1 ? 'transparent' : newApi.wall.ThemeRule.backgroundColor,
            backgroundImage: newApi.wall.BackgroundImage.newName != null ? `url(${SERVER_PATH}${newApi.wall.BackgroundImage.path}${newApi.wall.BackgroundImage.newName}` : 'none'
        };
        let wBannerImage = 0; let wtickerBottom = 0; let rootHeight = window.innerHeight; let rootWidth = parseInt(window.innerWidth) - 30;
        if ($("#wBannerImage") && $("#wBannerImage").height()) wBannerImage = $("#wBannerImage").height();
        if ($("#wtickerBottom") && $("#wtickerBottom").height()) wtickerBottom = $("#wtickerBottom").height();
        //if ($("#root") && $("#root").height()) rootHeight = $("#root").height();
        //if ($("#root") && $("#root").width()) rootWidth = $("#root").width();

        const customStyle = `.tb_scroll_to_bottom { width:${rootWidth}px;height:${parseInt(rootHeight) - (parseInt(wBannerImage) + parseInt(wtickerBottom))}px;} .tb_scroll_to_bottom > div {
 overflow-x: hidden!important;
}`
        return <>
            <Helmet>
                <style>{customStyle}</style>
            </Helmet>
            <div id={`theme-${newApi.wall.Personalization.signageTheme}`} className={`tb_con_theme theme-${newApi.wall.Personalization.signageTheme}`}>
                <div className="tb_con_theme_wrapper" style={{ height: parseInt(rootHeight) - (parseInt(wBannerImage) + parseInt(wtickerBottom)) }}>

                    <ScrollToBottom className={"tb_con_container tb_scroll_to_bottom"} mode={"bottom"} >
                        {
                            slackThemeData.map((item, itemKey) => {
                                {
                                    var isAnimate = false;
                                    if (slackThemeData && slackThemeData.length > 0 && (slackThemeData.length - 1) == itemKey) {
                                        isAnimate = true;
                                    }
                                    return (itemKey % 2 == 0) ? <LeftChatCard onScrollToBottom={this.onScrollToBottom} isAnimate={isAnimate} leftAnimation="tb_left_anim" key={`left_${itemKey}`} item={item} newApi={newApi.wall} webFilters={newApi.webFilters} />
                                        : <RightChatCard onScrollToBottom={this.onScrollToBottom} rightAnimation="tb_right_anim" isAnimate={isAnimate} key={`right_${itemKey}`} item={item} newApi={newApi.wall} webFilters={newApi.webFilters} />
                                }
                            })
                        }
                    </ScrollToBottom>

                </div>
            </div >
        </>
    }

}

export default memo(Conversation)