import React, { Component } from "react";
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import $ from 'jquery';
import queryString from 'query-string'
import io from "socket.io-client";
import { Provider } from 'react-redux'
import { STUDIO_API_URL, API_WALL_STATUS } from './studioPreview/actions/api'
import DisplayThemes from './displayThemes'
import StudioPreview from './studioPreview/App'
import ErrorComponent from './displayThemes/components/errors/error'
import PlanLimit from './displayThemes/components/errors/planLimit'
import store from "./studioPreview/store"
import { SOCKET_CLIENT_URL, V_EVENT } from './displayThemes/constants'
import { IS_EDITOR, IS_CHECK_WALL_KEY, getWallKey } from './displayThemes/utils'
import { iniFrame } from './utils'
import SubmitPic from './displayThemes/submitPic'
import OnSiteUpload from "./displayThemes/onSiteUpload";
const WINDOW_PATH_URL = window.location.pathname;

class App extends Component {
    state = {
        wallID: null,
        isEnable: null,
        isError: false,
        wallErrorCode: null,
        preview: false,
        isStudio: false,
        wall: null,
        screenLimitEnable: false,
        networkHandle: null,
        onsiteupload: false,
        planLimit: false,
        isErrorMessage: `Some error occurred.`,
        errorCodeHeading: null,
        onsite_token: null
    }
    componentWillMount() {
        this.onLoadUrl();
    }
    onLoadUrl = () => {
        if (window.wallID) {
            let isOnsiteUpload = false;
            const windowHref = window.location.href;
            if (windowHref.includes('onsite-upload')) {
                isOnsiteUpload = true;
            }
            this.setState({ wallID: window.wallID, onsiteupload: isOnsiteUpload }, () => this.onGetLoader())
        }
        else {
            const windowHref = window.location.href;
            if (windowHref.includes('display.taggbox.com')) {
                let wallid = window.location.href.split("https://display.taggbox.com/")[1];
                if (wallid.includes('/editor')) {
                    wallid = wallid.replace("/editor", "");
                }
                if (wallid.includes("?")) {
                    wallid = wallid.split("?")[0]
                }

                let isOnsiteUpload = false;
                const windowHref = window.location.href;
                console.log("windowHref",windowHref)
                if (windowHref.includes('onsite-upload')) {
                    isOnsiteUpload = true;
                }
                console.log("windowHref",isOnsiteUpload,wallid)
                if (windowHref.includes('onsite-upload') && wallid && wallid.length > 0) {
                    wallid = wallid.split("/")[0];
                }

                if (wallid && wallid.length > 0) this.setState({ wallID: wallid }, () => this.onGetLoader())
                else {
                    window.location.href = "https://taggbox.com/display";
                }

            }
            else if (windowHref.includes('display-demo.taggbox.com')) {
                let wallid = window.location.href.split("https://display-demo.taggbox.com/")[1];
                if (wallid.includes('/editor')) {
                    wallid = wallid.replace("/editor", "");
                }
                this.setState({ wallID: wallid }, () => this.onGetLoader())
            }
            else if (windowHref.includes('onsite-upload')) {
                if (windowHref.includes('display.taggbox.com')) {
                    let wallid = window.location.href.split("display.taggbox.com/")[1];
                    if (wallid.includes('/editor')) {
                        wallid = wallid.replace("/editor", "");
                    }
                    this.setState({ wallID: wallid }, () => this.onGetLoader())
                }
                else {
                    let wPath = WINDOW_PATH_URL.split('/s/')
                    let sSplit = wPath[1].split('/');
                    this.setState({ wallID: sSplit.length > 0 ? sSplit[0] : ``, onsiteupload: true }, () => this.onGetLoader())
                }

            }

            else {
                let checkIndex = WINDOW_PATH_URL.split('/index')
                let wallID = null;
                if (checkIndex.length > 1) {
                    wallID = checkIndex[1].split('/')[1];
                    if (wallID.includes('/editor')) {
                        wallID = wallID.replace("/editor", "");
                    }
                    this.setState({ wallID: wallID }, () => this.onGetLoader())
                }
                else {
                    let wPath = WINDOW_PATH_URL.split('/s/')
                    let sSplit = wPath[1].split('/');
                    if (sSplit.length > 1) {
                        if (sSplit[1] === "editor") wallID = sSplit[0];
                        else wallID = sSplit[1];
                    }
                    else if (sSplit.length === 1) wallID = sSplit[0];
                    if (wallID.includes('/editor')) {
                        wallID = wallID.replace("/editor", "");
                    }
                    this.setState({ wallID: wallID }, () => this.onGetLoader())
                }
            }
        }

    }

    onGetLoader = () => {
        const { wallID } = this.state;
        let editor = window.editor ? true : window.location.href.includes('/editor');
        if (window.location.href.includes('preview=1')) editor = true;
        else if (window.location.href.includes('studio=1')) editor = true
        const vEvent = V_EVENT ? { virtualEvent: 1 } : {}
        const wallKeys = V_EVENT && IS_CHECK_WALL_KEY ? { wallKey: getWallKey() } : {}
        const options = (editor) ? {
            headers: {
                'Access-Control-Allow-Origin': '*',
                editor: 1,
                ...vEvent,
                ...wallKeys
            }
        } : IS_CHECK_WALL_KEY ? {
            headers: {
                'Access-Control-Allow-Origin': '*',
                wallKey: getWallKey(),
            }
        } : {}
        axios.get(`${API_WALL_STATUS}/${wallID}`, options).then((response) => {
            const { scenes, response_code, older_days, is_studio_enabled, error_code, wall, planLimit, onsite_token } = response.data;

            // let { scenes, response_code, is_studio_enabled, error_code, wall } = response.data;
            // error_code= 211;

            let studio = false;
            let preview = false;
            const parsed = queryString.parse(window.location.search);
            if (parsed.studio && parsed.studio == 1) studio = true;
            if (parsed.preview && parsed.preview == 1) preview = true;
            let isStudioEnabled = false;
            if (is_studio_enabled == 1 && !studio) isStudioEnabled = true;
            else if (parsed.sceneID && parseInt(parsed.sceneID) > 0) isStudioEnabled = true
            if (isStudioEnabled) {
                if (wall && wall.User && wall.User.UserRule) {
                    let studioUserRuleEnable = wall.User.UserRule.studio;
                    if (studioUserRuleEnable == 0) {
                        isStudioEnabled = false
                    }
                }

            }
            //let vEvent = (window.location.href.includes('/editor')) && window.location.href.includes('webembed=1') ? true : false
            this.setState({ onsite_token, planLimit: (window.editor || window.location.href.includes('/editor')) && !V_EVENT ? false : planLimit, isEnable: isStudioEnabled, isStudio: studio, preview, wallErrorCode: error_code, wall, networkHandle: older_days }, () => this.onConnectedWithSocket())
        }).catch((error) => {
            const { data } = error.response;
            if (data && Object.keys(data).length > 0) {
                const { error_code, message } = data;
                this.setState({ isError: true, wallErrorCode: error_code, isErrorMessage: `This wall is secured with a Protected Layer. Please use the protected URL/code.`, errorCodeHeading: `Forbidden Access!` })

            }
            else {
                this.setState({ isError: true, isErrorMessage: `Some error occurred.`, errorCodeHeading: null })
            }

        })
    }

    onConnectedWithSocket = () => {
        const { wall, isEnable, wallErrorCode } = this.state;
        if (isEnable) {
            let screenLimit = wall.User.UserRule.screenLimit;
            const screenLimitExceed = wall.User.UserRule.screenLimitExceed
            let screens = wall.User.UserRule.screens;
            let screenId = this.onWindowLoad();
            screenId = String(screenId).replace(/-/g, "");
            let query = IS_EDITOR && !V_EVENT ? `` : `name=signage&screenId=${screenId}&wallId=${wall.id}&owner=${wall.owner}&screenLimit=${screenLimit}&screens=${screens}&screenLimitExceed=${screenLimitExceed}&timeStamp=${Math.floor(Date.now() / 1000)}&virtualEvent=${V_EVENT ? `1` : `0`}`

            if ((wallErrorCode === 213) && !IS_EDITOR) query = ``
            const SOCKET_CONNECTION = io(`${SOCKET_CLIENT_URL}`, {
                transports: ["websocket"],
                transport: ["websocket"],
                query: query,
                upgrade: true,
                'reconnection': true,
                'reconnectionDelay': 500,
            });
            const ON_EMIT_SOCKET_DATA = (params, callBack) => SOCKET_CONNECTION.on(params, callBack);
            setInterval(() => SOCKET_CONNECTION.emit("active-screen", { screenId, wallID: wall.id, timeStamp: Math.floor(Date.now() / 1000) }, screenLimitExceed), 30000)
            ON_EMIT_SOCKET_DATA(screenId, (data) => {
                const { screenLimit } = data;

                if (screenLimit) this.setState({ screenLimitEnable: true })
                else this.setState({ screenLimitEnable: false })
            });
            $(window).on('unload beforeunload', () => {
                SOCKET_CONNECTION.emit("remove-screen", { screenId })
            });
        }
    }
    onWindowLoad = () => {
        var tabID = sessionStorage.tabID &&
            sessionStorage.closedLastTab !== '2' ?
            sessionStorage.tabID :
            sessionStorage.tabID = this.makeid(25);
        sessionStorage.closedLastTab = '2';
        $(window).on('unload beforeunload', () => {
            sessionStorage.closedLastTab = '1';
        });
        return tabID
    }


    makeid = (length) => {
        var result = '';
        var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        var charactersLength = characters.length;
        for (var i = 0; i < length; i++) {
            result += characters.charAt(Math.floor(Math.random() *
                charactersLength));
        }
        return result;
    }
    onSiteUpdateToken = (onsite_token) => {
        this.setState({ onsite_token })
    }

    render() {
        const { planLimit, isEnable, isError, wallErrorCode, preview, isStudio, screenLimitEnable, networkHandle, onsiteupload, wall, isErrorMessage, errorCodeHeading, onsite_token } = this.state;
        const IsEditor = window.editor ? true : window.location.href.includes('/editor')
        const isIframe = iniFrame()


        return onsiteupload ? wall && Object.keys(wall).length > 0 ? <OnSiteUpload UgcSettings={wall.UgcSettings} wall={wall} onsite_token={onsite_token} onSiteUpdateToken={this.onSiteUpdateToken} /> : null : <>{!isIframe && IsEditor ? <ErrorComponent err={405} message="This display wall URL is only compatible with virtual event platforms." /> : <>{isError ? <ErrorComponent err={404} message={isErrorMessage} errorCodeHeading={errorCodeHeading} /> : (wallErrorCode == 0 || wallErrorCode) && isEnable ? IsEditor ? <DisplayThemes networkHandle={networkHandle} wallErrorCode={wallErrorCode} preview={preview} isStudio={isStudio} /> : <Provider store={store}><StudioPreview screenLimitEnable={screenLimitEnable} preview={preview} /> </Provider> : ((wallErrorCode == 0 || wallErrorCode) && !planLimit) ? <DisplayThemes wallErrorCode={wallErrorCode} preview={preview} isStudio={isStudio} networkHandle={networkHandle} /> : planLimit ? <PlanLimit /> : null
        }</>}</>
    }
}
export default App;