import React, { Component } from 'react';
import './assets/css/mashup-style.css'
import MashupPost from './post'
var MashUpData = []
class Mashup extends Component {
  constructor(props) {
    super(props)
  }
  state = { isFirst: true, activeAnimation: 1, slideRightClass: false, slideUpDownClass: false, slide3: false, totalPostCount: 0, postCount: 0 }
  componentDidMount() {
    const { newItems } = this.props;
    if (newItems && newItems.length > 0) this.setState({ totalPostCount: newItems.length }, () => setTimeout(() => this.onUpdateAnimation(), 3000))
  }
  componentDidUpdate(prevProps, prevState) {
    const { newItems } = this.props;
    if (prevState.totalPostCount != newItems.length) {
      this.setState({ totalPostCount: newItems.length })
    }

  }
  onUpdateAnimation = () => {
    const { activeAnimation, slideRightClass, slideUpDownClass, postCount, totalPostCount } = this.state;

    const postCountD = (parseInt(postCount) + 1) < totalPostCount ? (parseInt(postCount) + 1) : 0;
    if (activeAnimation === 1) {

      setTimeout(
        function() {
          this.setState({ isFirst: false, activeAnimation: 2, slideRightClass: !slideRightClass, postCount: postCountD });
        }
        .bind(this),
        100
    );

    setTimeout(
      function() {
        this.onUpdateAnimation()
      }
      .bind(this),
      5000
  );

    
      // this.setState({ isFirst: false, activeAnimation: 2, slideRightClass: !slideRightClass, postCount: postCountD },
        
      //   () => setTimeout(() => this.onUpdateAnimation(), 5000)
      //   )
    }
    else if (activeAnimation === 2) {

      setTimeout(
        function() {
          this.setState({ activeAnimation: 3, postCount: postCountD });
        }
        .bind(this),
        100
    );

    setTimeout(
      function() {
        this.onUpdateAnimation()
      }
      .bind(this),
      5000
  );


     // this.setState({ activeAnimation: 3, postCount: postCountD }, () => setTimeout(() => this.onUpdateAnimation(), 5000))
    }
    else if (activeAnimation === 3) {
      const { slide3 } = this.state


      if (!slide3)
      {
      
     // this.setState({ activeAnimation: 3, slide3: true, postCount: postCountD }, () => setTimeout(() => this.onUpdateAnimation(), 5000))

      setTimeout(
        function() {
          this.setState({ activeAnimation: 3, slide3: true, postCount: postCountD });
        }
        .bind(this),
        100
    );

    setTimeout(
      function() {
        this.onUpdateAnimation()
      }
      .bind(this),
      5000
  );
    }

      else {


        // this.setState({ activeAnimation: 1, slideRightClass: true, slideUpDownClass: false, slide3: false, postCount: postCountD }, () => setTimeout(() => this.onUpdateAnimation(), 5000))

        setTimeout(
          function() {
            this.setState({ activeAnimation: 1, slideRightClass: true, slideUpDownClass: false, slide3: false, postCount: postCountD });
          }
          .bind(this),
          100
      );
  
      setTimeout(
        function() {
          this.onUpdateAnimation()
        }
        .bind(this),
        5000
    );

    
      }
    }

  }


  render() {
    const { slideRightClass, slideUpDownClass, activeAnimation, slide3, postCount, totalPostCount, isFirst } = this.state;
    const { timer, newItems, newApi, rootHeight, height } = this.props;
    const style = { height: `${height}px` }
   
  
    return (
      <div className="mashup__" style={style}>
        {!slide3 ? <div className={"tb_mash_grid tb_grid_23 tb_slide_up tb_animate__slideInDown" + (slideRightClass ? ' tb_slideRight' : '')} style={activeAnimation == 3 ? { left: '100%' } : {}}>
          <MashupPost slideUpDownClass={slideUpDownClass} postCount={postCount} totalPostCount={totalPostCount} newItems={newItems} newApi={newApi} MashUpData={MashUpData} activeAnimation={activeAnimation} isFirst={isFirst} cRender={isFirst ? 2 : 0} rootHeightHalf={height/2} />
        </div> : null}
        {(activeAnimation == 3 && !slide3) ? <div className="tb_mash_grid tb_grid_22">
          <MashupPost slideUpDownClass={false} postCount={postCount} totalPostCount={totalPostCount} newItems={newItems} newApi={newApi} MashUpData={MashUpData} activeAnimation={activeAnimation} isFirst={isFirst} cRender={isFirst ? 4 : 0} rootHeightHalf={height/2}/>
        </div> : null}
        {
          (activeAnimation == 3 && slide3) ? <div className={"tb_mash_grid tb_grid_23 tb_slide_up tb_animate__slideInDown"}>
            <MashupPost slideUpDownClass={true} postCount={postCount} totalPostCount={totalPostCount} newItems={newItems} newApi={newApi} MashUpData={MashUpData} activeAnimation={activeAnimation} isFirst={isFirst} cRender={isFirst ? 6 : 0} rootHeightHalf={height/2}/>
          </div> : null
        }
        {slideRightClass && !slideUpDownClass ? <div className={`tb_mash_grid tb_grid_13 tb_animate__slideInUp `} style={activeAnimation == 3 ? {} : { left: 0 }}>
          <MashupPost slideUpDownClass={!slideUpDownClass} postCount={postCount} totalPostCount={totalPostCount} newItems={newItems} newApi={newApi} MashUpData={MashUpData} activeAnimation={activeAnimation} isFirst={isFirst} cRender={isFirst ? 8 : 0} rootHeightHalf={height/2} />
        </div> : null}
        <div className={"tb_mash_grid tb_grid_13 tb_animate__slideInUp" + (slideRightClass ? ' tb_slideRight' : '')}>
          <MashupPost slideUpDownClass={slideUpDownClass} postCount={postCount} totalPostCount={totalPostCount} newItems={newItems} newApi={newApi} MashUpData={MashUpData} activeAnimation={activeAnimation} isFirst={isFirst} cRender={isFirst ? 10 : 0} rootHeightHalf={height/2} />
        </div>
      </div>
    );
  }
}

export default Mashup;