

import React, { PureComponent, memo } from 'react';
import "./../../../css/ticker-theme.css";
import NetworkCardComponent from '../../networkCardComponent'
import AuthorDetails from '../../sepcomponents/AuthorDetails'
import FontFamilyForPost from '../../sepcomponents/FontFamilyForPost'
import { findNetworkDataFromWebFilters } from './../../../utils'
import $ from "jquery";

var charlength, pwidth, fwidth;

var ReachedMaxScroll = false;
var PreviousScrollTop = 0;
var ScrollInterval, DivElmnt, scrollTime;

const speedUpPixel = (scrollDelay) => {
	switch (scrollDelay) {
		case 80:
			return 60;
		case 70:
			return 40;
		case 50:
			return 30;
		case 30:
			return 20;
		case 20:
			return 10;
		default:
			return 20;
	}
}



class TickerWall extends PureComponent {

	state = {
		valuep: []
	}


	scrollDiv_init = () => {
		const { newApi } = this.props;
		DivElmnt = document.querySelector("#sPosts");
		ReachedMaxScroll = false;
		DivElmnt.scrollLeft = 0;
		PreviousScrollTop = 0;
		ScrollInterval = setInterval(() => {
			this.scrollDiv();
		}, speedUpPixel(newApi.wall.Personalization.scrollDelay));

	}

	scrollDiv = () => {
		if (!ReachedMaxScroll) {
			DivElmnt.scrollLeft = PreviousScrollTop;
			PreviousScrollTop++;
			ReachedMaxScroll = DivElmnt.scrollLeft >= (DivElmnt.scrollWidth - DivElmnt.offsetWidth) - 10;
		}
		else {
			DivElmnt.scrollLeft = PreviousScrollTop = 0;
			ReachedMaxScroll = false;
		}
	}

	changeTickerWidth = () => {


		let { newItems, newApi } = this.props;
		let { valuep } = this.state

		var fontSize = parseInt(newApi.wall.ThemeRule.fontSize);
		//var fontSize = parseInt(config.wall.more.fontSize);
		$('.postItem.item').each(function () {

			var str = $(this).find('p').text();
			var charlength = str.length;

			if (fontSize >= 10 && fontSize <= 12) {
				if (charlength <= 100) {
					$(this).find('.postedTxt').width(300);
					$(this).width(675);
				}
				else if (charlength > 100 && charlength <= 150) {
					$(this).find('.postedTxt').width(515);
					$(this).width(890);
				}
				else if (charlength > 150 && charlength <= 200) {
					$(this).find('.postedTxt').width(580);
					$(this).width(955);
				}
				else if (charlength > 200 && charlength <= 250) {
					$(this).find('.postedTxt').width(700);
					$(this).width(1075);
				}
				else if (charlength > 250) {
					$(this).find('.postedTxt').width(850);
					$(this).width(1225);
				}
			}
			else if (fontSize >= 13 && fontSize <= 15) {
				if (charlength <= 100) {
					$(this).find('.postedTxt').width(330);
					$(this).width(715);
				}
				else if (charlength > 100 && charlength <= 150) {
					$(this).find('.postedTxt').width(660);
					$(this).width(1035);
				}
				else if (charlength > 150 && charlength <= 200) {
					$(this).find('.postedTxt').width(710);
					$(this).width(1085);
				}
				else if (charlength > 200 && charlength <= 250) {
					$(this).find('.postedTxt').width(855);
					$(this).width(1230);
				}
				else if (charlength > 250) {
					$(this).find('.postedTxt').width(1075);
					$(this).width(1460);
				}
			}
			else if (fontSize >= 16 && fontSize <= 18) {
				if (charlength <= 100) {
					$(this).find('.postedTxt').width(360);
					$(this).width(735);
				}
				else if (charlength > 100 && charlength <= 150) {
					$(this).find('.postedTxt').width(750);
					$(this).width(1125);
				}
				else if (charlength > 150 && charlength <= 200) {
					$(this).find('.postedTxt').width(805);
					$(this).width(1180);
				}
				else if (charlength > 200 && charlength <= 250) {
					$(this).find('.postedTxt').width(1000);
					$(this).width(1375);
				}
				else if (charlength > 250) {
					$(this).find('.postedTxt').width(1250);
					$(this).width(1625);
				}
			}
			else if (fontSize >= 19) {
				if (charlength <= 100) {
					$(this).find('.postedTxt').width(400);
					$(this).width(775);
				}
				else if (charlength > 100 && charlength <= 150) {
					$(this).find('.postedTxt').width(810);
					$(this).width(1185);
				}
				else if (charlength > 150 && charlength <= 200) {
					$(this).find('.postedTxt').width(870);
					$(this).width(1245);
				}
				else if (charlength > 200 && charlength <= 250) {
					$(this).find('.postedTxt').width(1070);
					$(this).width(1445);
				}
				else if (charlength > 250) {
					$(this).find('.postedTxt').width(1380);
					$(this).width(1755);
				}
			}
		});


	}


	componentDidMount() {
		const ellem = document.querySelector("#sPosts");
		this.changeTickerWidth();
		// this.getallwidth();
		this.scrollDiv_init()

	}

	render() {

		let { newItems, newApi } = this.props;
		return <div id="sPosts"
			className={`theme${newApi.wall.Personalization.signageTheme}`}
			style={{ height: 'inherit', position: 'relative', overflow: 'hidden', backgroundColor: newApi.wall.ThemeRule.cardColor }}
		>
			{
				newItems.map((item, itemKey) => {
					//console.log(item.id)
					const networkd = findNetworkDataFromWebFilters(item.networkId, newApi.webFilters);
					return <div id={`"postId${item.id}`} className={`feedId${item.feedId} postItem item flatCard autoBold`}
						data-post-id={item.id} data-highlight={item.highlight} data-pin={item.pin}
						data-created={item.createdAt} >
						<div className="postContentCard">
							<h4 margin-top="0px">
								<NetworkCardComponent network={networkd} item={item} themeRule={newApi.wall.ThemeRule} />
								<AuthorDetails item={item} personalization={newApi.wall.Personalization} font={newApi.wall.ThemeRule} network={networkd} backgroundColor={networkd.color} />
							</h4>
							<FontFamilyForPost item={item} newApi={newApi} personalization={newApi.wall.Personalization} themeRule={newApi.wall.ThemeRule} network={networkd} font={newApi.wall.ThemeRule} signageTheme={newApi.wall.Personalization.signageTheme} ownerID={newApi.wall.Wall.owner} />

						</div>
					</div>
				})
			}

		</div>

	}
}
export default memo(TickerWall);
