import React, { Component } from "react";
import YouTube from 'react-youtube';

class YoutubePlayers extends Component {
    _onReady = (event) => {
        // access to player in all event handlers via event.target
        event.target.mute();
        event.target.playVideo();
    }
    render() {
        const { videoId } = this.props;
        const opts = {
            height: '100%',
            width: '100%',
            playerVars: {
                'autoplay': 1,
                'playsinline': 1
            }
        };
        return <YouTube containerClassName="youtube_vid_container" className="youtube_vid" videoId={videoId} opts={opts} onReady={this._onReady} />
    }

}

export default YoutubePlayers;