import React, { PureComponent, Fragment } from 'react';
import "../../css/users.css";
import { CloudUrl } from '../../constants';

const styles = {
    addBtn: {
        border: '1px solid #2b2b2b',
        color: '#2b2b2b',
        fontSize: '14px',
        textTransform: 'uppercase',
        padding: '12px 20px',
        borderRadius: '5px', margin: 8
    },
    showDemoPost: {
        fontSize: '14px',
        textTransform: 'uppercase',
        padding: '12px 20px',
        borderRadius: '5px', margin: 8, color: '#fff',
        border: 'none'
    },
    demoWallErrorStyle: {
        maxWidth: '100%',
        width: '100%',
        left: 'auto',
        right: '0px',
        transform: 'none',
        bottom: 'auto',
        top: '0px',
        zIndex: 9998,
        position: 'relative',
        padding: '10px 15px',
        borderTop: '1px solid rgb(244, 244, 244)',
        borderRadius: '0px',
        textAlign: 'center',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        background: 'rgb(255, 255, 255)'
    },
    hideBtnStyle: {
        color: 'rgb(194, 14, 94)',
        textTransform: 'capitalize',
        background: 'transparent',
        borderWidth: '1px',
        borderStyle: 'solid',
        borderColor: 'rgb(194, 14, 94)',
        borderImage: 'initial',
        padding: '10px 15px',
        margin: '3px'
    },
    strong: {
        color: '#c20e5e',
        fontWeight: 600
    }
}
export default class Error extends PureComponent {
    onClickToadd = event => {
        event.preventDefault();
        let data = [];
        data['type'] = 'create_feed';
        window.parent.postMessage(data, '*');
    }

    onClickToDemoPost = event => {
        event.preventDefault();
        const { newApi } = this.props;
        let wallId = newApi.wall.Wall.id;
        let status = newApi.wall.Personalization.demoWall;
        // $.ajax({
        //     url: `${API_ROOT_URL}events/wall/demoStatus/${wallId}/${status}`,
        //     cache: false,
        //     dataType: "json",
        //     timeout: (1000 * 60 * 7),
        //     success: function (response) {
        //         window.location.reload();
        //     }
        // });

    }

    onClicktoUrl = (path) => event => {
        event.preventDefault();
        window.top.location.href = path;
    }

    onClickHref = (e) => {
        e.preventDefault()

    }


    render() {
        let editor = window.editor ? true : window.location.pathname.includes('/editor');
        let urltagg = 'https://app.taggbox.com/display';
        const { errorCodeHeading, message, err } = this.props;

        return (



            <Fragment>
                <div className="col-md-12 text-center noPostSec" id="noPosts"
                    style={{ position: 'absolute', backgroundImage: `url(${CloudUrl}/images/nopost.png)` }}>
                    <div className="noPostContent">
                        <div className="noPostMsgBlock" >
                            {
                                err == 405 ? <>
                                    <h2>Invalid Url !</h2><p>{message}</p>
                                </> : errorCodeHeading ? <><h2>{errorCodeHeading}</h2><p>{message}</p></> : <h2> <a href={urltagg} target="_blank" rel="noopener noreferrer"> {message}</a></h2>
                            }

                        </div>
                    </div>
                </div>
            </Fragment>


        )
    }

}
