import React, { Component } from 'react';
import ReactStars from "react-rating-stars-component";
class SubmitReview extends Component {
    ratingChanged = (newRating) => {
        const { isActiveIndex, addRatings } = this.props;

        addRatings(newRating, isActiveIndex)
    };

    render() {
        const { onClickToUpdateStep, isAddMoreActive, isActiveIndex, addReview, item, onClickToUpdateMoreStep } = this.props;
        return (
            <div className='add_caption_tab'>
                <div className='add-text'>
                    <input type="text" className="input-fileld" placeholder='Add your review here....' onChange={addReview(isActiveIndex)} value={(item && item.review) ? item.review : ""} />
                    {item.error ? <div className='error-text'>* Please add review and ratings.</div> : null}
                </div>
                <div className='rating-stars'>
                    <ReactStars size={32} count={5} onChange={this.ratingChanged} value={item && item.rating ? item.rating : 0} />
                </div>
                <div className='next-step' style={item && isAddMoreActive ? { display: 'flex', justifyContent: 'space-between' } : null}>
                    {item && isAddMoreActive ? <button type='submit' className='btn-primary' onClick={onClickToUpdateMoreStep(2)}>Skip</button> : null}
                    <button type='submit' className='btn-primary' id="tb_onsite_btn_id" onClick={onClickToUpdateStep(2)}>Next</button>
                </div>
            </div>
        )
    }
}

export default SubmitReview;