import React, { memo } from "react";
import '../sepcomponents/AuthorAvatar.css'
const AuthorAvatar = ({ username, color, style, backgroundColor }) => {
    //console.log("username, color, style ,backgroundColor ", username, color, style, backgroundColor)
    return <>
        <div className="author-avatar-block" style={{ ...style, backgroundColor: backgroundColor, color: color, fontFamily: 'Open Sans' }}>
            <span style={color ? { color } : {}}> {(username && username.length > 0) && username.charAt(0).toUpperCase()}</span>
        </div>
    </>
}

export default memo(AuthorAvatar)