import React, { PureComponent, Fragment } from 'react';
import "../../css/users.css";
import { CloudUrl } from '../../constants';


export default class PlanLimit extends PureComponent {
    render() {
        return <div className="col-md-12 text-center noPostSec" id="noPosts"
            style={{ position: 'absolute', backgroundImage: `url(${CloudUrl}/images/nopost.png)` }}>
            <div className="noPostContent">
                <div className="noPostMsgBlock" >
                    <h2>You're Running Extra Social Walls/Feeds!</h2>
                    <p>To enjoy uninterrupted services,kindly revoke the additional wall/feed or upgrade your plan.</p>
                </div>
            </div>
        </div>

    }

}
