import React, { Component } from "react";
import $ from 'jquery';
import '../css/quote.css'
import { shuffleDataArray } from '../utils'
import { GET_QUOTES } from '../actions/sceneActions'
class MotivationalQuotePreview extends Component {
    state = {
        quotesData: "", categoryValue: "", quoteCompleteData: [],
        animationStyle: 'toggleIn',
        animationStyleNew: 'zoomScale',
        timeInterval: null, currentIndex: null
    }
    componentWillMount() {
        const { app_data } = this.props;

        GET_QUOTES(app_data.categoryValue).then((response) => {
            const { responseData, responseCode } = response.data;
            if (responseCode == 200) {
                let currentIndex = 0;
                var dataResponseData = responseData
                try {
                    dataResponseData = shuffleDataArray(responseData);
                }
                catch (ex) {
                    dataResponseData = responseData;
                }

                this.setState({ animationStyle: 'toggleIn', animationStyleNew: 'zoomScale', quoteCompleteData: dataResponseData, quotesData: dataResponseData[currentIndex], currentIndex }, () => this.autoSizeText())
                this.setState({ timeInterval: app_data.timeInterval }, () => this.onUpdateQuotes())
            }
        })
    }
    componentDidMount() {
        window.addEventListener("resize", () => {
            this.autoSizeText();
        });
        this.autoSizeText();
    }
    onUpdateQuotes = () => {
        const { timeInterval, currentIndex, quoteCompleteData } = this.state;
        if (quoteCompleteData && quoteCompleteData.length > 0) {
            if (timeInterval && timeInterval > 0) {
                const currentTimeInterval = parseInt(timeInterval * 60 * 1000);
                if (this.timeUpdateInterval && this.timeUpdateInterval != undefined) clearInterval(this.timeUpdateInterval)
                this.timeUpdateInterval = setInterval(() => {
                    if (quoteCompleteData.length < currentIndex) {
                        this.setState({ animationStyle: 'toggleOut' }, () => setTimeout(() => this.setState({ animationStyle: 'toggleIn', quotesData: quoteCompleteData[0], currentIndex: 0 }, () => this.autoSizeText()), 300));
                        this.setState({ animationStyleNew: 'toggleOut' }, () => setTimeout(() => this.setState({ animationStyle: 'zoomScale', quotesData: quoteCompleteData[0], currentIndex: 0 }, () => this.autoSizeText()), 300))
                    }
                    else {
                        const updateCurrentIndex = this.state.currentIndex + 1;
                        this.setState({ animationStyle: 'toggleOut' }, () => setTimeout(() => this.setState({ animationStyle: 'toggleIn', quotesData: quoteCompleteData[updateCurrentIndex], currentIndex: updateCurrentIndex }, () => this.autoSizeText()), 300))
                        this.setState({ animationStyleNew: 'toggleOut' }, () => setTimeout(() => this.setState({ animationStyleNew: 'zoomScale', quotesData: quoteCompleteData[updateCurrentIndex], currentIndex: updateCurrentIndex }, () => this.autoSizeText()), 300))
                    }
                }, currentTimeInterval);
            }
        }
    }
    autoSizeText = () => {
        var el, elements, _i, _len, _results;
        $('.resize').removeAttr("style");
        elements = $('.resize');
        if (elements.length < 0) {
            return;
        }
        _results = [];
        for (_i = 0, _len = elements.length; _i < _len; _i++) {
            el = elements[_i];
            _results.push((function (el) {
                var resizeText, _results1;
                resizeText = function () {
                    var elNewFontSize;
                    elNewFontSize = (parseInt($(el).css('font-size').slice(0, -2)) - 1) + 'px';
                    return $(el).css('font-size', elNewFontSize);
                };
                _results1 = [];
                while (el.scrollHeight > el.offsetHeight) {
                    var fontSize = $(el).css("font-size");
                    // console.log("fontSize", fontSize, parseInt(fontSize))
                    if (parseInt(fontSize) > 8) _results1.push(resizeText());
                    else return _results1;
                }
                return _results1;
            })(el));
        }
        return _results;
    }


    render() {
        const {
            app_data,
            text } = this.props;
        //console.log("app_data", app_data)
        const { quotesData, animationStyle, animationStyleNew } = this.state;
        //console.log("quotesData", quotesData)
        return quotesData && Object.keys(quotesData).length > 0 && <div className={`mqtd_quote_card mqtd_quote_card_new ${animationStyleNew}`} style={{ backgroundImage: `url(${quotesData.background})` }}  >
            <div className="mqtd_quote_overlay"></div>
            <div className={`mqtd_quoteCardContent flexStyle ${animationStyle}`}>
                <div className={`overlay2 ${animationStyle}`}>
                    <div className={`mqtd_quote_icon ${animationStyle}`}><svg xmlns="http://www.w3.org/2000/svg" className="mqtd_quote_svg_icon" viewBox="0 0 103.366 82.649">
                        <path id="Path_2" data-name="Path 2" d="M-425.49-235.981h44.52v-44.3h-25.346c.661-7.934,2.2-11.461,6.171-14.767,4.408-3.747,8.816-5.51,17.411-6.832l-9.7-16.75c-10.579,2.424-15.648,4.849-21.379,10.359-7.714,6.832-11.681,16.53-11.681,27.991Zm59.066,0h44.3v-44.3h-25.125c.661-7.934,1.984-11.461,6.171-14.767,4.188-3.747,8.6-5.51,17.191-6.832l-9.7-16.75c-10.359,2.424-15.428,4.849-21.379,10.359-7.493,6.832-11.461,16.53-11.461,27.991Z" transform="translate(425.49 318.63)" fill="#dac30c" />
                    </svg>
                    </div>
                    <div className={`mqtd_quote_text resize ${animationStyle}`} onLoad={this.autoSizeText()}> {quotesData.description}</div>
                    <div className={`mqtd_quote_authername ${animationStyle}`}>- {quotesData.author}</div>
                </div>

            </div>
        </div >

    }
}

export default MotivationalQuotePreview