import React, { Component } from "react";
import { IFrame } from './iframe'
import CalendarPreview from './index'

const toAbsoluteUrl = pathname => process.env.PUBLIC_URL + pathname;
const CalendarFrame = (porps) => (
    <IFrame>

        <link rel="stylesheet" href={toAbsoluteUrl("/css/fullcalendar.css")} />
        <link rel="stylesheet" href={toAbsoluteUrl("/css/day.css")} />
        <link rel="stylesheet" href={toAbsoluteUrl("/css/month.css")} />
        <link rel="stylesheet" href={toAbsoluteUrl("/css/week.css")} />
        <link rel="stylesheet" href={toAbsoluteUrl("/css/inter-fonts.css")} />

        <CalendarPreview {...porps} />

        <script src={toAbsoluteUrl("https://unpkg.com/fullcalendar@5.9.0/main.js")} ></script>
    </IFrame>
)
export default CalendarFrame;
