import React, { PureComponent, Fragment, Suspense, memo, useRef, useState, Component } from "react"
import $ from 'jquery'
import FallingCard from './elements/FallingCard'
import { Swiper, SwiperSlide } from "swiper/react";
import { findNetworkDataFromWebFilters } from '../../../utils'
import ErrorBoundary from '../../../errorBoundary'


class SwiperFalling extends Component {
    constructor(props) {
        super(props);
        this.myRef = React.createRef();
    }
    componentWillReceiveProps(nextProps) {
        const { activeID, pIndex } = nextProps;
        if (activeID == pIndex && this.myRef.current) {
            this.myRef.current.swiper.slideNext()
        }

    }
    onUpdate = () => {
        setTimeout(() => {
            const { pIndex } = this.props;
            this.props.onUpdateRef(pIndex, null);
            window.dispatchEvent(new Event('resize'))
            setTimeout(() => {

                $(`#tb_wall_list_wrap___${pIndex + 1}`).addClass("tb_fc_animation")
                //window.dispatchEvent(new Event('resize'))

            }, 2500)

        }, 1000)
    }
    render() {
        const { delaySlide, pItem, pIndex, newApi } = this.props;
        return <>

            <Swiper
                ref={this.myRef}
                slidesPerView={'auto'}
                direction={"vertical"}
                spaceBetween={0}
                mousewheel={true}
                loop={true}
                speed={2000}
                autoplay={{
                    //delay: delaySlide,
                    enabled: false,
                    disableOnInteraction: true
                }}
                id={`tb_wall_list_wrap___${pIndex + 1}`}
                className="tb_wall_list_wrap___"
                key={`tb_wall_list_wrap___${pIndex + 1}`}
                style={{ width: `${100 / newApi.postData.length}%` }}
                onAfterInit={this.onUpdate}

            >
                {
                    pItem && pItem.map((item, index) => {
                        const networkd = findNetworkDataFromWebFilters(item.networkId, newApi.webFilters);
                        return <SwiperSlide key={`item_${index}_${pIndex}`}>
                            <ErrorBoundary><FallingCard columnsId={pIndex} activeIndex={index} key={index} item={item} newApi={newApi} networkData={networkd} /></ErrorBoundary>
                        </SwiperSlide>
                    })
                }
            </Swiper>
        </>
    }
}

export default SwiperFalling;