import React, { Component } from 'react';
import { v4 as uuidv4 } from 'uuid';
import AtmosItemRender from '../../sepcomponents/AtmosItemRender'
import NetworkCardComponent from '../../networkCardComponent'
import { findNetworkDataFromWebFilters } from './../../../utils'
import $ from "jquery";
import './assets/css/atmosphere-style.css'
import Skeleton from 'react-loading-skeleton'

class Atmosphere extends Component {

  state = {
    wWidth: document.getElementById('root').clientWidth,
    wHeight: window.innerHeight,
    height: 0,
    switcher: 0,
    aiy: 0,
    count: 0,
    loading: true,
    scrollheight: 0,
    postWidth: 0,
    postHeight: 0,
    cardNumber: 0,
    aftereffect: null,
    theme44Height: 0,
    theme44Width: 0,
    postNumber: []

  }

  componentDidMount() {
    /* ==============FOR POST RESPONSIVE============== */
    this.onResponsiveTheme();
    window.addEventListener("resize", () => {
      this.setState({ wWidth: document.getElementById('root').clientWidth, wHeight: window.innerHeight }, () => this.onResponsiveTheme())
    })

    /* ==============FOR MOUSE SCROLL STOP============== */
    window.addEventListener("wheel", e => e.preventDefault(), { passive: false })
    this.onLoadTheme();
  }
  onLoadAnimation = () => {
    const { newItems } = this.state;
    let data = document.querySelectorAll(".tb_01_media_001__");
    if (data && data.length) {
      data.forEach((item, index) => {
        const randomTimer = Math.floor(Math.random() * newItems.length) + 1
        setTimeout(() => {
          let classData = item.getAttribute("class");
          item.setAttribute("class", `${classData} tb_show_`)
        }, parseInt(randomTimer) * 400);
        if (parseInt(index) + 1 == data.length) this.animationRevert();
      })
    }
  }

  animationRevert = () => setTimeout(() => this.removeAnimation(), 20000);

  removeAnimation = () => {
    $(".tb_01_media_001__").removeClass("tb_show_");
    this.onLoadSocketData();
  }
  onLoadSocketData = () => {
    const { newItems } = this.state;
    const { socketData, isSocketAction, isSocketRemoveIDs } = this.props;
    let dataWithSocket = (socketData && socketData.length > 0) ? [...socketData, ...newItems] : newItems;
    let removeDuplicateData = []; let removeDuplicateIDs = []; let removeDuplicateData1 = []


    if (dataWithSocket && dataWithSocket.length > 0) {
      dataWithSocket.map((item) => {
        if (!removeDuplicateIDs.includes(item.id)) {
          removeDuplicateData.push(item)
          removeDuplicateIDs.push(item.id)
        }
      })
    }
    removeDuplicateData1 = removeDuplicateData
    if (isSocketAction === "remove" && isSocketRemoveIDs && isSocketRemoveIDs.length > 0) {
      removeDuplicateData = removeDuplicateData.filter(item => !isSocketRemoveIDs.includes(item.id));
    }
    this.setState({ newItems: removeDuplicateData }, () => setTimeout(() => this.onLoadAnimation(), 1000))

  }


  onLoadTheme = () => {
    const { newItems } = this.props;
    if (newItems && newItems.length > 0) setTimeout(() => this.setState({ newItems: newItems, loading: false }, () => this.onLoadAnimation()), 1000)
  }

  componentDidUpdate(prevProps, prevState) {
    const { newItems } = prevProps;
    if (newItems != this.props.newItems) setTimeout(() => this.setState({ loading: false }), 1000)
  }

  /* ==============FOR Theme Responsive ============== */
  onResponsiveTheme = () => {
    let { newApi } = this.props;
    let { wWidth, wHeight } = this.state;
    let totalpost = newApi.wall.ThemeRule.numberOfPosts;
    let theme44H = (newApi.wall.CustomBanner.status == 1) ? wHeight - newApi.wall.CustomBanner.banner_height : wHeight;

    if (totalpost >= 32) {
      this.setState({ postWidth: (wWidth / 8), postHeight: (theme44H / 4) })

    }
    else if (totalpost < 32 && totalpost >= 18) {
      this.setState({ postWidth: (wWidth / 6), postHeight: (theme44H / 3) })
    }
    else {
      this.setState({ postWidth: (wWidth / 3), postHeight: (theme44H / 2) })

    }
    this.setState({ theme44Height: theme44H })

  }

  render() {
    const { loading, postWidth, postHeight, theme44Height, wWidth, postNumber, newItems } = this.state;
    let { newApi, socketData } = this.props;

    return <div id="wPosts" className={`atmosphere__ theme${newApi.wall.Personalization.signageTheme}`}
      style={{ width: wWidth, height: theme44Height, position: 'relative' }}
    >
      {
        (newItems && newItems.length > 0) && newItems.map((item, itemKey) => {
          const networkd = findNetworkDataFromWebFilters(item.networkId, newApi.webFilters);
          return item.type !== 1 && <div  key={uuidv4()} className={`tb_01_post_001__ postImgDiv media_holder${item.id}`} style={{ width: postWidth, height: postHeight }}>
            <div className='tb_01_post_wrap_'>
              <div className={`tb_01_media_001__`} key={uuidv4()} >
                {loading ? <Skeleton height={300} /> : <>
                  <AtmosItemRender key={uuidv4()} network={networkd} newApi={newApi} item={item} itemKey={itemKey} />

                  <div className="tb_01_post_icon__003">
                    <NetworkCardComponent network={networkd} themeRule={newApi.wall.ThemeRule} />
                  </div>
                </>}
              </div>
            </div>
          </div>
        })
      }
    </div>

  }
}

export default Atmosphere;