import React, { PureComponent } from 'react';
//import Masonry from "react-masonry-component";
import Masonry from 'react-masonry-css'
import ScrollAnimation from 'react-animate-on-scroll';
// import { themeAutoScrolling } from './../../utils'
import './../../../css//social-walls.css'
import AnimatedSocialWallItem from './AnimatedSocialWallItem';
import SocialWallItem from './SocialWallItem';
import { findNetworkDataFromWebFilters } from './../../../utils'
import ErrorBoundary from '../../../errorBoundary'
const masonryOptions = {
    transitionDuration: 0,
    horizontalOrder: false
};

var ReachedMaxScroll = false;
var PreviousScrollTop = 0;
var ScrollInterval, DivElmnt, scrollTime;



const speedUpPixel = (scrollDelay) => {
    switch (scrollDelay) {
        case 80:
            return 20;
        case 70:
            return 20;
        case 50:
            return 20;
        case 30:
            return 20;
        case 20:
            return 20;
        default:
            return 20;
    }
}

class HueCardWaterfall extends PureComponent {
    state = {
        rootWidth: document.getElementById('root').clientWidth - 1,
        wHeight: window.innerHeight,
        height: 0,
        switcher: 0,
        aiy: 0,
        count: 0,
        loading: false,
        scrollheight: 0,
        postWidth: 0,
        cardNumber: 0,
        aftereffect: null,
        is_condition: false,
        items: Array.from({ length: 30 }),
        itemCount: 0,
        PreviousScrollget: 0,
        ReachedMaxScroll: false,
        socialpopopen: false,
        highestNumber: 3,
        repeatTimes: null,
        randomNumber: 0,
        reversedata: [],
        mouseEnter: 0,
        count: 0,
        mouseover: false,
    }

    componentDidMount() {
        /* ==============FOR POST RESPONSIVE============== */
        setTimeout(() => {
            this.setState({ rootWidth: document.getElementById('root').clientWidth })
        }, 2000);
        this.onResponsiveTheme();
        window.addEventListener("resize", () => {
            this.setState({ rootWidth: document.getElementById('root').clientWidth, wHeight: window.innerHeight }, () => this.onResponsiveTheme())
        })

        /* ==============FOR MOUSE SCROLL STOP============== */
        window.addEventListener("wheel", e => this.pauseDiv(e), { passive: true })
        this.onLoadTheme();
        this.scrollDiv_init();
        this.onUpdateSocketPost(this.props);

    }

    scrollDiv_init = () => {
        const { newApi } = this.props;
        DivElmnt = document.getElementById('wPosts');
        DivElmnt.scrollTop = 0;
        ScrollInterval = setInterval(() => this.scrollDiv(), speedUpPixel(newApi.wall.Personalization.scrollDelay));
        this.onScrollWPosts();
    }
    onScrollWPosts = () => {
        DivElmnt = document.getElementById('wPosts');
        var dataPost = document.getElementById("wPosts");
        if (dataPost) {
            if (window.innerHeight > dataPost.clientHeight) {
                DivElmnt.setAttribute("style", "overflow: unset;display:flex;height: 100vh;")
            }
            else {
                DivElmnt.setAttribute("style", "overflow: auto;display:flex;height: 100vh;")
            }
        }
    }

    scrollDiv = () => {
        if (!ReachedMaxScroll) {
            DivElmnt.scrollTop = PreviousScrollTop;
            PreviousScrollTop++;
            ReachedMaxScroll = DivElmnt.scrollTop >= (DivElmnt.scrollHeight - window.innerHeight) - 10;
        }
        else {
            ReachedMaxScroll = (DivElmnt.scrollTop == 0) ? false : true;
            DivElmnt.scrollTop = PreviousScrollTop;
            PreviousScrollTop--;
        }
    }


    pauseDiv = event => {
        if (ScrollInterval) clearInterval(ScrollInterval);
        if (scrollTime) clearTimeout(scrollTime)
        scrollTime = setTimeout(() => this.resumeDiv(), 7000);
    }

    resumeDiv = () => {
        const { newApi } = this.props;
        DivElmnt = document.getElementById('wPosts');
        PreviousScrollTop = DivElmnt.scrollTop;
        ScrollInterval = setInterval(() => this.scrollDiv(), speedUpPixel(newApi.wall.Personalization.slideDelay));

    }


    onLoadTheme = () => {
        const { newItems } = this.props;
        if (newItems && newItems.length > 0) setTimeout(() => this.setState({ loading: false, is_visible: false }), 1000)

    }
    componentDidUpdate(prevProps, prevState) {
        const { newItems } = prevProps;
        if (newItems != this.props.newItems) setTimeout(() => this.setState({ loading: false, is_visible: false }, () => {
            if (ScrollInterval) clearInterval(ScrollInterval);
            if (scrollTime) clearTimeout(scrollTime)
            this.scrollDiv_init();
            this.onUpdateSocketPost(this.props);
        }), 1000)
    }


    onUpdateSocketPost = (props) => {
        const { itemCount } = props;
        this.setState({ itemCount: itemCount })
    }

    /* ==============FOR Theme Responsive ============== */
    onResponsiveTheme = () => {
        const { newApi } = this.props;
        const { rootWidth } = this.state;
        let cardNumber = null; let adjustSpacing = null; let spacing = null; let adjustWidth = null;
        const postWidth = newApi.wall.Personalization.minimumPostWidth;
        cardNumber = (rootWidth) / (postWidth);
        let rowCard = Math.trunc(cardNumber);
        spacing = (rootWidth) % postWidth;
        adjustSpacing = spacing / rowCard;
        if (cardNumber >= 1) adjustWidth = postWidth + adjustSpacing;
        else adjustWidth = postWidth - adjustSpacing;
        this.setState({ postWidth: adjustWidth, cardNumber })
    }
    componentWillReceiveProps(nextProps) {
        this.onUpdateSocketPost(nextProps);
    }
    render() {
        const { loading, is_visible, itemCount } = this.state;
        let { newItems, newApi } = this.props;
        const breakpointColumnsObj = {
            default: 5,
            1920: 3,
            1100: 3,
            900: 2,
            500: 1
        };
        return <><div id="marqueecontainer">
            <Masonry id="wPosts"
                style={{ display: 'flex', overflow: 'auto', height: '100vh' }}
                breakpointCols={breakpointColumnsObj}
                className={`my-masonry-grid theme${newApi.wall.Personalization.signageTheme}`}
                columnClassName="my-masonry-grid_column"
                // onMouseOut={() => this.resumeDiv()}
                onPointerEnter={() => this.pauseDiv()}>

                 {
                    newItems.map((item, itemKey) => {
                        const networkd = findNetworkDataFromWebFilters(item.networkId, newApi.webFilters);

                        if (itemCount < (itemKey + 1)) this.setState({ itemCount: 0 });

                        if (newItems && ((newItems.length - 1) == itemKey)) {
                            setTimeout(() => {
                                let dataUpdateImage = document.querySelectorAll("img[data-is-optmized='0']");
                                if (dataUpdateImage && dataUpdateImage.length > 0) {
                                    dataUpdateImage.forEach((item) => {
                                        item.setAttribute("src", item.getAttribute("data-src"))
                                        item.setAttribute("data-is-optmized", "1")
                                    })
                                }
                            }, 1500)
                        }

                        return <div key={itemKey} id={item.id}>
                            <ScrollAnimation className={`fadein${itemKey + 1}`} animateIn='fadeIn' delay={(itemKey + 1) * 10} animationInDuration={3000} animateOnce={true} animatePreScroll={true} style={{ opacity: 1 }}>
                                {
                                    (itemCount > 0 && itemCount >= (itemKey + 1)) ? <AnimatedSocialWallItem key={item.id} item={item} itemKey={itemKey} newApi={newApi} loading={loading} /> : <ErrorBoundary><SocialWallItem networkd={networkd} item={item} itemKey={itemKey} key={item.id} popupid={item.id} newApi={newApi} newItems={newItems} loading={loading} /></ErrorBoundary>
                                }
                            </ScrollAnimation>
                        </div>
                    })
                }


            </Masonry>


        </div >

        </>
    }
}
export default HueCardWaterfall;