import axios from 'axios';
import { UPDATE_INSTAGRAM_IMAGE_VIDEO, UPDATE_COMMERCE_ONSITE_UPLOAD } from '../constants'
export const getShowLoadingScreen = (path, headers) => axios.get(path, headers);
export const getResults = (path, headers) => axios.get(path, headers);
export const getSocketData = (path, headers) => axios.get(path);
export const getDemoPost = (path, headers) => axios.get(path, { timeout: (1000 * 60 * 7) });

export const updateInstagramPostImage = (data) => {
    axios.post(`${UPDATE_INSTAGRAM_IMAGE_VIDEO}`, data)
        .then((response) => { })
        .catch((error) => {
            console.error(error);
        });
};

export const submitPostData = (data) => {
    return axios.post(`${UPDATE_COMMERCE_ONSITE_UPLOAD}`, data)
}