import React, { PureComponent, memo, Suspense } from 'react';
import $ from 'jquery'
import Skeleton from 'react-loading-skeleton'
import Masonry from "react-masonry-component";
import "../../../comoncss/modrn-watrfall.css"
import NetworkCardComponent from '../../networkCardComponent'
import AuthorDetails from '../../sepcomponents/AuthorDetails'
import FontFamilyForPost from '../../sepcomponents/FontFamilyForPost'
import ItemRender from '../../sepcomponents/ItemRender';
import { themeAutoScrolling, findNetworkDataFromWebFilters } from './../../../utils'

const masonryOptions = {
    transitionDuration: 0,
    horizontalOrder: true
};


var ReachedMaxScroll = false;
var PreviousScrollTop = 0;
var ScrollInterval, DivElmnt;

const speedUpPixel = (scrollDelay) => {
    switch (scrollDelay) {
        case 80:
            return 60;
        case 70:
            return 40;
        case 50:
            return 30;
        case 30:
            return 20;
        case 20:
            return 10;
        default:
            return 20;
    }
}

class ModernTheme extends PureComponent {

    state = {
        rootWidth: document.getElementById('root').clientWidth - 1,
        wHeight: window.innerHeight,
        height: 0,
        switcher: 0,
        aiy: 0,
        newPersons: [],
        count: 0,
        loading: false,
        scrollheight: 0,
        postWidth: 0,
        cardNumber: 0
    }

    componentDidMount() {
        /* FOR POST RESPONSIVE */
        setTimeout(() => {
            this.setState({ rootWidth: document.getElementById('root').clientWidth })
        }, 2000);

        this.onResponsiveTheme();
        window.addEventListener("resize", () => {
            this.setState({ rootWidth: document.getElementById('root').clientWidth, wHeight: window.innerHeight }, () => this.onResponsiveTheme())
        })
        /* ==============FOR MOUSE SCROLL STOP============== */
        window.addEventListener("wheel", e => e.preventDefault(), { passive: false })
        this.onLoadTheme();
    }

    scrollDiv_init = () => {
        DivElmnt = document.getElementById('marqueecontainer');
        ReachedMaxScroll = false;
        DivElmnt.scrollTop = 0;
        PreviousScrollTop = 0;
        this.onScrollData();
        this.onScrollWPosts();
    }

    onScrollData = () => {
        const { newApi } = this.props;
        //console.log("speedUpPixel(newApi.wall.Personalization.scrollDelay)", speedUpPixel(newApi.wall.Personalization.scrollDelay))
        ScrollInterval = setInterval(() => {
            this.scrollDiv();
        }, speedUpPixel(newApi.wall.Personalization.scrollDelay));
    }

    onScrollWPosts = () => {
        const { newApi } = this.props
        DivElmnt = document.getElementById('marqueecontainer');
        var dataPost = document.getElementById("wPosts");
        console.log(window.innerHeight > dataPost.clientHeight, 'window.innerHeight > dataPost.clientHeight')
        if (dataPost) {
            if (window.innerHeight > dataPost.clientHeight) {
                //DivElmnt.setAttribute("style", "overflow: unset;")
                var margin = window.innerHeight;
                var min_height = dataPost.clientHeight;
                dataPost.setAttribute("style", `position: relative; margin-bottom: ${margin}px; min-height:${min_height}px`)
            }
            else {
                DivElmnt.setAttribute("style", "overflow: auto;")
            }
        }
    }

    scrollDiv = () => {
        if (ScrollInterval) clearInterval(ScrollInterval)
        if (!ReachedMaxScroll) {
            DivElmnt.scrollTop = PreviousScrollTop;
            PreviousScrollTop++;
            if (this.props.newApi.wall.Personalization.wallId == 122464) ReachedMaxScroll = DivElmnt.scrollTop >= ($("#wPosts").height() - DivElmnt.offsetHeight)
            else {
                ReachedMaxScroll = DivElmnt.scrollTop >= (DivElmnt.scrollHeight - DivElmnt.offsetHeight) - 10;
            }



            // ReachedMaxScroll = DivElmnt.scrollTop >= (DivElmnt.scrollHeight - DivElmnt.offsetHeight) - 10;
            //console.log("DivElmnt.scrollTop", ReachedMaxScroll, DivElmnt.scrollTop, PreviousScrollTop, DivElmnt.scrollHeight, DivElmnt.offsetHeight, DivElmnt.scrollTop >= (DivElmnt.scrollHeight - DivElmnt.offsetHeight))
            this.onScrollData();
        }
        else {

            //console.log("DivElmnt.scrollTop else", DivElmnt.scrollTop)
            DivElmnt.scrollTop = PreviousScrollTop = 0;
            ReachedMaxScroll = false;
            this.onScrollData();
        }
    }

    onLoadTheme = () => {
        const { newItems } = this.props;
        if (newItems && newItems.length > 0) setTimeout(() => this.setState({ loading: false }, () => this.scrollDiv_init()), 1000)
    }

    componentWillUnmount() {
        //clearInterval(this.state.newPersons);
    }

    /* ============== FOR Vertical Scroll Loop ============== */

    onResponsiveTheme = () => {
        const { newApi } = this.props;
        const { rootWidth } = this.state;
        let cardNumber = null; let adjustSpacing = null; let spacing = null; let adjustWidth = null;
        const postWidth = newApi.wall.Personalization.minimumPostWidth;
        cardNumber = (rootWidth) / (postWidth);
        let rowCard = Math.trunc(cardNumber);
        spacing = (rootWidth) % postWidth;
        adjustSpacing = spacing / rowCard;
        if (cardNumber >= 1) adjustWidth = postWidth + adjustSpacing;
        else adjustWidth = postWidth - adjustSpacing;
        this.setState({ postWidth: adjustWidth, cardNumber, wHeight: window.innerHeight })
    }

    componentDidUpdate(prevProps, prevState) {
        const { newItems } = prevProps;
        if (newItems != this.props.newItems) setTimeout(() => this.setState({ loading: false }, () => {
            if (ScrollInterval) clearInterval(ScrollInterval)
            this.scrollDiv_init()
        }), 1000)
    }


    render() {
        const { loading, postWidth } = this.state;
        let { newItems, newApi } = this.props;
        return <div id="marqueecontainer" style={{ overflow: 'auto' }}>
            <Masonry id="wPosts"
                className={`theme${newApi.wall.Personalization.signageTheme}`}
                elementType={"div"}
                options={masonryOptions}
                disableImagesLoaded={false} // default false
                updateOnEachImageLoad={true} // default false and works only if disableImagesLoaded is false
            >
                {
                    newItems.map((item, itemKey) => {
                        const networkd = findNetworkDataFromWebFilters(item.networkId, newApi.webFilters);
                        if (newItems && ((newItems.length - 1) == itemKey)) {
                            setTimeout(() => {
                                let dataUpdateImage = document.querySelectorAll("img[data-is-optmized='0']");
                                if (dataUpdateImage && dataUpdateImage.length > 0) {
                                    dataUpdateImage.forEach((item) => {
                                        item.setAttribute("src", item.getAttribute("data-src"))
                                        item.setAttribute("data-is-optmized", "1")
                                    })
                                }
                            }, 1500)
                        }

                        /*<div key={itemKey} id={item.id}></div>*/

                        return <div key={itemKey} id={`"postId${item.id}`} className={`feedId${item.feedId} postItem item flatCard ${networkd.name}Taggbox ${item.theme48.class1 === 'onlyTextCard' ? 'onlyTextCard' : ''} completeFadeIn`}
                            data-post-id={item.id} data-highlight={item.highlight} data-pin={item.pin}
                            style={{ padding: newApi.wall.Personalization.padding / 2, margin: 0, width: postWidth, minWidth: 0, left: 0, top: 0 }}
                            data-created={item.createdAt} >
                            <div className="post" style={{ backgroundColor: newApi.wall.ThemeRule.cardColor }}>
                                <div className="postContent">
                                    {loading ? <Skeleton height={300} /> : <Suspense fallback={<Skeleton height={300} />}><>

                                        <ItemRender network={networkd} newApi={newApi} item={item} itemKey={itemKey} key={`"render-${item.id}`} />
                                        <div className="postContentCard" >
                                            <h4 margin-top="0px"> <AuthorDetails item={item} personalization={newApi.wall.Personalization} backgroundColor={networkd.color} font={newApi.wall.ThemeRule} network={networkd} />
                                                <NetworkCardComponent network={networkd} item={item} themeRule={newApi.wall.ThemeRule} />
                                            </h4>
                                            <FontFamilyForPost item={item} newApi={newApi} personalization={newApi.wall.Personalization} themeRule={newApi.wall.ThemeRule} network={networkd} signageTheme={newApi.wall.Personalization.signageTheme} ownerID={newApi.wall.Wall.owner} />

                                        </div>
                                    </></Suspense>}</div>
                            </div>
                        </div>
                    })
                }

            </Masonry>
        </div >
    }
}
export default memo(ModernTheme);