import React, { Component } from "react";
import { v4 as uuidv4 } from 'uuid';
import { IFrame } from './iframe'
import ClockApp from './clockApp'


const toAbsoluteUrl = pathname => process.env.PUBLIC_URL + pathname;
const ClockPreview = (porps) => {
let iframeID=uuidv4();
   return <IFrame key={uuidv4()} id={iframeID}>
        <link rel="stylesheet" href={toAbsoluteUrl("/css/clocks.css")} />
        <link rel="stylesheet" href={toAbsoluteUrl("/css/inter-fonts.css")} />
        <ClockApp {...porps} iframeID={iframeID}/>
    </IFrame>
}
export default ClockPreview;