import React, { Component } from "react";
import FullCalendar, { formatDate } from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import listPlugin from '@fullcalendar/list'
import moment from 'moment';

//import "@fullcalendar/core/main.css";

class MonthCalendar extends Component {
    state = {
        weekendsVisible: true, upcomingEve: [], allEvents: []
    }
    componentDidMount() {
        setTimeout(() => {
            window.dispatchEvent(new Event('resize'))
        }, 500);
    }


    render() {
        const { upcomingEve, allEvents, upcomingEvents } = this.props;
        return (
            <div className='cldtd_calendar_card'>
                <div className='cldtd_calendar_card_main'>
                    <FullCalendar
                        plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
                        headerToolbar={{
                            left: '',
                            center: 'title',
                            right: ''
                        }}
                        displayEventTime={true}
                        initialView='dayGridMonth'
                        events={allEvents}

                        eventColor="#D1A273"
                        //dayMaxEvents={2}
                        eventDisplay='background'
                    // initialEvents={INITIAL_EVENTS}
                    //eventsSet={this.handleEvents}
                    />
                </div>
                {this.renderSidebar()}
            </div>
        )
    }
    renderSidebar() {
        return (
            <div className='cldtd_calendar_card_sidebar'>
                <div className='cldtd_calendar_card_sidebar_section'>
                    <div className="cldtd_calendar_card_sidebar_date">Upcoming Events</div>
                    <div className="cldtd_calendar_card_eventSection">
                        <FullCalendar
                            plugins={[listPlugin]}
                            headerToolbar={{
                                left: '',
                                center: '',
                                right: ''
                            }}
                            initialView='listMonth'
                            timeZone='local'
                            eventLimit={4}

                            dayMaxEventRows={2}
                            events={this.props.upcomingEvents}
                        />
                    </div>

                </div>
            </div>
        )
    }
}


export default MonthCalendar

