import React, { PureComponent, memo } from 'react';
import moment from 'moment';
import "./../../css/feature-popup.css"
import NetworkIcon from '../networkIcon'
import FontFamModal from './../sepcomponents/FontFamModal'
import VideoImageRender from './../sepcomponents/VideoImageRender'
import { modalPostCssClass, seprateDotStyle, authorHandleNameStyle, findNetworkDataFromWebFilters } from './../../utils'
import Ratings from './../themes/ModernWaterFall/ratings'
import AuthorAvatar from '../sepcomponents/AuthorAvatar';
import ProfileImage from './profileImage'

class PopupOnOff extends PureComponent {
  state = {
    isPopUpHide: true,
    isDataCount: -1,
    checktimeout: 0,
    isAuthorImagevalid: true
  }

  videoChange = event => {
    this.setState({ isPopUpHide: true }, () => this.onLoadedPopUp())
  }
  loadPopUpWithTimer = () => {

    const { newApi } = this.props;

    const { checktimeout } = this.state;

    const updatetime = ((newApi.wall.ThemeRule.timeForPopup) * 1000);

    setTimeout(() => {

      const timer = newApi.wall.ThemeRule.timeForPopup === 0 ? 5000 : updatetime;

      if (checktimeout == 0) {
        this.setState({ checktimeout: 1 })
        this.setState({ isPopUpHide: true }, () => setTimeout(() => this.onLoadedPopUp(), timer))
      } else {
        this.setState({ isPopUpHide: false }, () => setTimeout(() => this.onLoadedPopUp(), timer))
      }

    }, newApi.wall.ThemeRule.timeForPopup === 0 ? 5000 : updatetime, { position: 1 })
  }


  onLoadedPopUp = () => {

    const { checktimeout } = this.state;

    if (checktimeout == 1) {

      const { newApi, newItems } = this.props;
      const { isDataCount } = this.state;

      this.setState({ checktimeout: 0 });

      if (isDataCount >= (newItems.length - 1)) this.setState({ isDataCount: 0, isPopUpHide: false }, () => this.loadPopUpWithTimer())
      else this.setState({ isDataCount: isDataCount + 1, isPopUpHide: false }, () => this.loadPopUpWithTimer())
    }
  }
  componentDidMount() {
    const { newApi, newItems } = this.props;
    if (newItems && newApi.wall.Personalization.postFeatured === 1) this.loadPopUpWithTimer();
  }

  render() {
    const { isPopUpHide, isDataCount } = this.state;
    let { newApi, newItems, windowWidth, windowHeight } = this.props;
    let popUpData = newItems[isDataCount];
    const netwrokd = popUpData !== undefined && findNetworkDataFromWebFilters(popUpData.networkId, newApi.webFilters);
    //console.log("popUpData", popUpData)
    return (
      <React.Fragment>
        {((popUpData !== undefined && !isPopUpHide)) &&
          <div id="modalPost" className={`modalId${popUpData.id} taggModalShow tb_modal tb_fade ${modalPostCssClass(popUpData)} themeModal${newApi.wall.Personalization.theme} poweredbytaggbox__ ${isPopUpHide === true ? '' : 'show'}`}>
            <div className="tb_modal_dialog taggModal">
              <div className="tb_conetent">
                <div className="tb_modal_body">
                  <div className={`tb_details_left post_network-${popUpData.networkId}`} >
                    <div className="tb_media_holder">
                      {/* <div className="tb_img_holder">
                              <img className="tb-detail-image" src={popUpData.filename} alt="" height="1000" width="1000" />
                          </div> */}
                      <VideoImageRender person={popUpData} videoChange={this.videoChange} newApi={newApi.wall} />
                    </div>
                  </div>
                  <div className="tb_details_right" style={{ backgroundColor: newApi.wall.ThemeRule.cardColor }}>
                    <div className="tb_content_wrap">
                      <div className="tb_post_detail_wrap">
                        <div className="tb_post_author">
                          <div className="tb_author_details">
                            <div className="tb_author_media" style={{ display: newApi.wall.Personalization.postAuthor === 1 && popUpData.author.isInstaUser === false ? "flex" : "none" }}>
                              {/* {this.state.isAuthorImagevalid && <img className="img-fluid" src={popUpData.author.picture}

                                onError={(e) => {
                                  this.setState({
                                    isAuthorImagevalid: false
                                  });
                                  //console.clear()
                                }}
                                alt=""
                              />}


                              {!this.state.isAuthorImagevalid && <AuthorAvatar style={{ display: (popUpData.postAuthor === 0 || popUpData.author.isInstaUser) ? 'none' : 'flex' }} username={popUpData.author.name} backgroundColor={netwrokd.color} color={newApi.wall.ThemeRule.fontColor} />} */}
                              <ProfileImage popUpData={popUpData} newApi={newApi} key={popUpData.id} netwrokd={netwrokd}/>

                              <div className="postAuthorName" style={{ display: (newApi.wall.Personalization.postAuthor === 1 || popUpData.author.isInstaUser) ? 'none' : 'block', color: newApi.wall.ThemeRule.authorColor }}>
                                {popUpData.author.name}
                              </div>


                            </div>
                            <div className="tb_author_deatils" style={{ display: newApi.wall.Personalization.postAuthor === 1 && popUpData.author.isInstaUser === false ? "block" : "none" }}>
                              <strong className="tb_author_usrname" style={{ color: authorHandleNameStyle(newApi) }} target="_blank">{popUpData.author.name}</strong>
                              <div className="tb_author_info">
                                <span className="tb_author_handlename" style={{ color: authorHandleNameStyle(newApi) }} target="_blank"> @{popUpData.author.username}</span>

                                <div className="sepratedot" style={{ color: seprateDotStyle(newApi) }}> <i className="fa fa-circle" aria-hidden="true"></i> </div>
                                <span className="tb_post_time" style={{ color: authorHandleNameStyle(newApi) }}>{moment(new Date(popUpData.createdAt * 1000)).fromNow()}</span>
                              </div>
                            </div>
                          </div>
                          <div className="tb_social_ico" data-network={netwrokd.name} style={{ borderBottomColor: newApi.wall.Personalization.signageTheme == 46 ? newApi.wall.ThemeRule.iconColor : '' }}>
                            <NetworkIcon network={netwrokd} activeCardId={popUpData} themeRule={newApi.wall.ThemeRule} />
                          </div>
                        </div>

                        <div className="tb_post_wrapper" ref={(divElement) => { this.divElement = divElement }}>
                          <FontFamModal person={popUpData} newApi={this.props.newApi} themeRule={newApi.wall.ThemeRule} windowWidth={windowWidth} windowHeight={windowHeight} />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>



        }
      </React.Fragment>
    )
  }
}

export default memo(PopupOnOff);