import React from "react"
import { CloudUrl } from '../../constants'

let socialIcon = null;
let defaultIcon = null;
export default class SocialNetwork extends React.Component {

    render() {
        switch (this.props.networkID) {
            case 1:
                socialIcon = 'tb_icon fa-twitter';
                defaultIcon = 'twitter';
                break;
            case 2:
                socialIcon = 'tb_icon fa-instagram';
                defaultIcon = 'instagram';
                break;
            case 3:
                socialIcon = 'tb_icon fa-facebook';
                defaultIcon = 'facebook';
                break;
            case 4:
                socialIcon = 'tb_icon fa-google';
                defaultIcon = 'google';
                break;
            case 5:
                socialIcon = 'tb_icon fa-pinterest';
                defaultIcon = 'pinterest';
                break;
            case 6:
                socialIcon = 'tb_icon fa-flickr';
                defaultIcon = 'flickr';
                break;
            case 7:
                socialIcon = 'tb_icon fa-youtube';
                defaultIcon = 'youtube';
                break;
            case 8:
                socialIcon = 'tb_icon fa-vimeo-v';
                defaultIcon = 'vimeo';
                break;
            case 9:
                socialIcon = 'tb_icon fa-vine';
                defaultIcon = 'vine';
                break;
            case 10:
                socialIcon = 'tb_icon fa-linkedin-in';
                defaultIcon = 'linkedin';
                break;
            case 11:
                socialIcon = 'tb_icon fa-tumblr';
                defaultIcon = 'tumblr';
                break;
            case 12:
                socialIcon = 'tb_icon2 fa-rss';
                defaultIcon = 'rss';
                break;
            case 13:
                socialIcon = 'tb_icon fa-star';
                defaultIcon = 'star';
                break;
            case 14:
                socialIcon = 'tb_icon fa-zomato';
                defaultIcon = 'zomato';
                break;
            case 15:
                socialIcon = 'tb_icon fa-workplace';
                defaultIcon = 'workplace';
                break;
            case 16:
                socialIcon = 'tb_icon fa-whatsapp';
                defaultIcon = 'whatsapp';
                break;
            case 17:
                socialIcon = 'tb_icon fa-envelope-o';
                defaultIcon = 'twitter';
                break;
            case 18:
                socialIcon = 'tb_icon fa-instagram';
                defaultIcon = 'instagram';
                break;
            case 19:
                socialIcon = 'tb_icon fa-yelp';
                defaultIcon = 'yelp';
                break;
            case 20:
                socialIcon = 'tb_icon fa-slack';
                defaultIcon = 'slack';
                break;
            case 21:
                socialIcon = 'tb_icon fa-yammer';
                defaultIcon = 'yammer';
                break;
            case 22:
                socialIcon = 'tb_icon fa-snapchat';
                defaultIcon = 'snapchat';
                break;
            case 23:
                socialIcon = 'tb_icon fa-airbnb';
                defaultIcon = 'airbnb';
                break;
            case 24:
                socialIcon = 'tb_icon fa-spotify';
                defaultIcon = 'spotify';
                break;
            case 25:
                socialIcon = 'tb_icon fa-soundcloud';
                defaultIcon = 'soundcloud';
                break;
            case 26:
                socialIcon = 'tb_icon fa-giphy';
                defaultIcon = 'giphy';
                break;
            case 27:
                socialIcon = 'tb_icon fa-deviantart';
                defaultIcon = 'deviantart';
                break;
            case 30:
                socialIcon = 'tb_icon fa-vkontakte';
                defaultIcon = 'vkontakte';
                break;
            default:
                socialIcon = '';
        }
        return (
            <div className={this.props.iconColor.iconType == 505 ? 'tb_svg_icon' : socialIcon} style={{ color: this.props.iconColor.iconType == 1 ? this.props.iconColor.iconColor : '' }} network-id={this.props.networkID}>
                {(this.props.iconColor.iconType == 505 && socialIcon != '' && this.props.networkID != 0) ? <img src={`${CloudUrl}/network-icon/${defaultIcon}.svg`} /> : ''}
            </div>
        )
    }

}
