import React, { PureComponent, Fragment, Suspense, memo } from "react"
import "../../../css/zoomSlideshow.css";
import './../../../css/slidshows.css'
import SlideShowSkeleton from '../SlideShowSkeleton'
import AnimationOverlay from './animationOverlay'
import { findNetworkDataFromWebFilters, renewSlideShowImage } from '../../../utils'
import { INSTAGRAM_IMAGE_BLANK, CloudUrl } from '../../../constants'
const Posts = React.lazy(
    () =>
        new Promise((resolve, reject) =>
            setTimeout(() => resolve(import("./post")), 1000)
        )
);
class SlideShow extends PureComponent {
    state = {
        isLoading: true,
        isAnimation: true,
        isActiveIndex: -1,
        activeData: null,
        activeVideoLength: 0,
        active: "",
        newImage: ""
    }


    componentDidMount() {
        this.onChangeNextData();

    }

    onChangeNextData = () => {
        const { newItems } = this.props;
        const { isActiveIndex, activeVideoLength } = this.state;
        if (activeVideoLength === 0) {
            if (isActiveIndex >= (newItems.length - 1)) {
                const dataItem = newItems[0];
                const newImage = renewSlideShowImage(dataItem.networkId, dataItem.carausel, dataItem.file, dataItem.postFileNew)
                this.setState({ isActiveIndex: 0, activeData: dataItem, active: "", newImage }, () => this.onRepeatLoad())
            }
            else {
                const dataItem = newItems[(isActiveIndex + 1)];
                const newImage = renewSlideShowImage(dataItem.networkId, dataItem.carausel, dataItem.file, dataItem.postFileNew)
                this.setState({ isAnimation: (isActiveIndex + 1) === 0 ? false : true, isActiveIndex: isActiveIndex + 1, activeData: dataItem, active: "", newImage }, () => this.onRepeatLoad())
            }
        }
    }

    onRepeatLoad = () => {
        const { timer, newApi } = this.props;
        const { activeVideoLength, activeData } = this.state;
        if (activeVideoLength === 0) {
            setTimeout(() => this.setState({ active: "active" }), 200)
            setTimeout(() => this.setState({ isAnimation: false }), 700)
            setTimeout(() => {
                this.setState({ isLoading: false }, () => this.onChangeNextData())
            }, timer)
        }
    }


    getVideoProgressDuration = (data) => {
        const { activeVideoLength } = this.state;
        let playleng = Math.floor(data.playedSeconds);
        if ((activeVideoLength - 1) === playleng) this.setState({ activeVideoLength: 0 }, () => this.onChangeNextData())

    }
    getVideoDuration = (data) => {
        const { newApi } = this.props;
        const { activeData } = this.state;
        if (newApi.wall.Personalization.playFullVideo === 1 && (activeData.type === 3 || activeData.type === 5)) {
            this.setState({ activeVideoLength: Math.floor(data) })
        }
    }
    updateNewImage = e => {
        this.setState({ newImage: e.target.src })
    }
    render() {
        const { newApi, height } = this.props;
        const { activeData, isAnimation, isActiveIndex, active, networkData, newImage } = this.state;
        var typeClass = ''; var blurStyle = {};
        var columnLeftClass = 'col-sm-6';
        var columnRightClass = 'col-sm-6';
        if (activeData && activeData.id) {
            if (activeData.type === 1) {
                columnLeftClass = 'hidden';
                columnRightClass = 'col-sm-12';
                typeClass = 'onlyTextPost';
            }
            else if (activeData.type === 4 || activeData.type === 5) typeClass = 'postTypeImg';

            var mediaClass = 'makeBgImg';
            if ((activeData.type === 3 || activeData.type === 5)) mediaClass = 'blackBgVideo';
            // else if ((activeData.type === 3 || activeData.type === 5) && activeData.link.includes("instagram")) mediaClass = 'makeBgImg';
            if (activeData.file) {
                if ((activeData.networkId === 2 || activeData.networkId === 18) && activeData.carausel === 0) {
                    blurStyle = { backgroundImage: `url(${newImage}),url(${INSTAGRAM_IMAGE_BLANK})` }
                }
                else {
                    blurStyle = { backgroundImage: `url(${newImage}),url(${INSTAGRAM_IMAGE_BLANK})` }
                }
            }
            else {
                blurStyle = { backgroundImage: `url(${CloudUrl}/images/blank.jpg)` }
            }
            var blurClass = '';
            if (newApi.wall.ThemeRule.blurBackground === 0) {
                blurStyle = { backgroundColor: newApi.wall.ThemeRule.blurColor, filter: 'blur(0px)', WebkitFilter: 'blur(0px)', MozFilter: 'blur(0px)', opacity: 1 }
                blurClass = 'blurRemoved';
            }
        }

        return (

            newApi.wall && <Fragment>
                {newApi.wall.Personalization.animateBackground === 1 && activeData && newApi.wall.Personalization.signageTheme == 35 && <AnimationOverlay active={isAnimation} transitionStatus={newApi.wall.ThemeRule.transitionStatus} transition={newApi.wall.ThemeRule.transition} network={(activeData && activeData.id ? findNetworkDataFromWebFilters(activeData.networkId, newApi.webFilters) : '')} />}
                <div id="wPosts" className={`theme${newApi.wall.Personalization.signageTheme}`} style={{ height: 'inherit' }}>
                    <div className={`${newApi.wall.Personalization.signageTheme == 21 || newApi.wall.Personalization.signageTheme == 37 ? 'tb_slide_show_wrapper' : 'tb_slide_show_wrapper_no_padding'}`}>
                        <div className="grid-sizer flatCard col-ms-6 col-xs-12"></div>
                        {activeData && activeData.id && <Suspense fallback={<SlideShowSkeleton height={height} />}>{ active ? <Posts updateNewImage={this.updateNewImage} activeData={activeData} typeClass={typeClass} blurStyle={blurStyle} blurClass={blurClass} mediaClass={mediaClass} columnLeftClass={columnLeftClass} columnRightClass={columnRightClass} newApi={newApi} getVideoProgressDuration={this.getVideoProgressDuration} getVideoDuration={this.getVideoDuration} activeIndex={isActiveIndex} active={active} /> : null }</Suspense>}
                    </div>
                </div>

            </Fragment >
        )
    }

}

export default memo(SlideShow)