import React, { Component } from "react";
import moment from 'moment'
import 'moment-timezone';
import FullCalendar, { formatDate } from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import listPlugin from '@fullcalendar/list'

//import './day.css'
class DayCalendar extends Component {
    state = {
        weekendsVisible: true,
        calendar: '',
    }


    render() {
        const { upcomingEve, allEvents, upcomingEvents } = this.props;
        const nowDate = new Date()
        return (
            <div className='cldtd_calendar_card'>
                <div className='cldtd_calendar_card_main_day'>
                    <div className='cldtd_calendar_card_DateDay'>
                        {moment(nowDate).format('dddd')},&nbsp;{moment(nowDate).format('D')}
                    </div>
                    <div className='cldtd_calendar_card_YearMonth'>
                        {moment(nowDate).format('MMMM')}&nbsp;
                        {moment(nowDate).format('YYYY')}
                    </div>
                </div>
                {this.renderSidebar()}
            </div>
        )
    }
    renderSidebar() {
        return (
            <div className='cldtd_calendar_card_sidebar_day'>
                <div className='cldtd_calendar_card_sidebar_section'>
                    <div className="cldtd_calendar_card_sidebar_date">Upcoming Events</div>
                    <div className="cldtd_calendar_card_eventSection">
                        <FullCalendar
                            plugins={[listPlugin]}
                            headerToolbar={{
                                left: '',
                                center: '',
                                right: ''
                            }}
                            initialView='listDay'
                            timeZone='local'
                            events={this.props.upcomingEvents}
                        />
                    </div>

                </div>
            </div>
        )
    }
}

export default DayCalendar

