import React, { PureComponent } from 'react';
import { instanceOf } from 'prop-types';
import { withCookies, Cookies } from 'react-cookie';
import { API_ROOT_URL, CloudUrl, VERIFY_IDS } from '../../constants'

import '../popUp/assets/css/upgrade-popup-style.css'


const styles = {
    brandingClose: {
        display: 'inline-block',
        marginTop: 0,
        marginRight: 0,
        backgroundRepeat: 'no-repeat!important',
        outline: 0,
        cursor: 'pointer'
    }

}
class TaggboxBrandingTicker extends PureComponent {
    static propTypes = {
        cookies: instanceOf(Cookies).isRequired
    };
    onCreateCookies = event => {
        const { cookies } = this.props;
        var date = new Date();
        date.setTime(date.getTime() + (1 * 60 * 60 * 1000));
        cookies.set('planUpgrade', date, { path: '/', expires: date });
    }
    render() {
        const { see, newApi, dismissPopUp, poweredByTag } = this.props;
        return (<React.Fragment>
            <div className={`tb_upgrade_001 ticker_upgrade_ ${see ? 'tb_show_02_' : ''}`}>
                <div className="tb_popup_wrap_002">
                    <div className="tb_popup_wrap_in__">
                        <div className="tb_popup_body_">
                            <div onClick={dismissPopUp} className="tb_close_btn00">
                                <img src={`${CloudUrl}/images/remove-icon-small-light.svg`} />
                            </div>
                            <div className="tb_ticker_content__003">
                                <div className="tb_pera__004">
                                    Wish to upgrade your plan to remove Taggbox's branding?
                                </div>
                                <div className="tb_popup_btn_004">
                                    {
                                        newApi.wall != null && parseInt(newApi.wall.Wall.owner) && VERIFY_IDS.includes(newApi.wall.Wall.owner) ?
                                            <a target="_blank" className="tb_btn__005" href={`${API_ROOT_URL}events`} onClick={this.onCreateCookies}>Upgrade Now!</a> :
                                            <a target="_blank" className="tb_btn__005" href="https://taggbox.com/price/display-price/">Upgrade Now!</a>
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {(see ? <div className={`.powerdBy tb_show_02_`}></div> : '')}

            <div className={`taggbox_branding ${see ? 'move__' : ''}`} style={{ display: newApi.wall.UserRule.branding ? '' : 'none' }} onClick={poweredByTag}>
                <div className="taggbox_branding_00" data-toggle="modal" data-target="#taggboxBranding_div_modal" onClick={dismissPopUp}>
                    <img src={`${CloudUrl}/images/PoweredBy-taggbox.svg`} alt="Taggbox" ></img>
                </div>
            </div>

        </React.Fragment>)
    }
}

export default withCookies(TaggboxBrandingTicker);