import React, { useState } from "react";
import AuthorAvatar from './../sepcomponents/AuthorAvatar'
import moment from 'moment';


const AuthorIconIMG = ({ picture, name, color, isInstaUser, backgroundColor }) => {
  const [isImageUrlError, setIsImageUrlError] = useState(false);
  return <div className="authorIcoBg" style={(isImageUrlError && name) ? { padding: 2, display: isInstaUser ? "none" : "" } : { display: isInstaUser ? "none" : "" }}>
    {!isImageUrlError && <img
      src={picture}
      onError={(e) => { setIsImageUrlError(true) }}
      alt=""
    />}
    {(isImageUrlError && name) && <AuthorAvatar style={{ display: name ? 'flex' : 'flex', alignItems: 'center' }} username={name} backgroundColor={backgroundColor} color={color} />}
  </div>
}


const AuthorName = ({ styleCss, name, isInstaUser }) => <div className="postAuthorName" style={{ styleCss, display: isInstaUser ? "none" : "" }} >{name}</div>

const AuthorHandle = ({ authrHandleStyle, username, isInstaUser, color }) => <>
  <span className="authrHandle" target="_blank" style={{ display: isInstaUser ? "none" : "", color: color.authorColor }} > @{username} </span>
</>

const AuthorTimeHandle = ({ seprateDotStyle, timePostStyle, isInstaUser, color, item, personalization }) => <>
  {personalization.postAuthor === 1 && <span className="sepratedot" style={{ display: isInstaUser ? "none" : "", seprateDotStyle, color: color.authorColor }} > <i className="fa fa-circle" aria-hidden="true"></i> </span>}
  <span className="timePost" style={{ display: isInstaUser ? "none" : "", color: color.authorColor, timePostStyle }} >{moment(new Date(item.createdAt * 1000)).fromNow()}</span>

</>

export { AuthorIconIMG, AuthorName, AuthorHandle, AuthorTimeHandle }