import React, { PureComponent, Fragment, memo } from "react";
import { isMobile } from "react-device-detect";
import { v4 as uuidv4 } from 'uuid';
import $ from 'jquery';
import axios from 'axios';
import io from "socket.io-client";
import "./css/all-fonts.css";
import "./css/users.css";
import "./App.css";
import "./css/animate.css";
import './comoncss/new-custom.css';
import ErrorComponent from './components/errors'
import BottomTicker from './components/Tickers/bottomTicker'
import TopTicker from './components/Tickers/topTicker';
import PlanTicker from './components/Tickers/planTicker';
import PageHeadSection from './components/pageHeadSection'
import ModelPopUp from './components/modelPopUp'
import ShowLoadingScreenComponent from './components/loadingComponent'
import AnnouncementsOverlayView from './components/announcementOverlay'
import BannerView from './components/banners';
import ThemeRoutes from './themeRoutes'
import TaggboxBranding from './components/taggboxBranding'
import UpgradeScreenPopup from "./components/popUp/UpgradeScreenPopup";
import { getResults } from './actions/action'
import queryString from "query-string"
import { S3_IMG_PATH, GET_DATA_API_PATH, SOCKET_CLIENT_URL, ERROR_BACKGROUND_IMG, IS_LIVE, API_WALL_STATUS } from './constants'
import { scrollBarCommonThemeBar, CONVERT_OBJ_TO_ARRAY, noPostErrorDisplayCss, IS_EDITOR, popUpEnable, isEnableFullScreenButton, IS_CHECK_WALL_KEY, getWallKey } from './utils';
import OnSiteUpload from './onsiteBanner'
import ErrorBoundary from './errorBoundary'
import { iniFrame } from '../utils'

var lastUpdate = 0;
class Themes extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      enableFullScreenBtnActive: true,
      isLoading: true,
      items: [],
      width: window.innerWidth,
      newItems: [],
      popupItems: [],
      see: false,
      wallId: '',
      themeIdwall: null,
      URL: this.props.mainUrl,
      height: window.innerHeight,
      BannerImageHeight: 0,
      counter: null,
      itemCount: 0,
      preRenderSocketData: [],
      preRenderCount: 0,
      socketData: [],
      wallErrorCode: null,
      isSocketAction: null,
      isSocketRemoveIDs: []


    };
  }

  dismissPopUp = event => {
    const { see } = this.state;
    this.setState({ see: !see })
  }

  componentDidMount() {
    const { wallErrorCode } = this.props;


    this.setState({
      URL: this.props.mainUrl,
      wallErrorCode: wallErrorCode
    })
    this.fetchResults();
    this.onEnableFullScreenBtnActive();
  }

  /* Full Screen Button */
  onEnableFullScreenBtnActive = () => {
    let themeid = queryString.parse(window.location.search);
    if (themeid.studio && themeid.studio == 1) {
      //this.setState({ enableFullScreenBtnActive: false })
      if (document.querySelector("#enableFullScreen")) {
        document.querySelector("#enableFullScreen").style.display = 'none'
      }
    }
    else {
      setTimeout(() => {
        if (document.querySelector("#enableFullScreen")) {
          document.querySelector("#enableFullScreen").style.display = 'none'
        }
        //this.setState({ enableFullScreenBtnActive: false })
      }, 7000);
    }


  }
  /* Get Wall Data From API */
  fetchResults = () => {
    const { socialWallId } = this.props;
    let getthemeid = queryString.parse(window.location.search);
    let tbPostsCount = {}
    if (getthemeid && getthemeid.tb_posts_count && getthemeid.tb_posts_count != null) {
      let tbPostCount = getthemeid.tb_posts_count;
      if (tbPostCount.includes("?digitalsignage=1")) {
        tbPostCount = tbPostCount.replace("?digitalsignage=1", "")
      }
      tbPostsCount = { "tb-post-count": tbPostCount }
    }
    let wallResuestKey = {}
    if (!IS_EDITOR && IS_CHECK_WALL_KEY) {
      wallResuestKey = { wallKey: getWallKey() }
    }
    let editorRequest = IS_EDITOR ? { editor: 1 } : {}
    const options = (getthemeid && getthemeid.themeId && getthemeid.themeId != null) ? {
      headers: {
        'Access-Control-Allow-Origin': '*',
        themeId: getthemeid.themeId.substr(0, 2),
        ...tbPostsCount,
        ...wallResuestKey
      }
    } : IS_EDITOR ? {
      headers: {
        'Access-Control-Allow-Origin': '*',
        ...editorRequest
      }
    } : IS_CHECK_WALL_KEY ? {
      headers: {
        'Access-Control-Allow-Origin': '*',
        ...wallResuestKey
      }
    } : {}


    getResults(`${GET_DATA_API_PATH}${socialWallId}`, options)
      .then(async res => {
        let datashow = res.data;
        //console.log(JSON.stringify(datashow));
        const { wall, postData, customPost, error_code } = res.data;
        let items = CONVERT_OBJ_TO_ARRAY(postData);
        ///let customdata = CONVERT_OBJ_TO_ARRAY(customPost);
        //console.log(items.length, customPost);

        if (customPost && customPost.length) {
          await customPost.map((cItem, index) => {
            const { status, after, every } = cItem;
            if (status === 1) {
              let customPostIndex = parseInt(after);
              for (customPostIndex; customPostIndex <= items.length; customPostIndex = customPostIndex + parseInt(every)
              ) {

                if (items.length > customPostIndex) {
                  if (items[customPostIndex].id != cItem.id) items.splice(customPostIndex, 0, cItem);
                }
              }
            }
          });
        }
        // console.log("items", items)

        if (error_code !== 404) {

          if (wall.Personalization && wall.Personalization.signageTheme) {
            if (wall.Personalization.signageTheme === 26) wall.Personalization.signageTheme = 58;
            else if (wall.Personalization.signageTheme === 38) wall.Personalization.signageTheme = 57;
            else if (wall.Personalization.signageTheme === 5) wall.Personalization.signageTheme = 56;
            else {
            }
          }
          // if (items && items.length == 0) {
          //   res.data.error_code = 201;
          // }
          let popupItems = [];
          if (wall.ThemeRule.hideContent === 1) {
            if (items && items.length > 0) {
              popupItems = items.filter((keyItem) => keyItem.type != 1);
            }
          }
          else {
            if (items && items.length > 0) {
              popupItems = items.filter((keyItem) => {
                if ((keyItem.type === 2) || (keyItem.type === 4) && keyItem.filename && keyItem.filename.length > 0) return keyItem;
                else if (keyItem.type === 1 && keyItem.content && keyItem.content.length > 0) return keyItem;
                else if ((keyItem.type === 3) || (keyItem.type === 5) && keyItem.filename && keyItem.filename.length > 0 && keyItem.mediaUrl && keyItem.mediaUrl.length > 0) return keyItem;
              });
            }
          }



          this.setState({
            wallErrorCode: this.props.wallErrorCode,
            wallId: socialWallId,
            newItems: items,
            popupItems: popupItems,
            isLoading: false,
            newApi: res.data,
            finalPath: wall.BannerImage && Object.keys(wall.BannerImage).length > 0 && wall.BackgroundImage.path !== null ? `${S3_IMG_PATH}${wall.BackgroundImage.path}${wall.BackgroundImage.newName}` : null,
            finalBannerPath: wall.BannerImage && Object.keys(wall.BannerImage).length > 0 && wall.BannerImage.path !== null ? `${S3_IMG_PATH}${wall.BannerImage.path}${wall.BannerImage.newName}` : null,
            postWidth: wall.Personalization.minimumPostWidth,
            planId: wall.UserDetail.planId
          }, () => this.wallConnectedWithSocket());
        } else this.setState({ isLoading: true, err: res.data.error_code })
      });
  }
  /* Connected With Socket  */
  wallConnectedWithSocket = () => {
    const { newApi, newItems, wallErrorCode } = this.state;
    const { isActiveSocket, preview, isStudio } = this.props;
    const screenLimitExceed = newApi.wall.UserRule.screenLimitExceed;
    this.onGetErrorData()
    /* isActiveSocket => for display and embed  */
    if (newApi && newApi.wall && newApi.wall.Wall && newApi.wall.Wall.id && isActiveSocket !== 1) {
      let screenLimit = newApi.wall.UserRule.screenLimit;
      let screens = newApi.wall.UserRule.screens;
      let planId = newApi.wall.UserDetail.planId;
      // let screenId = uuidv4();
      let screenId = this.onWindowLoad();
      // screenId = String(screenId).replace(/-/g, "");

      let queryStudio = false;
      const parsed = queryString.parse(window.location.search);
      if (parsed.studio && parsed.studio == 1) queryStudio = true;

      let screenFlag = `&screenId=${screenId}`; let oldScreenIDQuery = ``;
      let screenLFlag = `&screenLimit=${screenLimit}&screens=${screens}&planId=${planId}`
      if (isStudio) {
        screenFlag = ``; screenLFlag = ``;
      }
      let query = IS_EDITOR ? `` : `name=signage${screenFlag}&wallId=${newApi.wall.Wall.id}&owner=${newApi.wall.Wall.owner}${screenLFlag}&screenLimitExceed=${screenLimitExceed}&timeStamp=${Math.floor(Date.now() / 1000)}`
      if ((newApi.error_code === 213 || wallErrorCode === 213) && !IS_EDITOR) query = ``
      const SOCKET_CONNECTION = io(`${SOCKET_CLIENT_URL}`, {
        transports: ["websocket"],
        transport: ["websocket"],
        query: query,
        upgrade: true
      });
      const ON_EMIT_SOCKET_DATA = (params, callBack) => SOCKET_CONNECTION.on(params, callBack);

      //const activeScreenData = { screenId, wallID: newApi.wall.Wall.id, timeStamp: Math.floor(Date.now() / 1000) }
      setInterval(() => SOCKET_CONNECTION.emit("active-screen", { screenId, wallID: newApi.wall.Wall.id, timeStamp: Math.floor(Date.now() / 1000), screenLimitExceed }), 30000)

      ON_EMIT_SOCKET_DATA(`deleteFeed_${newItems.feedId}`, this.deleteFeedAndReloadWall);
      ON_EMIT_SOCKET_DATA(`post_object_${newApi.wall.Wall.id}`, this.wallDataActionFromSocket);
      ON_EMIT_SOCKET_DATA(screenId, (data) => {
        const { screenLimit } = data;
        if (!preview && !isStudio && !queryStudio) {
          if (!this.state.wallErrorCode || this.state.wallErrorCode == 0 || this.state.wallErrorCode == 213) {

            if (screenLimit) this.setState({ wallErrorCode: 213 })
            else this.setState({ wallErrorCode: 0 })
            sessionStorage.removeItem("oldScreenID");
          }
        }
      });
      $(window).on('unload beforeunload', () => {
        SOCKET_CONNECTION.emit("remove-screen", { screenId, timeStamp: Math.floor(Date.now() / 1000) })
      });
    }
  }

  onWindowLoad = () => {
    try {
      var tabID = sessionStorage.tabID &&
        sessionStorage.closedLastTab !== '2' ?
        sessionStorage.tabID :
        sessionStorage.tabID = this.makeid(25);
      sessionStorage.closedLastTab = '2';
      $(window).on('unload beforeunload', () => {
        sessionStorage.closedLastTab = '1';
      });
      return tabID
    }
    catch (ex) {
      var tabID = this.makeid(25);
      return tabID
    }
  }


  makeid = (length) => {
    var result = '';
    var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() *
        charactersLength));
    }
    return result;
  }

  /* Delete Feed From API and Refresh Wall  */
  deleteFeedAndReloadWall = () => window.location.reload();
  /* Action (Refresh/Prepend)  Data from Socket  */
  wallDataActionFromSocket = (data) => {
    const { counter } = this.state;
    if (data.action === "refresh") window.location.reload();
    else if (data.action === "refreshWall") window.location.reload();
    else if (data.action === "prepend") {
      // const socketData = CONVERT_OBJ_TO_ARRAY(data.data);
      // this.setState({ preRenderSocketData: socketData }, () => this.onSocketData());
      const socketData = CONVERT_OBJ_TO_ARRAY(data.data);
      // console.log("prepend", socketData)
      let { newItems, newApi, wallErrorCode } = this.state;
      if ((newItems && newItems.length > 0) || (socketData && socketData.length > 0)) {
        newApi.error_code = 0;
        wallErrorCode = 0;
      }
      if (newApi.wall.Personalization.signageTheme && newApi.wall.Personalization.signageTheme == 44) {
        this.setState({ socketData, newApi, isSocketAction: data.action });
      }
      else if (newApi.wall.Personalization.signageTheme && newApi.wall.Personalization.signageTheme == 40) {
        let newUpdateItem = []
        if (newItems && newItems.length > 0) {
          newItems.map((falingItem, fIndex) => {
            let updateItem = CONVERT_OBJ_TO_ARRAY(falingItem);
            newUpdateItem.push(updateItem)
          })
        }
        //console.log("test",socketData)
        if (socketData && socketData.length > 0) {
          let columnCount = parseInt(lastUpdate);
          socketData.map((sItemData, sIndex) => {
            if (newUpdateItem[columnCount]) newUpdateItem[columnCount].push(sItemData);
            columnCount = parseInt(columnCount) + 1;
            if (columnCount >= newUpdateItem.length) {
              columnCount = 0;
            }
            lastUpdate = columnCount;
          })
        }
        this.setState({ wallErrorCode, socketData: [], newItems: newUpdateItem, newApi, isSocketAction: data.action })

      }
      else this.setState({ wallErrorCode, socketData, newItems: [...socketData, ...this.state.newItems], newApi, isSocketAction: data.action });

    }
    else if (data.action === "remove") {

      const { newItems, newApi, popupItems } = this.state;
      let removeid = data.data.posts;
      let result = removeid.map((i) => Number(i));
      let removePostData = [];
      //removePostData = newItems.filter(item => !result.includes(item.id));

      if (newApi.wall.Personalization.signageTheme && newApi.wall.Personalization.signageTheme == 40) {

        if (newItems && newItems.length > 0) {
          newItems.map((fItem) => {
            let arrayfItem = CONVERT_OBJ_TO_ARRAY(fItem);
            let childNewItemFalling = arrayfItem.filter(item => !result.includes(item.referenceId))
            removePostData.push(childNewItemFalling);
          })
        }
      }
      else { removePostData = newItems.filter(item => !result.includes(item.referenceId)); }

      let removePopupItems = popupItems.filter(item => !result.includes(item.referenceId));
      if (removePostData && removePostData.length == 0) window.location.reload()
      if (newApi.wall.Personalization.signageTheme && newApi.wall.Personalization.signageTheme == 44) {
        this.setState({ newApi, isSocketAction: data.action, isSocketRemoveIDs: result, popupItems: removePopupItems })
      }
      else {
        this.setState({ newItems: removePostData, newApi, isSocketAction: data.action, isSocketRemoveIDs: result, popupItems: removePopupItems })
      }
    }
  }
  onSocketData = () => {
    const interval = setInterval(() => {
      const { counter, preRenderSocketData } = this.state;
      if (counter >= preRenderSocketData.length) {
        this.setState({ counter: null })
        clearInterval(interval);
      } else {
        if (counter != null && preRenderSocketData[counter] === undefined) {
          this.setState({ counter: null })
          //  this.setState({ counter: null, itemCount: 0 })
          clearInterval(interval);
        } else {
          let count1 = counter != null ? counter + 1 : 0;
          if (preRenderSocketData[count1]) this.setState({ itemCount: 1, counter: count1, newItems: [preRenderSocketData[count1], ...this.state.newItems] })
          else this.setState({ counter: count1 })


          // console.log('preRenderSocketData', this.state.newItems);
        }
      }
    }, 3000);
  }
  /* Enable Full Screen */
  enableFullScreen = event => {
    let elem = document.documentElement;
    if (elem.requestFullscreen) {
      elem.requestFullscreen();
    } else if (elem.mozRequestFullScreen) { /* Firefox */
      elem.mozRequestFullScreen();
    } else if (elem.webkitRequestFullscreen) { /* Chrome, Safari and Opera */
      elem.webkitRequestFullscreen();
    } else if (elem.msRequestFullscreen) { /* IE/Edge */
      elem.msRequestFullscreen();
    }
    event.preventDefault();
    if (document.querySelector("#enableFullScreen")) {
      document.querySelector("#enableFullScreen").style.display = 'none'
    }
    //this.setState({ enableFullScreenBtnActive: false })
  }
  onBannerHeight = (e) => {
    const banneriheight = e.currentTarget.clientHeight;
    this.setState({ BannerImageHeight: banneriheight })
  }
  onSocketRemoveData = () => {
    this.setState({ socketData: [] })

  }
  onGetErrorData = () => {
    const { wallId, newApi } = this.state;

    let dataOption = {}
    let editor = window.editor ? true : window.location.href.includes('/editor');
    if (editor) {
      dataOption.editor = 1;
    }
    if (newApi.wall.Personalization.signageTheme) {
      dataOption.themeId = newApi.wall.Personalization.signageTheme;
    }
    const options = (Object.keys(dataOption).length > 0) ? {
      headers: {
        'Access-Control-Allow-Origin': '*',
        ...dataOption
      }
    } : {}
    // axios.get(`${API_WALL_STATUS}/${wallId}`, options).then((response) => {
    //   const { scenes, response_code, is_studio_enabled, error_code } = response.data;
    //   this.setState({ wallErrorCode: error_code })
    // }).catch((error) => {

    // })
  }
  render() {
    const { BannerImageHeight, err, isLoading, newApi, finalPath, newItems, finalBannerPath, enableFullScreenBtnActive, postWidth, width, socialWallId, height, itemCount, socketData, wallErrorCode, popupItems, isSocketAction, isSocketRemoveIDs } = this.state;
    const { isLoad, networkHandle, olderDays } = this.props;
    const wallStatus = (wallErrorCode != null) ? wallErrorCode == 0 ? true : (IS_EDITOR) ? true : (newApi?.wall?.Personalization.demoWall === 1) ? true : [213].includes(wallErrorCode) : [213].includes(wallErrorCode)

    //console.log('AABX-AAA',newApi.wall.UgcSettings)
   

    return (
      err === 404 ? <ErrorBoundary> <ErrorComponent newApi={newApi} err={err} wallErrorCode={wallErrorCode} olderDays={olderDays} networkHandle={networkHandle} /></ErrorBoundary> : isLoading ? '' :
        (
          <Fragment>
            {newApi.wall ?

            

              <PageHeadSection PageStyle={newApi.wall.Personalization.cssStatus === 1 ? newApi.wall.Personalization.css : null} PageTitle={`${newApi.wall.Wall.name} | Live Socialwall - Taggbox`} PageMetaData={null} newApi={newApi} /> : null}

            {newApi.wall.Personalization.demoWall === 1 && IS_EDITOR ? <ErrorComponent {...this.state} wallErrorCode={wallErrorCode} networkHandle={networkHandle} olderDays={olderDays} /> :
              <ErrorComponent {...this.state} wallErrorCode={wallErrorCode} networkHandle={networkHandle} olderDays={olderDays} />
            }
            {

              ((newApi.error_code === 213 || wallErrorCode === 213) && !IS_EDITOR) ? <div className="wrappContentDivW" id="screenLimitExceed">
                <div className="contentDiv">
                  <h1 className="commanH1Modal">Exceeded Screen Limit?</h1>
                  <p className="commanPeraModal">Oops! Looks like you need to upgrade your plan in order to add more screens</p>
                </div>
              </div> : null
            }

            {(!IS_EDITOR) && !window.location.href.includes('studio=1') && !iniFrame() && (window.innerWidth < 1920 && !isMobile) && isEnableFullScreenButton() && <a id="enableFullScreen" className="commanBtn btn-sm triggerFullScreen" onClick={this.enableFullScreen}>
              <i className="fa fa-arrows-alt" aria-hidden="true"></i>&nbsp; Enable Full Screen
            </a>}
            <div className={`scrlBarComan themesWrapper themeStart${newApi.wall.Personalization.signageTheme} themeType${newApi.wall.Personalization.themeType}`}
              style={{
                backgroundImage: newApi.wall.BackgroundImage && Object.keys(newApi.wall.BackgroundImage) && newApi.wall.BackgroundImage.path && newApi.wall.BackgroundImage.path !== '' && newApi.wall.ThemeRule.transparent === 0 ? 'url(' + finalPath + ')'
                  : '',
                backgroundColor: newApi.wall.ThemeRule.transparent === 0 && newApi.wall.BackgroundImage && newApi.wall.BackgroundImage.path === null ? newApi.wall.ThemeRule.backgroundColor : '',
                display: newApi.wall.Personalization.demoWall !== 1 ? scrollBarCommonThemeBar(newApi.error_code, err) : ''
              }}>
              {wallStatus ? <>
                {newApi.wall.CustomTicker && Object.keys(newApi.wall.CustomTicker).length > 0 && newApi.wall.CustomTicker.status === 1 && newApi.wall.CustomTicker.ticker_position === "1" && <TopTicker CustomTicker={newApi.wall.CustomTicker} newApi={newApi} UgcSettings={newApi.wall.UgcSettings} />}
                {newApi.wall.BannerImage && Object.keys(newApi.wall.BannerImage).length > 0 && newApi.wall.BannerImage.status === 1 && <div id="wBannerImage" className="bannerTop" onLoad={this.onBannerHeight}><img src={finalBannerPath} alt={finalBannerPath} className="img-responsive" /></div>}

                {newApi.wall.Personalization.signageTheme != 51 ? (newApi.wall.CustomBanner && Object.keys(newApi.wall.CustomBanner).length > 0 && newApi.wall.CustomBanner.status === 1 && newApi.wall.CustomBanner.banner_position === "1" && newApi.wall.Personalization.signageTheme !== 51) &&
                  <BannerView
                    wallData={newApi.wall}
                    CustomBanner={newApi.wall.CustomBanner}
                    CustomBannerImage={newApi.wall.CustomBannerImage}
                    LogoImage={newApi.wall.LogoImage}
                  /> : ''}
                <ErrorBoundary>
                  <ThemeRoutes newApi={newApi} width={width} postWidth={postWidth} newItems={newItems} height={height} BannerImageHeight={BannerImageHeight} itemCount={itemCount} see={this.state.see} dismissPopUp={this.dismissPopUp} poweredByTag={this.poweredByTag} socketData={socketData} onSocketRemoveData={this.onSocketRemoveData} isSocketAction={isSocketAction} isSocketRemoveIDs={isSocketRemoveIDs} />
                </ErrorBoundary>

                {newApi.wall.Personalization.signageTheme != 51 ? (newApi.wall.CustomBanner && Object.keys(newApi.wall.CustomBanner).length > 0 && newApi.wall.CustomBanner.status === 1 && newApi.wall.CustomBanner.banner_position === "2" && newApi.wall.Personalization.signageTheme !== 51) &&
                  <BannerView
                    wallData={newApi.wall}
                    CustomBanner={newApi.wall.CustomBanner}
                    CustomBannerImage={newApi.wall.CustomBannerImage}
                    LogoImage={newApi.wall.LogoImage}
                  /> : ''}

                {!popUpEnable(newApi.wall.Personalization.signageTheme) && newItems && newItems.length > 0 && <ModelPopUp newItems={popupItems} newApi={this.state.newApi} />}
                {/* {
                (newApi.wall.Personalization.postFeatured === 1 && newItems && newItems.length > 0) ? <ModelPopUp newItems={newItems} newApi={this.state.newApi} /> : null
              } */}


                {newApi.announcements && Object.keys(newApi.announcements).length > 0 && <AnnouncementsOverlayView newApi={this.state.newApi} />}
              </> : null}
              {newApi.wall.Personalization.signageTheme === 51 ? '' : <TaggboxBranding see={this.state.see} newApi={this.state.newApi} dismissPopUp={this.dismissPopUp} poweredByTag={this.poweredByTag} />}


              {newApi.wall.Personalization.signageTheme === 51 ? '' : <UpgradeScreenPopup {...this.state} />}
              {newApi.wall.CustomTicker && Object.keys(newApi.wall.CustomTicker).length > 0 && newApi.wall.CustomTicker.status === 1 && newApi.wall.CustomTicker.ticker_position === "2" && <BottomTicker newApi={newApi} UgcSettings={newApi.wall.UgcSettings} />}
              <PlanTicker newApi={newApi} UgcSettings={newApi.wall.UgcSettings} />
           
              {newApi.wall && newApi.wall.UgcSettings && Object.keys(newApi.wall.UgcSettings).length > 0 && newApi.wall.UgcSettings.onsite_status && newApi.wall.UgcSettings.onsite_button_status ? <OnSiteUpload UgcSettings={newApi.wall.UgcSettings} /> : null}
            </div>
          </Fragment >
        ))
  };
}
export default memo(Themes);