import React, { memo } from 'react'
import { isMobile } from "react-device-detect";
import Ratings from '../themes/ModernWaterFall/ratings'
import ReactHtmlParser from 'react-html-parser';
import SlackEmoji from './../../components/slackEmoji'
import { Component } from 'react';
import { CloudUrl } from '../../constants';
export const reactStringReplace = require('react-string-replace');


class FontFamModal extends Component {
  state = {
    postHeight: null,
    fontsize: 1.2,
    lineClip: 0
  }

  fontSizeCal() {
    let fontsizeCal = 30;
    const contStr = this.props.person.content;
    const charCount = contStr.length;
    let xData1 = 2.4;
    let xData2 = .0025;

    let cData1 = 1.2;
    let cData2 = 2.41;
    if (this.props.windowWidth < 991) {
      xData1 = 1.7;
      xData2 = .0010;
      cData1 = 1.1;
      cData2 = 2.0;
    }
    fontsizeCal = parseFloat(xData1 - xData2 * charCount).toFixed(2);
    const clampNumber = (num, a, b) => Math.max(Math.min(num, Math.max(a, b)), Math.min(a, b));
    fontsizeCal = clampNumber(fontsizeCal, cData1, cData2);
    this.setState({ fontsize: fontsizeCal })

  }

  componentDidMount() {
    this.fontSizeCal()
    let elHeight = document.getElementById('post_content_00')
    setTimeout(() => {
      if (elHeight) {
        let fontSize = this.state.fontsize
        let lineHeight = fontSize * 22
        elHeight = elHeight.clientHeight
        let totalLines = elHeight / lineHeight
        totalLines = totalLines.toFixed(0);
        this.setState({ lineClip: totalLines })
      }
    }, 100);
  }


  render() {
    const { person, newApi, themeRule, windowWidth } = this.props;

    return (
      <div id="post_content_00" className="tb_post_wrap_in">
        <div className="tb_post_content">
          {(person.rating && person.rating != null) ? <div className="tb_rating_wrap">
            <div className="tb_rating_001">
              {<img src={`${CloudUrl}/rating/${person.networkId}/${person.rating}.png`} alt="description" />}
            </div>
          </div> : null}

          <div className={`post_content_00 ${newApi.wall.Personalization.textDecorate === 1 && person.theme48.class1 === "onlyTextCard" ? 'decorateTxt' : ''} sGFfonte-${newApi.wall.ThemeRule.font_varient}`}
            style={{
              fontFamily: newApi.wall.ThemeRule.css_font,
              color: (themeRule.cardColor === "#000000" && themeRule.fontColor === "#000000") ? "#ffffff"
                : themeRule.cardColor === "#ffffff" && themeRule.fontColor == "#ffffff" ? "#000000"
                  : themeRule.fontColor,
              display: themeRule.hideContent === 0 ? " " : "none",
              fontSize: `${this.state.fontsize}em`,
              WebkitLineClamp: this.state.lineClip
            }}>

            <strong style={{ fontSize: themeRule.fontsize }}>{person.contentTitle}</strong>
            {/* <span className="leftquoteModal" style={{ display: person.theme48.class1 === "onlyTextCard" && newApi.wall.Personalization.textDecorate === 1 ? "inline" : "none", }}>
                    <i className="fa fa-quote-left" aria-hidden="true"></i> </span> */}

            <>{newApi.wall.Personalization.hashtag_highlight !== 0 ?
              newApi.wall.Personalization.hashtag_all === 1 ? reactStringReplace(person.content, /#(\w+)/g, (match, i) => (
                <span key={match + i} style={{ color: newApi.wall.Personalization.hashtag_color, fontWeight: "bold" }}>#{match}</span>
              ))
                : newApi.wall.Personalization.hashtag_feed === 1 ? reactStringReplace(person.content, person.hash.hashString, (match, i) => (
                  <span key={match + i} style={{ color: newApi.wall.Personalization.hashtag_color, fontWeight: "bold" }}>{match}</span>

                ))
                  : person.network.id === 20 ? <SlackEmoji text={window.slackdown.parse(person.content, person.slackMember)} /> : ReactHtmlParser(person.content)
              : person.content
            }</>

          </div>
        </div>
      </div>
    )
  }

}

export default memo(FontFamModal);