import React, { Component, memo } from "react";
import styled from 'styled-components'
import Zones from './zone'

class SceneContent extends Component {
    state = { scenes: [], activeID: null, rendomChange: false, noRepeatScene: [] }
    componentDidMount() {
        const { scenes } = this.props;
        this.setState({ scenes }, () => this.onTimeInterval())
    }
    onTimeInterval = () => {
        const { scenes, activeID, rendomChange, noRepeatScene } = this.state;


        if (!rendomChange) {
            if (activeID != undefined && activeID != null) {
                let newActiveID = parseInt(activeID) + 1;
                // if (newActiveID == (scenes.length - 1)) {
                //     this.setState({ rendomChange: true })
                // }

                if (newActiveID > (scenes.length - 1)) {
                    if (scenes && scenes[0]) {
                        const { totalTime } = scenes[0];
                        //this.setState({ activeID: 0 }, () => setTimeout(() => this.onTimeInterval(), (totalTime * 1000)))
                        if (parseInt(totalTime) > 0) this.setState({ activeID: 0 }, () => this.onUpdateStudioTimeOut(totalTime))
                        else {
                            if (parseInt(totalTime) == 0) this.setState({ activeID: 0 }, () => this.onTimeInterval())
                        }
                    }
                }
                else {
                    const { totalTime } = scenes[newActiveID];
                    //this.setState({ activeID: newActiveID }, () => setTimeout(() => this.onTimeInterval(), (totalTime * 1000)))

                    if (parseInt(totalTime) > 0) this.setState({ activeID: newActiveID }, () => this.onUpdateStudioTimeOut(totalTime))
                    else {
                        if (parseInt(totalTime) == 0) this.setState({ activeID: newActiveID }, () => this.onTimeInterval())
                    }
                }
                // console.log("hhello")

            }
            else {
                if (scenes && scenes[0]) {
                    const { totalTime } = scenes[0];
                    //this.setState({ activeID: 0 }, () => setTimeout(() => this.onTimeInterval(), (totalTime * 1000)))
                    if (parseInt(totalTime) > 0) this.setState({ activeID: 0 }, () => this.onUpdateStudioTimeOut(totalTime))
                    else {
                        if (parseInt(totalTime) == 0 && scenes && scenes.length > 0) this.setState({ activeID: 1 }, () => this.onTimeInterval())
                    }
                }
            }
        }
        else {
            if (scenes && scenes[0]) {
                var randNub = Math.floor(Math.random() * scenes.length);
                var addsceneNoRepeat = noRepeatScene;
                if (noRepeatScene.length == scenes.length) {
                    addsceneNoRepeat = [];
                } else {
                    if (addsceneNoRepeat.includes(randNub)) {
                        for (let i = 0; i < scenes.length; i++) {
                            randNub = Math.floor(Math.random() * scenes.length);
                            if (!addsceneNoRepeat.includes(randNub)) {
                                addsceneNoRepeat.push(randNub)
                                break;
                            }
                        }

                    }
                    else {

                        addsceneNoRepeat.push(randNub)
                    }
                }
                const { totalTime } = scenes[randNub];
                //this.setState({ activeID: randNub, noRepeatScene: addsceneNoRepeat }, () => setTimeout(() => this.onTimeInterval(), (totalTime * 1000)))
                if (parseInt(totalTime) > 0) this.setState({ activeID: randNub, noRepeatScene: addsceneNoRepeat }, () => this.onUpdateStudioTimeOut(totalTime))
                else if (parseInt(totalTime) == 0) this.setState({ activeID: randNub, noRepeatScene: addsceneNoRepeat }, () => this.onTimeInterval())
            }
        }
    }

    onUpdateStudioTimeOut = (totalTime) => {
        this.interval = setTimeout(() => this.onTimeInterval(), (totalTime * 1000))
    }
    componentWillUnmount() {
        clearInterval(this.interval);
    }

    render() {
        const { scenes, activeID } = this.state;
        const { feedList } = this.props;
        return <>
            {
                scenes && scenes.map((item, sceneIndex) => {
                    let StyledWrapper = styled.div`width: 100%;height: 100%;${item.css}`
                    return activeID == sceneIndex ? <div class={`contentScreen slide${sceneIndex} ${activeID == sceneIndex ? `active` : ``}`} data-time={item.totalTime} key={sceneIndex}>
                        {
                            item && <div className="preview-layoutSide" style={{ height: `100%` }}>
                                <StyledWrapper>
                                    {
                                        Object.keys(item.partitions).map((zItem, zIndex) => <Zones key={`z_${zIndex}`} zIndex={zIndex} data={item.sceneContent && item.sceneContent && item.sceneContent[zIndex] && item.sceneContent[zIndex].data && item.sceneContent[zIndex].data} feedList={feedList} />)
                                    }
                                </StyledWrapper>
                            </div>

                        }
                    </div> : null
                }
                )
            }
        </>
    }

}
export default memo(SceneContent)