
import React, { PureComponent } from 'react';
import Skeleton from 'react-loading-skeleton';
import NetworkCardComponent from '../../networkCardComponent';
import AuthorDetails from '../../sepcomponents/AuthorDetails';
import FontFamilyForPost from '../../sepcomponents/FontFamilyForPost';
import ItemRender from '../../sepcomponents/ItemRender';


class SocialWallItem extends PureComponent {
    state = {
        rootWidth: document.getElementById('root').clientWidth,
        wHeight: window.innerHeight,
        height: 0,
        postWidth: 0
    }
   
render() {
        const { item, newApi, itemKey, loading, networkd } = this.props;
return(
<>

<div id={`"postId${item.id}`}
    className={`feedId58195 postItem flatCard ${networkd.name}}Taggbox ${item.theme48.class1 === 'onlyTextCard' ? 'onlyTextCard' : ''} completeFadeIn`}
    key={item.id}
    data-post-id={item.id}
    data-highlight={item.highlight}
    data-pin={item.pin}
    data-pi={item.pin}
    style={{ padding: newApi.wall.Personalization.padding / 2, margin: 0, minWidth: 0, left: 0, top: 0 }}
    data-created={item.createdAt} >
    <div className={`post ${ newApi.wall.ThemeRule.cardColor === '#111111' ? `post${itemKey % 4 + 1}` : null }`}  style={{ backgroundColor: newApi.wall.ThemeRule.cardColor }}>
        <div className="postContent">
            {loading ? <Skeleton height={300} /> : <>
                <ItemRender network={networkd} newApi={newApi} item={item} itemKey={itemKey} key={`"render-${item.id}`}/>
                <div className="postContentCard" >
                    <FontFamilyForPost item={item} newApi={newApi} personalization={newApi.wall.Personalization} themeRule={newApi.wall.ThemeRule} network={networkd} signageTheme={newApi.wall.Personalization.signageTheme} ownerID={newApi.wall.Wall.owner} />
                    <h4 margin-top="0px" >
                        <NetworkCardComponent network={networkd} themeRule={newApi.wall.ThemeRule} />
                        <AuthorDetails backgroundColor={networkd.color} item={item} personalization={newApi.wall.Personalization} font={newApi.wall.ThemeRule} network={networkd} />
                    </h4>
                </div>
            </>}
        </div>
    </div>
</div>

</>)
}
}
export default SocialWallItem;