import React, { PureComponent } from 'react';
import { Helmet } from "react-helmet";

export default class PageHeadSection extends PureComponent {

    render() {
        const { PageTitle, PageMetaData, PageStyle, newApi } = this.props;
        return <Helmet>
            {PageMetaData != null ? <meta {...PageMetaData} /> : null}
            {PageTitle != null ? <title>{PageTitle}</title> : null}
            {PageStyle != null ? <style type="text/css">{PageStyle}</style> : null}
            {
                newApi.wall?.CustomTicker?.status === 1 && Object.keys(newApi.wall.CustomTicker).length > 0 ? <link href={`https://fonts.googleapis.com/css?family=${newApi.wall.CustomTicker?.title_link_font}:${newApi.wall.CustomTicker?.title_font_varient}`} rel="stylesheet" /> : null
            }
            {
                newApi.wall?.CustomTicker?.status === 1 && Object.keys(newApi.wall.CustomTicker).length > 0 ? <link href={`https://fonts.googleapis.com/css?family=${newApi.wall.CustomTicker?.subtitle_link_font}:${newApi.wall.CustomTicker?.subtitle_font_varient}`} rel="stylesheet" /> : null
            }
            {
                newApi.wall.ThemeRule && Object.keys(newApi.wall.ThemeRule).length > 0 && newApi.wall.ThemeRule.link_font && newApi.wall.ThemeRule.link_font != "" ? <link href={`https://fonts.googleapis.com/css?family=${newApi.wall.ThemeRule.link_font}:${newApi.wall.ThemeRule.font_varient}`} rel="stylesheet" /> : null
            }
            {
                newApi.wall.CustomBanner && Object.keys(newApi.wall.CustomBanner).length > 0 && newApi.wall.CustomBanner.status == 1 && newApi.wall.CustomBanner.subtitle_link_font && newApi.wall.CustomBanner.subtitle_link_font != "" ? <link href={`https://fonts.googleapis.com/css?family=${newApi.wall.CustomBanner.subtitle_link_font}:${newApi.wall.CustomBanner.subtitle_font_varient}`} rel="stylesheet" /> : null
            }
            {
                newApi.wall.CustomBanner && Object.keys(newApi.wall.CustomBanner).length > 0 && newApi.wall.CustomBanner.status == 1 && newApi.wall.CustomBanner.title_link_font && newApi.wall.CustomBanner.title_link_font != "" ? <link href={`https://fonts.googleapis.com/css?family=${newApi.wall.CustomBanner.title_link_font}:${newApi.wall.CustomBanner.title_font_varient}`} rel="stylesheet" /> : null
            }

            {
                newApi.wall.CustomTicker && Object.keys(newApi.wall.CustomTicker).length > 0 && newApi.wall.CustomTicker.status == 1 ? <link href={`https://fonts.googleapis.com/css?family=${newApi.wall.CustomTicker.title_css_font}:${newApi.wall.CustomTicker.title_font_varient}`} rel="stylesheet" /> : null
            }

            {
                newApi.wall.UgcSettings && Object.keys(newApi.wall.UgcSettings).length > 0 && newApi.wall.UgcSettings.onsite_txt_font ? <link href={`https://fonts.googleapis.com/css?family=${newApi.wall.UgcSettings.onsite_txt_font}:${newApi.wall.UgcSettings.onsite_txt_font_variation}`} rel="stylesheet" /> : null
            }


            {
                newApi.wall.CustomTicker && Object.keys(newApi.wall.CustomTicker).length > 0 && newApi.wall.CustomTicker.status == 1 ? <link href={`https://fonts.googleapis.com/css?family=${newApi.wall.CustomTicker.subtitle_css_font}:${newApi.wall.CustomTicker.subtitle_font_varient}`} rel="stylesheet" /> : null
            }
            <script type="text/javascript" src="https://widget.taggbox.com/common-assets/js/jquery.min.js"></script>
            <script type="text/javascript" src="https://widget.taggbox.com/common-assets/js/slackdown.js"></script>
        </Helmet>
    }
}