import React, { Component } from "react";
import renderHTML from 'react-render-html';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
import $ from 'jquery';
import { updateURLForIframe } from '../utils'
import { APP_URL, IS_TEST } from '../actions/api'

import { default as DisplayQRCode } from 'qrcode.react';
import { default as DisplayCounter } from "./counter";
import NoticeBoardPreview from './noticeBoardApp'
import MotivationalQuotePreview from './motivationalQuotes'
import Clocks from './clocks'
import YoutubePlayers from './youtube'
import CalendarFrame from './calendar/calendarIframe'
import VideoPreview from './video'
import WeatherAPP from './WeatherPreview/weatherIframe'
import LeaderIframe from "./leaderBorad/leaderIframe";
 
const SceneType1 = (props) => {
 
    return <iframe id={props.id} className="wUrl" data-src={IS_TEST ? `https://display.taggbox.com/${props.content}?studio=1` : `https://display.taggbox.com/${props.content}?studio=1`} src={IS_TEST ? `https://display.taggbox.com/${props.content}?studio=1` : `https://display.taggbox.com/${props.content}?studio=1`} width="100%" height="100%" style={{ border: '0px' }}>
    </iframe>
}
 
const SceneType2 = (props) => {

    return <iframe id={props.id} className="url" src={props.content} width="100%" height="100%"></iframe>
}

const getVideoId = (url) => {
    var regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
    var match = url.match(regExp);

    if (match && match[2].length == 11) {
        return match[2];
    } else {
        return 'error';
    }
}

const SceneType3 = (props) => {

    return <YoutubePlayers videoId={getVideoId(updateURLForIframe(props.content))} />
    //<iframe id={props.id} className="video" src={updateURLForIframe(props.content)} frameborder="0" style={{ width: '100%', height: '100%' }}></iframe>
}
const SceneType4 = (props) => {

    return <iframe id={props.id} className="video" src={updateURLForIframe(props.content)} frameborder="0" allow="" style={{ width: '100%', height: '100%' }}></iframe>
    // return <div id={`vimeo${props.id}`} className="video1" data-video={props.video_id} style={{
    //     width: '100%', height: '100%'
    // }}></div>
}
const SceneType5 = (props) => {

    // return <>
    //     <div style={{ textAlign: 'center' }}>
    //         <div className="blur-bg " style={{ backgroundImage: `url(${props.content})` }}></div>
    //         <img src={props.content} className="makebg" /></div>
    // </>
    return (props && props.name && props.name.includes("video-")) ? <><VideoPreview {...props} /></> : <img src={props.content} className="makebg" style={{
        position: 'absolute'
    }} />

    // return <div id={props.id} className="screenImg" style={{ backgroundImage: `url(${props.content})` }}>
    // </div>
}

const SceneType9 = (props) => {

    return (props && props.name && props.name.includes("video-")) ? <><VideoPreview {...props} /></> : <img src={props.content} className="makebg" style={{
        position: 'absolute'
    }} />
}

const SceneType6 = (props) => {

    let embedData = props.content;
    if (props.content.includes("taggbox-socialwall")) {
        const previewData = ReactHtmlParser(embedData);
        if (previewData && previewData[0]) {

            if (previewData[0].props && previewData[0].props.children && Object.keys(previewData[0].props.children).length > 0) {
                const childData = Object.keys(previewData[0].props.children).map((item) => previewData[0].props.children[item]);
                if (childData && childData[0]) {
                    embedData = `<iframe className="url" src=${childData[0].props["view-url"]} width="100%" height="100%" scrolling="yes">`
                }

            }
        }

    }
    return <div id={props.id} style={props.content.includes("taggbox-socialwall") ? { width: '100%', height: '100%', overflow: 'auto' } : { width: '100%', height: '100%' }} className="widget_code_custom_one">{
        renderHTML(embedData)
    }</div>
}

const SceneType8 = (props) => {

    switch (props.app_id) {
        case 4: {
            return <iframe id={props.id} className="url" src={`https://app.taggbox.com/apps_preview?content_id=${props.id}`} width="100%" height="100%"></iframe>
        }
        case 5: {
            return <iframe id={props.id} className="url" src={`https://app.taggbox.com/apps_preview?content_id=${props.id}`} width="100%" height="100%"></iframe>
        }
        case 9: {
            return <NoticeBoardPreview {...props} />
        }
        case 8: {
            return <MotivationalQuotePreview {...props} />
        }
        case 3: {
            return <Clocks {...props} />
        }
        case 2: {
            return <CalendarFrame {...props} />
        }
        case 1: {
            return <WeatherAPP {...props} />
        }
        case 10: {
            return <LeaderIframe {...props} />
        }



        default: {
            return ""
        }
    }
}

const CustomStyle = (props) => {

    if (props && props.type && props.type == 5) return { width: '100%', height: '100%', backgroundImage: `url(${props.content})` }
    else return { width: '100%', height: '100%' }
}

class Zones extends Component {
    state = { data: [], activeID: null }
    componentDidMount() {
        const { zIndex, data } = this.props;
        this.setState({ data }, () => this.onTimeInterval())
    }
    onTimeInterval = () => {
        const { data, activeID } = this.state;

        if (activeID != undefined && activeID != null) {
            let newActiveID = parseInt(activeID) + 1;

            if (newActiveID > (data.length - 1)) {

                if (data && data[0]) {
                    const { sceneDuration } = data[0];
                    //this.setState({ activeID: 0 }, () => setTimeout(() => this.onTimeInterval(), (sceneDuration * 1000)))
                    if (parseInt(sceneDuration) > 0) this.setState({ activeID: 0 }, () => this.onUpdateStudioTimeOut(sceneDuration))
                    else {
                        if (parseInt(sceneDuration) == 0) this.setState({ activeID: 0 }, () => this.onTimeInterval())
                    }
                }
            }
            else {

                const { sceneDuration } = data[newActiveID];

                //this.setState({ activeID: newActiveID }, () => setTimeout(() => this.onTimeInterval(), (sceneDuration * 1000)))
                if (parseInt(sceneDuration) > 0) this.setState({ activeID: newActiveID }, () => this.onUpdateStudioTimeOut(sceneDuration))
                else {
                    if (parseInt(sceneDuration) == 0) this.setState({ activeID: newActiveID }, () => this.onTimeInterval())
                }
            }

        }
        else {

            if (data && data[0]) {
                const { sceneDuration } = data[0];
                //this.setState({ activeID: 0 }, () => setTimeout(() => this.onTimeInterval(), (sceneDuration * 1000)))
                if (parseInt(sceneDuration) > 0) this.setState({ activeID: 0 }, () => this.onUpdateStudioTimeOut(sceneDuration))
                else {
                    if (parseInt(sceneDuration) == 0 && data && data.length > 1) this.setState({ activeID: 1 }, () => this.onTimeInterval())
                }
            }
        }
    }
    onUpdateStudioTimeOut = (sceneDuration) => {
        this.interval = setTimeout(() => this.onTimeInterval(), (sceneDuration * 1000))
    }
    componentWillUnmount() {
        clearInterval(this.interval);
    }
    render() {
        const { zIndex, feedList } = this.props;
        const { data, activeID } = this.state;


        return <div id={`zone${zIndex + 1}`} className="zone">
            {
                data && data.length > 0 && data.map((contentI, cItem) => <div key={`innerScene${contentI.id}`} id={`innerScene${contentI.id}`} className={`innerScene fullWidth ${(cItem == activeID) ? `active` : ``}  ${(contentI && contentI.type && (contentI.type == 5 && !contentI.name.includes("video-"))) ? `blur-bg` : ``}`} style={CustomStyle(contentI)} data-time={contentI.sceneDuration}>
                    {

                        (contentI && contentI.type && contentI.type == 1 && cItem == activeID) ? <SceneType1 {...contentI} cIndex={cItem} />
                            : (contentI && contentI.type && contentI.type == 2 && cItem == activeID) ? <SceneType2 {...contentI} cIndex={cItem} />
                                : (contentI && contentI.type && contentI.type == 3 && cItem == activeID) ? <SceneType3 {...contentI} cIndex={cItem} />
                                    : (contentI && contentI.type && contentI.type == 4 && cItem == activeID) ? <SceneType4 {...contentI} cIndex={cItem} />
                                        : (contentI && contentI.type && contentI.type == 5 && cItem == activeID) ? <SceneType5 {...contentI} cIndex={cItem} />
                                            : (contentI && contentI.type && contentI.type == 6 && cItem == activeID) ? <SceneType6 {...contentI} cIndex={cItem} />
                                                : (contentI && contentI.type && contentI.type == 8 && cItem == activeID) ? <SceneType8 {...contentI} cIndex={cItem} feedList={feedList} />
                                                    : (contentI && contentI.type && contentI.type == 9 && cItem == activeID) ? <SceneType9 {...contentI} cIndex={cItem} />
                                                        : null

                    }</div>)

            }
        </div>
    }

}
export default Zones