import React from 'react'
import DayCalendar from './day-calendar/dayCalendar'
import WeekCalendar from './week-calendar/weekCalendar'
import MonthCalendar from './month-calendar/monthCalendar'
import moment from 'moment';

export default class CalendarPreview extends React.Component {
  componentDidMount() {
    setTimeout(() => {
      window.dispatchEvent(new Event('resize'))
    }, 100);
  }


  render() {
    {
      const { app_data, calendarType, numEvents, events } = this.props;

      const today = new Date().getDate();
      let count = 0;
      // const upcomingEvents = app_data.events.filter(function (item) { if (this.count <= app_data.numEvents && item.date >= today) { this.count++; return true; } return false; }, { count: 0 })

      let upcomingEvents = (app_data.events && app_data.events.length > 0) ? app_data.events.filter((item) => {

        if (count <= parseInt(app_data.numEvents) && moment(item.end).format("DD/MM/YYYY HH:mm:ss") >= moment().format("DD/MM/YYYY HH:mm:ss")) {
          //console.log("item.date", item, moment(item.end).format("DD/MM/YYYY HH:mm:ss"), moment().format("DD/MM/YYYY HH:mm:ss"))
          count++; return true;

        }
        else return false;
      }) : []

      return app_data && Object.keys(app_data).length > 0 ? <> {
        app_data.calendarType == 0 ? <MonthCalendar app_data={app_data} allEvents={app_data.events} upcomingEvents={upcomingEvents} />
          : app_data.calendarType == 1 ? <WeekCalendar app_data={app_data} allEvents={app_data.events} upcomingEvents={upcomingEvents} />
            : <DayCalendar app_data={app_data} allEvents={app_data.events} upcomingEvents={upcomingEvents} />
      }
      </>
        : null
    }
  }
}

