import React from 'react'
import Iframe from 'react-iframe';
import ReactPlayer from 'react-player'
import Vimeo from '@u-wave/react-vimeo';
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP } from '../../utils'
import { NO_CAMERA_IMAGE } from '../../constants'


export default class VideoMedia extends React.Component {

    render() {
        // return <iframe src={`${this.props.mediaUrl}&autoplay=1&loop=1&mute=1`} width="600" height="400" frameborder="0" allow="autoplay; fullscreen" allowfullscreen></iframe>
        const { item, newApi } = this.props;
        return <>
            {
                (item.type === 2 || item.type === 4) ? <img src={item.postFileNew} className="tb_con_media" data-link={item.link} data-load="0" data-network={item.networkId} data-postid={item.id}
                    data-wall-id={newApi.Wall.id} data-item-id={item.id} data-filter-id={item.filterId}
                    data-stories={item.stories}
                    data-tb={newApi.UserDetail.db_table}
                    onError={(e) => {
                        e.target.src = NO_CAMERA_IMAGE;
                        IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)

                    }}
                /> : null
            }
            {((item.type === 3 || item.type === 5)) && <>
                <div>

                    {
                        (item.type === 3 || item.type === 5) && <>
                            {
                                item.link && (item.link.indexOf("youtube") >= 0 || item.link.indexOf("youtu.be") >= 0) ?

                                    <ReactPlayer url={`${item.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} playing
                                        muted
                                        config={{
                                            file: {
                                                attributes: {
                                                    autoPlay: true,
                                                    muted: true
                                                }
                                            }
                                        }} />

                                    : item.link && (item.link.indexOf("vimeo") >= 0) ?
                                        <ReactPlayer url={`${item.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} playing
                                            muted
                                            config={{
                                                file: {
                                                    attributes: {
                                                        autoPlay: true,
                                                        muted: false
                                                    }
                                                }
                                            }} />
                                        : item.link && (item.networkId && item.networkId == 30) && item.link.indexOf("vk") >= 0 ?
                                            <Iframe
                                                className="tb-detail-image-iframe"
                                                frameBorder="0"
                                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                src={`${item.mediaUrl}&autoplay=1&mute=1`}
                                                allowFullScreen="true"
                                                style={{ width: "100%!important" }}
                                                data-filter-id={item.filterId}
                                                data-stories={item.stories}
                                                data-type="video"
                                                data-network={item.networkId}
                                                data-link={item.link}
                                                data-item-id={item.id}
                                                data-load={0}
                                            ></Iframe>

                                            :

                                            item.link && (item.link.indexOf("soundcloud") >= 0) ?
                                                <Iframe
                                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                    src={`${item.mediaUrl}`}
                                                    allowFullScreen="true"
                                                    style={{ width: "100%!important" }}
                                                ></Iframe>
                                                : item.link && (item.mediaUrl.indexOf("onsiteupload/video") >= 0 || item.link.indexOf("linkedin") >= 0 || item.link.indexOf("twitter") >= 0 || item.link.indexOf("tumblr") >= 0) ? <video

                                                    src={`${item.mediaUrl}`}
                                                    autoPlay={true}
                                                    muted={true}
                                                    onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e) }}
                                                    data-type="video"
                                                    data-network={item.networkId}
                                                    data-postid={item.id}
                                                    data-link={item.link}
                                                    data-stories={item.stories}
                                                    data-load={0}
                                                    data-tb={newApi.UserDetail.db_table}
                                                    data-filter-id={item.filterId}
                                                    data-wall-id={newApi.Wall.id} data-item-id={item.id}
                                                ></video> : <video

                                                    src={`${item.mediaUrl}`}
                                                    autoPlay={true}
                                                    muted={true}
                                                    onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e) }}
                                                    data-type="video"
                                                    data-network={item.networkId}
                                                    data-stories={item.stories}
                                                    data-postid={item.id}
                                                    data-link={item.link}
                                                    data-tb={newApi.UserDetail.db_table}
                                                    data-filter-id={item.filterId}
                                                    data-load={0}
                                                    data-wall-id={newApi.Wall.id} data-item-id={item.id}
                                                ></video>
                            }
                        </>
                    }

                </div>

            </>}

        </>
    }

}
