import React, { Component } from "react";
import PopupOnOff from './popUp/PopupOnOff';
class ModelPopUp extends Component {
    state = {
        isPopupActive: false,
        isDataCount: -1,
        windowWidth: window.innerWidth,
        windowHeight: window.innerHeight

    }
    componentDidMount() {
        window.addEventListener("resize", () => {
            this.setState({ windowWidth: window.innerWidth, windowHeight: window.innerHeight })
        });
    }
    render() {
        const { newItems, newApi } = this.props;
        const { windowWidth, windowHeight } = this.state;
        return (newItems && newItems.length > 0) ? <PopupOnOff newItems={newItems} newApi={newApi} windowWidth={windowWidth} windowHeight={windowHeight} /> : null
    }
}
export default ModelPopUp;