import React from 'react';
import { Animated } from "react-animated-css";
import SocialWallItem from './SocialWallItem';
import ErrorBoundary from '../../../errorBoundary'
const AnimatedSocialWallItem = (props) => <>
<Animated animationIn="bounceIn" animationInDuration={1000} isVisible={true} key={props.item.id}><ErrorBoundary><SocialWallItem {...props} /></ErrorBoundary></Animated>
</>

export default AnimatedSocialWallItem;