import React, { Component, Fragment } from 'react';
import Uppy from '@uppy/core';
import { FileInput, DragDrop, ProgressBar, StatusBar } from '@uppy/react';
import '@uppy/core/dist/style.css'
import '@uppy/drag-drop/dist/style.css'
import '@uppy/status-bar/dist/style.css'
import Transloadit from '@uppy/transloadit';
import {CloudUrl} from './../constants'

const companionUrl = 'https://app.taggbox.com';
const uppyKey = '0701d3d7f8d94e92a65a9cba388b1d96';
const uppyBucket = 'cloud.taggbox.com';
const uppyAwsKey = '47ABVORKG52UYE1KB28S';
const uppyAwsSecret = 'pcaTfeokaz28geAAns08H3yaGbOVz6ILgTptu19v';


const uppy = new Uppy({
    meta: { type: 'avatar' },
    autoProceed: true,
    debug: true,
    allowMultipleUploads: true,
    restrictions: {
        maxFileSize: 5000000,
        maxNumberOfFiles: 1,
        minNumberOfFiles: 1,
        allowedFileTypes: ['.jpg', '.jpeg', '.png']
    },
    onBeforeFileAdded: (currentFile, files) => {
        const file = uppy.getFile('uppyteamkongjpg1501851828779')
        // console.log("onBeforeFileAdded", currentFile, files)
    },
    onBeforeUpload: (files) => {
        //console.log("onBeforeUpload", files)


    }
})


uppy.use(Transloadit, {
    params: {
        auth: {
            key: uppyKey
        },
        steps: {
            resize: {
                robot: '/image/resize',
                width: 1920,
                resize_strategy: 'fit',
                background: 'none',
            },
            // store: {
            //     robot: "/s3/store",
            //     bucket: uppyBucket,
            //     key: uppyAwsKey,
            //     secret: uppyAwsSecret,
            //     path: 'studio/images/${file.id}'
            // }
            store: {
                robot: '/wasabi/store',
                bucket_region: "us-west-1",
                result: true,
                bucket: uppyBucket,
                key: uppyAwsKey,
                secret: uppyAwsSecret,
                path: 'onsiteupload/images/${file.id}'
            }
        }
    },
    waitForEncoding: true
})

class ProfileUpload extends Component {
    state = {
        selectedFile: null,
        selectedUrl: null
    }
    componentDidMount() {
        uppy.on('transloadit:result', (stepName, result) => {
            this.setState({ selectedFile: result.id, selectedUrl: `https://cloud.taggbox.com/onsiteupload/images/${result.id}` }, () => this.onUpdateLocalState())
        })
    }
    onUpdateLocalState = () => {
        const { updateProfileImage } = this.props;
        const { selectedUrl } = this.state;
        updateProfileImage(selectedUrl)
        this.setState({
            selectedFile: null,
            selectedUrl: null
        }, () => uppy.reset())
    }
    onProfileImageClear = event => {
        const { updateProfileImage } = this.props;

        this.setState({
            selectedFile: null,
            selectedUrl: null
        }, () => {
            updateProfileImage(null);
            uppy.reset()
        })
    }
    render() {
        const { selectedUrl } = this.state
        const { profileImage } = this.props
        return <>

            <div className="submit_pic_form_profile_wrap">
                {profileImage && profileImage.length > 0 ? <><img className="submit_pic_form_profile_img" src={profileImage} />

                    <div className="submit_pic_form_profile_remove_ico" onClick={this.onProfileImageClear}>
                        <svg aria-hidden="true" focusable="false" class="uppy-c-icon" width="16" height="16" viewBox="0 0 18 18">
                            <path fill="#db4b20" d="M9 0C4.034 0 0 4.034 0 9s4.034 9 9 9 9-4.034 9-9-4.034-9-9-9z"></path>
                            <path fill="#FFF" d="M13 12.222l-.778.778L9 9.778 5.778 13 5 12.222 8.222 9 5 5.778 5.778 5 9 8.222 12.222 5l.778.778L9.778 9z"></path>
                        </svg>
                    </div></> :
                    <div className="submit_pic_form_profile_img_ico">
                        <div className="submit_pic_form_profile_img_ico_uppy_">
                            <FileInput
                                pretty={true}
                                uppy={uppy}
                                allowMultipleFiles={false}
                            />
                            <div className="submit_pic_user_dummy_" style={{backgroundImage: `url('${CloudUrl}/images/author.png')`}}></div>
                            <div className="submit_pic_form_profile_upload_ico">
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 12.013 12.013">
                                    <g id="Group_7989" data-name="Group 7989" transform="translate(-328 -654)">
                                        <g id="Ellipse_471" data-name="Ellipse 471" transform="translate(328 654)" fill="#fff" stroke="#c6c6c6" strokeWidth="0.2">
                                            <circle cx="6" cy="6" r="6" stroke="none" />
                                            <circle cx="6" cy="6" r="5.9" fill="none" />
                                        </g>
                                        <g id="noun_Plus_613089" transform="translate(328.166 654.166)">
                                            <g id="Group_7984" data-name="Group 7984" transform="translate(0 0)">
                                                <path id="Path_7224" data-name="Path 7224" d="M12.923,7a5.923,5.923,0,1,0,5.923,5.923A5.925,5.925,0,0,0,12.923,7Zm0,.276a5.648,5.648,0,1,1-5.648,5.648A5.646,5.646,0,0,1,12.923,7.276Zm-.017,2.475a.138.138,0,0,0-.121.142v2.893H9.867a.138.138,0,1,0,.026.276h2.893v2.893a.138.138,0,1,0,.276,0V13.061h2.893a.138.138,0,1,0,0-.276H13.061V9.893a.138.138,0,0,0-.155-.142Z" transform="translate(-7 -7)" fill="#c6c6c6" />
                                            </g>
                                        </g>
                                    </g>
                                </svg>
                            </div>
                        </div>
                    </div>
                }

            </div>

            <div className="submit_pic_form_profile_txt">
                Upload Profile Image
            </div>
            <div className="submit_pic_uploader_status">
                <StatusBar
                    uppy={uppy}
                    hideUploadButton
                    hideAfterFinish={true}
                    showProgressDetails
                />
            </div>


        </>
    }
}

export default ProfileUpload;