import React, { PureComponent } from "react";
import Marquee from 'react-double-marquee';
import { PLAN_ID_ACTIVATE_MESSAGE_FOR_TICKER } from '../../constants'

const checkPlanID = (planID) => {
    let status = false;
    planID.map((item) => {
        if (PLAN_ID_ACTIVATE_MESSAGE_FOR_TICKER.includes(item)) status = true;
    })
    return status;
}

export default class PlanTicker extends PureComponent {
    state = {
        planTickerHide: false
    }

    componentDidMount() {
        this.onLoadTicker();
    }

    onLoadTicker = () => {
        const { newApi } = this.props;
        const { activatedAt, planId } = newApi.wall.UserDetail;
        if (activatedAt > Math.floor(Date.now() / 1000) && checkPlanID(planId.toString().split(","))) this.setState({ planTickerHide: true })
    }

    render() {
        // const { newApi } = this.props;
        const { planTickerHide } = this.state;
        return (

            planTickerHide ?
                <div className="headerTicker tickerBottom">
                    <div className="tickerSubtitleView" style={{ backgroundColor: '#171717', height: '70px' }}>
                        <Marquee speed={5}>
                            <h2 className="setFont font4" style={{ fontSize: '25px', color: '#fff' }}>
                                Your account is in “Event Setup Plan” which has an update time of 4 hours. This ticker will be removed and real-time updates will be enabled on the actual Event Date/Time. Feel free to do your customizations and set up everything for your Event during this time.
                        </h2>
                        </Marquee>
                    </div>
                </div>

                : null
        )
    }
}