import React, { Component } from 'react';
import { instanceOf } from 'prop-types';
import './assets/css/upgrade-popup-style.css'


import { withCookies, Cookies } from 'react-cookie';
import { API_ROOT_URL, VERIFY_IDS } from '../../constants'
const styles = {
    modalHeader: { textAlign: 'center' }

}

class UpgradeScreenPopupTicker extends Component {
    static propTypes = {
        cookies: instanceOf(Cookies).isRequired
    };
    constructor(props) {
        super(props)
        this.state = {
            isUpgradeHide: true
        }
    }


    updateIsUpgrade = () => setTimeout(() => {
        this.setState({ isUpgradeHide: true }, () => this.upgradeScreenTimer())
    }, 15000)

    upgradeScreenTimer = () => {
        const { newApi } = this.props;
        if (newApi.wall.UserRule.branding) setTimeout(() => {
            this.setState({ isUpgradeHide: false }, () => this.updateIsUpgrade())
        }, 60000)
    }

    componentDidMount() {
        this.upgradeScreenTimer()
    }
    onCreateCookies = event => {
        const { cookies } = this.props;
        var date = new Date();
        date.setTime(date.getTime() + (1 * 60 * 60 * 1000));
        cookies.set('planUpgrade', date, { path: '/', expires: date });
    }

    render() {
        const { isUpgradeHide } = this.state;
        const { newApi, see } = this.props;


        return (
            <>
                <div className={`tb_upgrade_001 ticker_upgrade_ ${isUpgradeHide === false && newApi.wall.UserRule.branding && see !== 1 ? 'tb_show_02_' : ''}`}>
                    <div className="tb_popup_wrap_002">
                        <div className="tb_popup_wrap_in__">
                            <div className="tb_popup_body_">
                                <div className="tb_ticker_content__003">
                                    <div className="tb_pera__004">
                                        We hove just paused your account access for now. But no need to worry, It will resume onece you upgrade your plan.
                                </div>
                                    <div className="tb_popup_btn_004">
                                        {
                                            newApi.wall != null && parseInt(newApi.wall.Wall.owner) && VERIFY_IDS.includes(newApi.wall.Wall.owner) ? <a target="_blank" className="tb_btn__005" href={`${API_ROOT_URL}events`} onClick={this.onCreateCookies}>Upgrade Now!</a> : <a target="_blank" className="tb_btn__005" href="https://taggbox.com/price/display-price/">Upgrade Now!</a>
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


            </>

        )
    }
}


export default withCookies(UpgradeScreenPopupTicker);