import React from 'react'
import Skeleton from 'react-loading-skeleton'
const SlideShowSkeleton = ({ height }) => <div className="post">
    <div className="col-sm-6">
        <div className="makeBgImg">
            <Skeleton style={{ height: `calc(85vh - 80px)`, marginTop: '5vh' }} />
        </div>
    </div>
    <div className="col-sm-6">
        <div className="skeletonContent">
            <div className="skeltoncss">
                <div className="section-one-skel">
                    <Skeleton circle height={100} width={100} />
                    <h4>
                        <div className="skel-span-m">
                            <p><Skeleton height={15} /></p>
                            <p><Skeleton height={15} /></p>
                            <p className="s-sket-m"><Skeleton height={15} /></p>
                        </div>
                    </h4>
                </div>
                <div className="section-two-skel">
                    <Skeleton circle={true} height={80} width={80} />
                    <div className="skel-span-m2">
                        <p><Skeleton height={15} /></p>
                        <p className="sket-two-m"><Skeleton height={15} /></p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
export default SlideShowSkeleton;