import React, { Component } from 'react';
import { v4 as uuidv4 } from 'uuid';
import moment from 'moment';
import ReactPlayer from 'react-player'
import Iframe from 'react-iframe';
import { findNetworkDataFromWebFilters, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP, IMG_LOAD_AFTER_COMPLETE_LAYOUT, getRandomIntNoDuplicates } from '../../../utils'
import { NO_CAMERA_IMAGE } from '../../../constants'
import FontFamilyForPost from '../../sepcomponents/FontFamilyForPost'
import AuthorAvatar from '../../sepcomponents/AuthorAvatar';
const VideoComponent = ({ data, newApi }) => {
    return (data.type === 3 || data.type === 5) && <>
        {
            data.link && (data.link.indexOf("youtube") >= 0 || data.link.indexOf("youtu.be") >= 0) &&

            <ReactPlayer url={`${data.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} playing
                muted
                config={{
                    file: {
                        attributes: {
                            autoPlay: true,
                            muted: true
                        }
                    }
                }} />
        }


        {
            data.link && (data.link.indexOf("vimeo") >= 0) &&
            <ReactPlayer url={`${data.mediaUrl}`} height='100%' width='100%' controls={true} allowFullScreen={true} playing
                muted
                config={{
                    file: {
                        attributes: {
                            autoPlay: true,
                            muted: false
                        }
                    }
                }} />
        }

        {

            data.link && (data.link.indexOf("soundcloud") >= 0) &&
            <Iframe
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                src={`${data.mediaUrl}`}
                allowFullScreen="true"
                style={{ width: "100%!important" }}
            ></Iframe>

        }

        {
            data.link && (data.mediaUrl.indexOf("onsiteupload/video") >= 0 || data.link.indexOf("linkedin") >= 0 || data.link.indexOf("twitter") >= 0 || data.link.indexOf("instagram") >= 0 || data.link.indexOf("tumblr") >= 0 || data.link.indexOf("instagram") >= 0) && <video
                src={`${data.mediaUrl}`}
                autoPlay={true}
                muted={true}
                onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e) }}
                data-type="video"
                data-stories={data.stories}
                data-filter-id={data.filterId}
                data-network={data.networkId}
                data-postid={data.id}
                data-link={data.link}
                data-load={0}
                data-tb={newApi.wall.UserDetail.db_table}
                data-wall-id={newApi.wall.Wall.id}  data-owner-id={newApi.wall.Wall.owner} data-item-id={data.id}
            ></video>
        }
    </>

}


const SeprateDots = ({ data,userStyle }) => {

    return (data && data.author) ? <div className="tb_sepratedot_" style={{
        display:
            data.postAuthor == 0 || data.timePost == 0 || data.author.isInstaUser
                ? "none"
                : "" ,  background:userStyle.authorColor
    }}></div> : null
}
const TimePosts = ({ data,userStyle }) => {
    return (data && data.createdAt && Object.keys(data.author).length > 0) ? <div className="tb_post_time_" style={{ display: data.timePost == 0 || data.author.isInstaUser ? "none" : "",color:userStyle.authorColor}}>{moment(new Date(data.createdAt * 1000)).fromNow()}</div> : null
}

const AutherName = ({ data ,userStyle}) => {
    return (data && Object.keys(data.author).length > 0) ? <div className="tb_author_name_" style={{ display: (data.postAuthor == 0 || data.author.isInstaUser) ? 'none' : '', color:userStyle.authorColor}}>{data.author.name}</div> : null
}

const UserName = ({ data , userStyle }) => {
    return (data && Object.keys(data.author).length > 0) ? <div className="tb_author_username" style={{ display: data.postAuthor == 0 || data.author.isInstaUser ? "none" : "", color:userStyle.authorColor}}>@ {data.author.username}</div> : null
}
class MashupPost extends Component {
    constructor(props) {
        super(props)
    }
    state = {
        slideUpDownClass: null,
        profileLogo: true,
        postIndex1: 0,
        postIndex2: 0,
        isAuthorImagevalid: true
    }
    componentDidMount() {
        let { slideUpDownClass, totalPostCount, MashUpData, isFirst, cRender, rootHeightHalf } = this.props;
        if (MashUpData.length == totalPostCount) MashUpData = []
        let postIndex1 = 0; let postIndex2 = 0;
        if (isFirst) {
            postIndex1 = parseInt(cRender) + 1
            postIndex2 = parseInt(cRender) + 2
        }
        else {
            postIndex1 = getRandomIntNoDuplicates(0, totalPostCount, MashUpData);
            postIndex1 = postIndex1 == totalPostCount ? 0 : postIndex1;

            postIndex2 = getRandomIntNoDuplicates(0, totalPostCount, MashUpData);
            postIndex2 = postIndex2 == totalPostCount ? 0 : postIndex2;

        }
        this.setState({ slideUpDownClass: slideUpDownClass, postIndex1: postIndex1, postIndex2: postIndex2 });
        //setTimeout(() => this.setState({ slideUpDownClass: slideUpDownClass }), 200)
    }
    componentWillUnmount() {
        this.setState({ slideUpDownClass: null })
    }
    render() {
        const { slideUpDownClass, postIndex1, postIndex2 } = this.state;
        const { postCount, totalPostCount, newItems, newApi, rootHeightHalf } = this.props;
        let postData1 = {}; let postData2 = {}; let netwrokd1 = {}; let netwrokd2 = {}



        if (newItems[parseInt(postIndex1)]) {
            postData1 = newItems[parseInt(postIndex1)];
            netwrokd1 = findNetworkDataFromWebFilters(postData1.networkId, newApi.webFilters);
        }
        if (totalPostCount > (parseInt(postIndex2)) && newItems[(parseInt(postIndex2))]) {
            postData2 = newItems[(parseInt(postIndex2))];
            netwrokd2 = findNetworkDataFromWebFilters(postData2.networkId, newApi.webFilters);
        }
        else if (newItems[0]) {
            postData2 = newItems[0];          
            netwrokd2 = findNetworkDataFromWebFilters(postData2.networkId, newApi.webFilters);
        }

        const style = { paddingBottom: rootHeightHalf }
        return slideUpDownClass != null ? <>
            {(postData1 && Object.keys(postData1).length > 0) && <div className="tb_01_post_36__" style={style}>
                <div id={`postID${postData1.id}`} className={"tb_01_media_36__" + (slideUpDownClass ? ' tb_animate__slideInDown' : '')} style={{ backgroundColor: newApi.wall.ThemeRule.font.cardColor }}>

                    <div className="tb_01_blur" style={{ backgroundImage: `url(${postData1.postFileNew})` }}>

                    </div>

                    <div className="tb_media_img_002">
                        {
                            (postData1.type === 2 || postData1.type === 4) ? <img src={postData1.postFileNew}
                                data-link={postData1.link} data-load="0" data-network={netwrokd1.id} data-postid={postData1.id} data-wall-id={newApi.wall.Wall.id} data-owner-id={newApi.wall.Wall.owner}
                                data-tb={newApi.wall.UserDetail.db_table}
                                data-item-id={postData1.id}
                                data-filter-id={postData1.filterId}
                                data-stories={postData1.stories}
                                onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)}
                                alt="" />
                                : <VideoComponent data={postData1} newApi={newApi} />
                        }


                    </div>

                    <div className="tb_post_content_wrap_001">
                        <div className="tb_post_cont_001">
                            <div className="tb_cont_txt_001">
                                <FontFamilyForPost item={postData1} newApi={newApi} personalization={newApi.wall.Personalization} themeRule={newApi.wall.ThemeRule} network={netwrokd1} signageTheme={newApi.wall.Personalization.signageTheme} ownerID={newApi.wall.Wall.owner} />

                            </div>
                        </div>
                        <div className="tb_post_author_wrap_001">
                            <div className="tb_post_author_001">
                                <div className="tb_author_wrap">

                                    {newApi.wall.Personalization.postAuthor == 1 && postData1 && postData1.author && Object.keys(postData1.author).length > 0 && postData1.author.picture && <div style={{ backgroundImage: `url("${postData1.author.picture}"),`, backgroundPosition: 'center', backgroundSize: 'cover', backgroundRepeat: 'no-repeat', display: (postData1.postAuthor == 0 || postData1.author.isInstaUser) ? 'none' : '' }} className="tb_author_profile_"  >
                                        {this.state.isAuthorImagevalid && < img className="img-fluid" style={{ display: ("" === 0 || postData1.author.isInstaUser) ? 'block' : 'block' }} src={postData1.author.picture}
                                            onError={(e) => {
                                                this.setState({
                                                    isAuthorImagevalid: false
                                                });
                                                //console.clear()
                                            }}
                                            alt="" />}

                                        {!this.state.isAuthorImagevalid && <AuthorAvatar style={{ display: (newApi.wall.Personalization.postAuthor === 0 || postData1.author.isInstaUser) ? 'none' : 'flex' }} username={postData1.author.name} backgroundColor={netwrokd1.color} color={newApi.wall.ThemeRule.fontColor} />}





                                    </div>}
                                    <div className="tb_author_inital_"></div>
                                </div>








                                <div className="tb_author_details_001">
                                    {/* <div className="tb_author_name_">{postData1.author.name}</div> */}
                                    {newApi.wall.Personalization.postAuthor == 1 && <AutherName data={postData1} userStyle={newApi.wall.ThemeRule}/>}

                                    <div className="tb_author_details_01">
                                        {/* <div className="tb_author_username">@ {postData1.author.username}</div> */}
                                        {newApi.wall.Personalization.postAuthor == 1 && <UserName data={postData1} userStyle={newApi.wall.ThemeRule} />}

                                        {newApi.wall.Personalization.postAuthor == 1 && newApi.wall.Personalization.postTime == 1 ? <SeprateDots data={postData1} userStyle={newApi.wall.ThemeRule} />:''}                                        
                                        {newApi.wall.Personalization.postTime == 1 && <TimePosts data={postData1} userStyle={newApi.wall.ThemeRule}/>}
                                    </div>
                                </div>
                            </div>
                            <div className="tb_post_social_icon">
                                {netwrokd1 && Object.keys(netwrokd1).length > 0 ? <div className={`fa ${netwrokd1.icon}`} style={{ color: newApi.wall.ThemeRule.iconColor }}></div> : null}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            }
            {(postData2 && Object.keys(postData2).length > 0) && <div className="tb_01_post_36__" style={style}>
                <div id={`postID${postData2.id}`} className={"tb_01_media_36__" + (slideUpDownClass ? ' tb_animate__slideInUp' : '')} style={{ backgroundColor: newApi.wall.ThemeRule.font.cardColor }}>
                    <div className="tb_01_blur" style={{ backgroundImage: `url(${postData2.file})` }}>
                    </div>

                    <div className="tb_media_img_002">
                        {
                            (postData2.type === 2 || postData2.type === 4) ? <img src={postData2.postFileNew}

                                data-link={postData2.link} data-load="0" data-network={netwrokd2.id} data-postid={postData2.id} data-wall-id={newApi.wall.Wall.id}
                                data-tb={newApi.wall.UserDetail.db_table} data-item-id={postData2.id}
                                data-filter-id={postData2.filterId}
                                data-stories={postData2.stories}
                                data-owner-id={newApi.wall.Wall.owner}
                                onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)}
                                alt="" />
                                : <VideoComponent data={postData2} newApi={newApi} />
                        }
                    </div>

                    <div className="tb_post_content_wrap_001">
                        <div className="tb_post_cont_001">
                            <div className="tb_cont_txt_001">
                                <FontFamilyForPost item={postData2} newApi={newApi} personalization={newApi.wall.Personalization} themeRule={newApi.wall.ThemeRule} network={netwrokd2} signageTheme={newApi.wall.Personalization.signageTheme} ownerID={newApi.wall.Wall.owner} />

                            </div>
                        </div>
                        <div className="tb_post_author_wrap_001">
                            <div className="tb_post_author_001">
                                <div className="tb_author_wrap">
                                    {newApi.wall.Personalization.postAuthor == 1 && postData2 && postData2.author && Object.keys(postData2.author).length > 0 && postData2.author.picture && <div style={{ backgroundImage: `url("${postData2.author.picture}"),url("${postData2.author.errorPic}")`, backgroundPosition: 'center', backgroundSize: 'cover', backgroundRepeat: 'no-repeat', display: (postData2.postAuthor == 0 || postData2.author.isInstaUser) ? 'none' : '' }} className="tb_author_profile_" >

                                        {this.state.isAuthorImagevalid && < img className="img-fluid" style={{ display: ("" === 0 || postData2.author.isInstaUser) ? 'block' : 'block' }} src={postData2.author.picture}
                                            onError={(e) => {
                                                this.setState({
                                                    isAuthorImagevalid: false
                                                });
                                                // console.clear()
                                            }}
                                            alt="" />}

                                        {!this.state.isAuthorImagevalid && <AuthorAvatar style={{ display: (newApi.wall.Personalization.postAuthor === 0 || postData2.author.isInstaUser) ? 'none' : 'flex' }} username={postData2.author.name} backgroundColor={netwrokd1.color} color={newApi.wall.ThemeRule.fontColor} />}
                                    </div>}
                                    <div className="tb_author_inital_"></div>
                                </div>
                                <div className="tb_author_details_001">
                                    {newApi.wall.Personalization.postAuthor == 1 && <AutherName data={postData2} userStyle={newApi.wall.ThemeRule} />}

                                    <div className="tb_author_details_01">
                                        {/* <div className="tb_author_username" style={{display: postData2.postAuthor == 0 || postData2.author.isInstaUser ? "none" : ""}}>@ {postData2.author.username}</div> */}
                                        {newApi.wall.Personalization.postAuthor == 1 && <UserName data={postData2} userStyle={newApi.wall.ThemeRule} />}
                                        {newApi.wall.Personalization.postAuthor == 1 && newApi.wall.Personalization.postTime == 1 ? <SeprateDots data={postData1} userStyle={newApi.wall.ThemeRule} />:''}
                                        {newApi.wall.Personalization.postTime == 1 && <TimePosts data={postData2} userStyle={newApi.wall.ThemeRule} />}
                                    </div>
                                </div>
                            </div>
                            <div className="tb_post_social_icon">
                                {netwrokd2 && Object.keys(netwrokd2).length > 0 ? <div className={`fa ${netwrokd2.icon}`} style={{ color: newApi.wall.ThemeRule.iconColor }}></div> : null}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            }</> : null

    }
}

export default MashupPost;