import React, { Component } from "react";
import { IFrame } from './iframe'
import LeaderBoard from "../leaderBorad";
// import './assets/css/web-font.css'
// import './assets/css/style.css'
const toAbsoluteUrl = pathname => `https://cloud.taggbox.com/display/studio` + pathname;
const LeaderIframe = (porps) => (
    <IFrame>
        <link rel="stylesheet" href={toAbsoluteUrl("/css/leaderboard/css/style.css")} />
        <link rel="stylesheet" href={toAbsoluteUrl("/css/leaderboard/css/web-font.css")} />
        <LeaderBoard {...porps} />
    </IFrame>
)
export default LeaderIframe;