import React, { Component } from "react";
import { Helmet } from "react-helmet";
import './assets/css/web-font.css'
import './assets/css/style.css'
import moment from "moment";
import { getLeaderboard } from "../../actions/sceneActions";
import { kFormatter } from '../../utils'

class LeaderBoard extends Component {
    state = { topUsers: [], noDataFound: false }
    componentDidMount() {
        this.getLeaderBoard()
    }
    getLeaderBoard = () => {

        //influen :id => 1 most active : 2 
        const { wallId, app_data, feedList } = this.props;
        const { numberOfRows, dateFrom, leader, dateTo } = app_data
        var self = this;
        getLeaderboard({
            wallId,
            leader: leader,
            rows: numberOfRows,
            startDate: `${moment(dateFrom).unix()}`,
            endDate: `${moment(dateTo).unix()}`,
            feedList
        }).then(res => {
            if (res.data.responseData.data.length > 0) self.setState({ topUsers: res.data.responseData.data, noDataFound: false })
            else self.setState({ noDataFound: true })
        }).catch((error) => {
            self.setState({ noDataFound: true })
        })
    }


    render() {

        const { numberOfRows, theme, leader } = this.props.app_data
        const rows = numberOfRows;
        const { topUsers, noDataFound } = this.state
        return (<div className="ts_board_users" data-theme={theme == 1 ? 'dark' : 'light'}>

            {topUsers && topUsers.length ? <div className="ts_user_wrapper">
                <div className="ts_board_title">Top {leader == 1 ? `Most Popular Influencers` : `Most Active Users`}</div>
                <div className={`ts_user_list_wrapper ts_users_top_${rows} ts_user_count_${rows}`}>
                    <div className="ts_top_users">
                        {topUsers && topUsers.map((user, index) => {
                            return (
                                <div className={`ts_user_list ts_user__${index + 1}`}>
                                    <div className="ts_author_wrapper">
                                        <div className="ts_author_count_wrapper">
                                            <div className="tb_author_count">{index + 1}</div>
                                        </div>
                                        <div className="ts_author_wrap_in">
                                            <div className="ts_author_profile">
                                                <img src={user.image} onError={event => event.target.src = user.author.errorPic} />
                                                {/* <div class="ts_author_avatar__" style={{backgroundColor: '#000'}}>S</div> */}
                                                <div className="ts_author_social_">
                                                    <div className={`ts_social_ico tb__icon ${user.network.icon}`}> </div>
                                                </div>
                                            </div>
                                            <div className="ts_author_info_wrapper">
                                                <div className="ts_author_info">
                                                    <div className="ts_authorname">{user.author.name}</div>
                                                    <div className="ts_username">{user.author.username}</div>
                                                </div>
                                                <div className="ts_followers_count">{leader == 1 ? kFormatter(parseInt(user.author.follower)) : kFormatter(parseInt(user.postCount))} {leader == 1 ? user.author.follower && parseInt(user.author.follower) > 1 ? `Followers` : `Follower` : user.postCount && parseInt(user.postCount) > 1 ? `Posts` : `Post`} </div>
                                            </div>
                                            <div className="ts_author_social_right">
                                                <div className={`ts_social_ico tb__icon ${user.network.icon}`}> </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                    {/* {rows == 10 || rows == 6 ? 
                        <div className="ts_all_users">
                                {topUsers && topUsers.map((user, index) => {
                                    if(rows == 6 && index > 2){
                                        return(
                                            <div key={index} className={`ts_user_list ts_user__${index+1}`}>
                                            <div className="ts_author_wrapper">
                                                <div className="ts_author_count_wrapper">
                                                    <div className="tb_author_count">{rows == 10 ? index + 1 : index + 1 }{}</div>
                                                </div>
                                                <div className="ts_author_wrap_in">
                                                    <div className="ts_author_profile">
                                                        <img src={user.image} />
                                                        <div className="ts_author_social_">
                                                            <div className={`ts_social_ico tb__icon ${user.network.icon}`}> </div>
                                                        </div>
                                                    </div>
                                                    <div className="ts_author_info_wrapper">
                                                        <div className="ts_author_info">
                                                            <div className="ts_authorname">{user.author.name}</div>
                                                            <div className="ts_username">{user.author.username}</div>
                                                        </div>
                                                        <div className="ts_followers_count">{user.author.follower} followers</div>
                                                    </div>
                                                    <div className="ts_author_social_right">
                                                        <div className={`ts_social_ico tb__icon ${user.network.icon}`}> </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        )
                                    }
                                    if(rows != 6 && index > 2){
                                        return(
                                            <div key={index} className={`ts_user_list ts_user__${index+1}`}>
                                            <div className="ts_author_wrapper">
                                                <div className="ts_author_count_wrapper">
                                                    <div className="tb_author_count">{ index + 1 }</div>
                                                </div>
                                                <div className="ts_author_wrap_in">
                                                    <div className="ts_author_profile">
                                                        <img src={user.image} />
                                                        <div className="ts_author_social_">
                                                            <div className={`ts_social_ico tb__icon ${user.network.icon}`}> </div>
                                                        </div>
                                                    </div>
                                                    <div className="ts_author_info_wrapper">
                                                        <div className="ts_author_info">
                                                            <div className="ts_authorname">{user.author.name}</div>
                                                            <div className="ts_username">{user.author.username}</div>
                                                        </div>
                                                        <div className="ts_followers_count">{user.author.follower} followers</div>
                                                    </div>
                                                    <div className="ts_author_social_right">
                                                        <div className={`ts_social_ico tb__icon ${user.network.icon}`}> </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        )
                                    }
                                })}
                            
                        </div> : null} */}
                </div>
            </div>
                :
                noDataFound ? <div className="ts_user_wrapper">
                    <div className="ts_no_users">
                        <img className="ts_no_users_img" src={`/css/leaderboard/user-profile.svg`} alt={'no users'} />
                        <div className="ts_no_user_title">
                            No Entry Found Yet
                        </div>
                    </div>
                </div> : null}

        </div>);

    }
}



export default LeaderBoard;