
import React, { PureComponent } from 'react';
import { Helmet } from "react-helmet";
import { SERVER_PATH, DEFAULT_FINAL_PATH, DEFAULT_LOGO } from '../../constants'
import { MiddleBanner, BannerSocialIcons } from '../../utils'
import BannerLogo from './bannerLogo'
const styles = { bannerLogoIMG: { maxWidth: 240, width: "auto", paddingTop: 5, paddingBottom: 5, objectFit: "contain" } }

export default class BannerView extends PureComponent {
  render() {
    const { LogoImage, CustomBannerImage, CustomBanner, wallData } = this.props;
    const bottomApply = wallData.CustomTicker.status === 1 && wallData.CustomTicker.ticker_position === "2" ? wallData.CustomTicker.tickerType == 1 ? wallData.CustomTicker.ticker_height : (wallData.CustomTicker.ticker_height - 10) < 0 ? wallData.CustomTicker.ticker_height : (wallData.CustomTicker.ticker_height - 10) : 0

    return <>
      {CustomBanner.status === 1 && CustomBanner.bannerType == 5 ? <Helmet>

        <style type="text/css">{`#embedBannerImage .banner-5 .headerbannerRight:before, #wBannerImage .banner-5 .headerbannerRight:before, #wallBannerImage .banner-5 .headerbannerRight:before{ background:${CustomBanner.background_color_two}}`}</style>
      </Helmet> : null}
      {
        CustomBanner.status === 1 ?
          <div className={CustomBanner.banner_position == 1 ? "bannerTop" : "bannerBottom"} id="wBannerImage" style={CustomBanner.banner_position != 1 ? { bottom: bottomApply } : null}>
            <div className={`taggboxHeaderBanner banner-${CustomBanner.bannerType}`} style={{ backgroundSize: 'cover', height: CustomBanner.banner_height, backgroundColor: CustomBanner.background_color, backgroundImage: (CustomBannerImage.newName === "8661481540843.08240" || CustomBannerImage.newName === 8661481540843.082) ? `url(${DEFAULT_FINAL_PATH})` : CustomBanner.bannerType != 5 ? `url(${SERVER_PATH}${CustomBannerImage.path}${CustomBannerImage.newName})` : '' }}>
              <div className="headBannerContent">
                <div className="headerBannerLeft">
                  <div className="headBannerLogo">
                    {LogoImage.newName != null ? <BannerLogo LogoImage={LogoImage} newName={wallData.LogoImage.newName} image_path={`${SERVER_PATH}${LogoImage.path}${LogoImage.newName}`} /> : null}
                  </div>
                </div>
                <MiddleBanner wall={wallData} />
                <div className="headerbannerRight">
                  <ul className="headbannerSocialIcon">
                    <BannerSocialIcons wall={wallData} />
                  </ul>
                </div>
                <div className="clear"></div>
              </div>
            </div>
          </div>
          : null
      }
    </>
  }
}
