import React, { memo } from 'react'
import ReactHtmlParser from 'react-html-parser';
// import './trim.css'
import { valuesDevided, mathTrunc, valuesMods, reactStringReplace } from '../../utils';
import { FontFamilyH1Heading, FontLeftQuote, FontRightQuote } from '../../fontFamily';
import SlackEmoji from '../slackEmoji'
import Ratings from '../themes/ModernWaterFall/ratings'
const FontFamilyForPostSlackTheme = ({ item, width, postWidth, signageTheme, trimcontent, personalization, themeRule, network }) => {

  if (signageTheme !== 3 && signageTheme !== 20) {
    if (personalization.hashtag_highlight === 1) {
      if (personalization.hashtag_feed === 0 && personalization.hashtag_all === 0) {
        return !(item.type !== 1 && themeRule.hideContent === 1) && <>
          {item.rating !== undefined && item.rating !== null ? <Ratings rating={item.rating} networkId={item.networkId} /> : null}


          {!(network.name === 'Slack' || network.name === null) && <FontFamilyH1Heading contentTitle={item.contentTitle} />}
          <>
            <>  {
              (signageTheme === 4 || signageTheme === 16) && item.content.length !== 0 && item.content !== undefined ?
                ReactHtmlParser(item.content)
                : network.id === 20 ? <SlackEmoji text={window.slackdown.parse(ReactHtmlParser(item.content), item.slackMember)} /> : item.content
            }
            </>
          </>
        </>
      }
      else if ((personalization.hashtag_feed === 0 && personalization.hashtag_all === 1) || (personalization.hashtag_feed === 1 && personalization.hashtag_all === 1)) {

        return !(item.type !== 1 && themeRule.hideContent === 1) && <>

          {item.rating && item.rating != 0 ? <Ratings rating={item.rating} networkId={item.networkId} /> : null}
          {!(network.name === 'Slack' || network.name === null) && <FontFamilyH1Heading contentTitle={item.contentTitle} />}
          <>
            <> {
              (signageTheme === 4 || signageTheme === 16) && item.content.length !== 0 && item.content === undefined ?
                reactStringReplace(ReactHtmlParser(item.content), /#(\w+)/g, (match, i) => (
                  <span key={match + i} style={{ color: personalization.hashtag_color, fontWeight: "bold" }}>#{match}</span>
                ))
                :
                (signageTheme === 4 || signageTheme === 16) && item.content.length !== 0 && item.content !== undefined ?
                  reactStringReplace(ReactHtmlParser(item.content), /#(\w+)/g, (match, i) => (
                    <span key={match + i} style={{ color: personalization.hashtag_color, fontWeight: "bold" }}>#{match}</span>
                  )).concat('...')
                  : item.networkId === 20 ?
                    window.slackdown.parse(reactStringReplace(ReactHtmlParser(item.content), /#(\w+)/g, (match, i) => (
                      <span key={match + i} style={{ color: personalization.hashtag_color, fontWeight: "bold" }}>#{match}</span>
                    )), item.slackMember)
                    :
                    item.networkId === 20 ?
                      window.slackdown.parse(reactStringReplace(ReactHtmlParser(item.content), /#(\w+)/g, (match, i) => (
                        <span key={match + i} style={{ color: personalization.hashtag_color, fontWeight: "bold" }}>#{match}</span>
                      )), item.slackMember) : reactStringReplace(ReactHtmlParser(item.content), /#(\w+)/g, (match, i) => (
                        <span key={match + i} style={{ color: personalization.hashtag_color, fontWeight: "bold" }}>#{match}</span>
                      ))

            }</>
          </>
        </>
      }
      else if (personalization.hashtag_feed === 1 && personalization.hashtag_all === 0) {

        return !(item.type !== 1 && themeRule.hideContent === 1) && <>

          {item.rating && item.rating != 0 ? <Ratings rating={item.rating} networkId={item.networkId} /> : null}
          {!(network.name === 'Slack' || network.name === null) && <FontFamilyH1Heading contentTitle={item.contentTitle} />}
          <>
            <>{
              (signageTheme === 4 || signageTheme === 16) && item.content.length !== 0 && item.content !== undefined && network.id != 20 ?
                reactStringReplace(ReactHtmlParser(item.content), item.hash.hashString, (match, i) => (
                  <span className="gridhashtag" style={{ color: personalization.hashtag_color, fontWeight: "bold" }}>{match}</span>
                )).concat('...')
                :
                (signageTheme === 4 || signageTheme === 16) && item.content.length !== 0 && item.content === undefined && network.id != 20 ?
                  reactStringReplace(ReactHtmlParser(item.content), item.hash.hashString, (match, i) => (
                    <span className="gridhashtag" style={{ color: personalization.hashtag_color, fontWeight: "bold" }}>{match}</span>
                  ))

                  : network.id == 20 ? <>{reactStringReplace(window.slackdown.parse(ReactHtmlParser(item.content), item.slackMember), item.hash.hashString, (match, i) => (
                    <span className="gridhashtag" style={{ color: personalization.hashtag_color, fontWeight: "bold" }}>{match}</span>
                  ))}</> :

                    reactStringReplace(ReactHtmlParser(item.content), item.hash.hashString, (match, i) => (
                      <span className="gridhashtag" style={{ color: personalization.hashtag_color, fontWeight: "bold" }}>{match}</span>
                    ))
            }</>
          </>
        </>
      }
    } else {
      return !(item.type !== 1 && themeRule.hideContent === 1) && <>
        {item.rating && item.rating != 0 ? <Ratings rating={item.rating} networkId={item.networkId} /> : null}

        {!(network.name && network.name === 'Slack') && <FontFamilyH1Heading contentTitle={item.contentTitle} />}
        <>
          <> {
            (signageTheme === 4 || signageTheme === 16) && item.content.length !== 0 && item.content !== undefined ?
              ReactHtmlParser(item.content) : item.networkId === 20 ? <SlackEmoji text={window.slackdown.parse(item.content, item.slackMember)} /> : ReactHtmlParser(item.content)
          }</>
        </>
      </>
    }
  } else return null
}
export default memo(FontFamilyForPostSlackTheme);