import React, { Component } from "react";
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import ReactHtmlParser from "react-html-parser";
import '../css/noticeBoard.css'
class NoticeBoardPreview extends Component {
    render() {
        const {
            app_data,
            text } = this.props;

        // console.log("this.props", app_data.text)
        const bgstyleImg = app_data.backgroundImage ? { backgroundImage: `url(${app_data.backgroundImage})`, height: '100%' } : { height: '100%' }
        const bgstyleColor = app_data.Background ? { backgroundColor: app_data.Background, height: '100%' } : { height: '100%' }
        return <div style={app_data.isBackground ? bgstyleImg : bgstyleColor} className="notice_preview">
            {app_data.logoIcon && <div className="nbtd_logo_icon" style={{ 'justifyContent': app_data.logoPosition }}>
                <img src={app_data.logoIcon} className="nbtd_logo_icon_img" alt="logo" />
            </div>}
            {app_data.text && <div class="ql-editor"> {ReactHtmlParser(app_data.text)}</div>}

        </div>


    }
}

export default NoticeBoardPreview