import React, { PureComponent } from "react";
import { PostNetworkChildIconView } from './sepcomponents/newutils';
import { findNetworkDataFromWebFilters } from './../utils'
import ErrorBoundary from '../errorBoundary'
export default class NetworkCardComponent extends PureComponent {

    render() {
        const { network, font, item, themeRule } = this.props;
        let netwrk = ""
        let networkData = network;

        if (network && network.icon) {
            netwrk = network.icon !== null ? network.icon.substring(3) : null;
        }
        else if (item && item.network && item.network.icon) {
            networkData = item.network;
            netwrk = item.network.icon !== null ? item.network.icon.substring(3) : null;
        }

        return (
            <ErrorBoundary> <>{
                networkData.icon !== null ?
                    network.name !== "Workplace" && networkData.name !== "SMS" ? <PostNetworkChildIconView themeRule={themeRule} network={networkData} iconClass={network.id==20 ? `tb_icon fa fa-${netwrk}` : `fa fa-${netwrk}`} fontAwesomeIcon={['fab', netwrk === 'facebook' ? 'facebook-f' : netwrk]} netwrk={netwrk} />
                        : networkData.name === "Workplace" ? <PostNetworkChildIconView themeRule={themeRule} network={networkData} iconClass={`fa icon-taggbox-workplace-2`} fontAwesomeIcon={null} netwrk={netwrk} />
                            : networkData.name === "SMS" ? <PostNetworkChildIconView themeRule={themeRule} network={networkData} iconClass={`fa fa-envelope-o`} fontAwesomeIcon={['far', 'envelope']} netwrk={netwrk} />
                                : <PostNetworkChildIconView themeRule={themeRule} network={networkData} iconClass={network.id==20 ? `tb_icon fa fa-${netwrk}` : `fa fa-${netwrk}`} fontAwesomeIcon={['fas', 'rss']} netwrk={netwrk} />
                    : null



            }</></ErrorBoundary>)
    }
}