import React from 'react'

export const PostNetworkChildIconView = ({  network, iconClass, fontAwesomeIcon, netwrk, themeRule }) => 
<div className="postNetwork" style={{ borderBottomColor : themeRule.iconType === 1 ? themeRule.iconColor : network.color }} data-network={network.name}

> 
{fontAwesomeIcon !== null ? <i className={iconClass} style={{color : themeRule.iconType === 1 ? themeRule.iconColor : network.color}}></i> :null
}
</div>


