import React from "react"
const OverlayUI = ({ transitionStatus, transition }) => <ul>
    {
        [0, 1, 2, 3].map((i) => <li key={i} style={transitionStatus === 1 ? { backgroundColor: transition } : null}></li>)
    }
</ul>

const AnimationOverlay = ({ active, transitionStatus, transition, network }) => {
    return <div className="slideShowWrapp" data-network={transitionStatus === 0 ? network.name : ""}>
        <div className={`topOverlay ${active ? `goToAnimate` : ``}`}>
            <OverlayUI transitionStatus={transitionStatus} transition={transition} />
        </div>
        <div className={`bottomOverlay ${active ? `goToAnimate` : ``}`}>
            <OverlayUI transitionStatus={transitionStatus} transition={transition} />
        </div>
    </div>
}

export default AnimationOverlay;

