import React, { Component } from "react";
import moment from 'moment'
import { getWeatherForcasteByLocation, getWeatherSpecificByLocation } from '../../actions/sceneActions'
import { isDayTime } from '../../utils'
import './weather/weather.css'
import './inter-fonts.css'
const weatherIcon = `https://cloud.taggbox.com/display/studio/weather-icons/ts_icon-`
class WeatherPreview extends Component {
    state = {
        weatherData: {},
        currentData: {}
    }
    componentDidMount() {
        this.onGetWeatherFromLocation();
    }
    onGetWeatherFromLocation = async () => {
        const { app_data } = this.props;
        const { languageValue, temperatureUnitText, selectedOption } = app_data;
        if (selectedOption && selectedOption.length > 0 && selectedOption[0].value && languageValue && temperatureUnitText) {
            await getWeatherForcasteByLocation(selectedOption[0].value, languageValue, temperatureUnitText).then((response) => {
                const data = response.data[selectedOption[0].value];
                this.setState({ weatherData: data })
            })
            await getWeatherSpecificByLocation(selectedOption[0].value).then((response) => {
                const data = response.data[selectedOption[0].value];
                this.setState({ currentData: data })
            })
        }
    }
    render() {
        const { app_data } = this.props;
        const { defaultThemeValue, selectedOption } = app_data;
        const { weatherData, currentData } = this.state;


        return Object.keys(weatherData) && weatherData && weatherData.Headline && weatherData.DailyForecasts && weatherData.DailyForecasts.length > 0 && selectedOption && selectedOption.length > 0 && selectedOption[0].label && currentData && currentData.length > 0 ? <div class="tb_weather_container tb_layout_1">

            <div class={`tb_weather ${defaultThemeValue == "0" ? `tb_light` : ``} tb_layout_portrait`} style={{ height: '100%' }}>
                <div class="tb_weather_wrap">
                    <div class="tb_weather_sec">
                        <div class="tb_weather_title">
                            {selectedOption && selectedOption.length > 0 ? <div class="tb_weather_head">
                                <div class="tb_weather_title_01">{selectedOption[0].label}</div>
                                <div class="tb_weather_date">
                                    <div class="tb_weather_day">Today</div>
                                    <div class="tb_weather_time">{moment(weatherData.DailyForecasts[0].Date).format('LL')}</div>
                                </div>
                            </div> : null}
                            <div class="tb_weather_details">
                                <div class="tb_wather_ico">
                                    <img class="tb_weather_ico_ani" src={`${weatherIcon}${currentData[0].WeatherIcon}.svg`} alt="" />
                                </div>
                                <div class="tb_weather_temp">
                                    <div class="tb_weather_temp_wrap">
                                        {
                                            (weatherData.DailyForecasts[0].Temperature.Maximum.Unit == currentData[0].Temperature.Imperial.Unit) ? <><div>{currentData[0].Temperature.Imperial.Value}</div>
                                                <div class="tb_weather_deg">{currentData[0].Temperature.Imperial.Unit}</div></> : (weatherData.DailyForecasts[0].Temperature.Maximum.Unit == currentData[0].Temperature.Metric.Unit) ? <><div>{currentData[0].Temperature.Metric.Value}</div>
                                                    <div class="tb_weather_deg">{currentData[0].Temperature.Metric.Unit}</div></> : null
                                        }
                                    </div>
                                    <div class="tb_weather_feel">
                                        <div class="tb_weather_realfeel_665">RealFeel
										<div class="tb_weather_day_deg">Ⓗ</div>
                                        </div>
                                        {
                                            (weatherData.DailyForecasts[0].Temperature.Maximum.Unit == currentData[0].RealFeelTemperature.Imperial.Unit) ? <>{currentData[0].RealFeelTemperature.Imperial.Value}<div class="tb_weather_day_deg">{currentData[0].RealFeelTemperature.Imperial.Unit}</div></> : (weatherData.DailyForecasts[0].Temperature.Maximum.Unit == currentData[0].RealFeelTemperature.Metric.Unit) ? <>{currentData[0].RealFeelTemperature.Metric.Value}<div class="tb_weather_day_deg">{currentData[0].RealFeelTemperature.Metric.Unit}</div></> : null
                                        }
                                    </div>
                                </div>

                            </div>
                            <div class="tb_weather_title01">
                                {currentData[0].WeatherText}
                            </div>
                        </div>
                    </div>
                    <div class="tb_weather_sec_02">
                        <div class="tb_weather_byday">
                            {
                                weatherData.DailyForecasts && weatherData.DailyForecasts.length > 0 && weatherData.DailyForecasts.map((item, itemKey) => <div class="tb_weather_00">
                                    <div class="tb_weather_wrapper">
                                        <div class="tb_weather_day01">
                                            <div class="tb_weather_day_title">
                                                {moment(item.Date).format('dddd')}
                                            </div>
                                            <div class="tb_weather_day_title_short">
                                                {moment(item.Date).format('ddd')}
                                            </div>
                                            <div class="tb_weather_day_ico">
                                                <img class="tb_weather_day_ani" src={isDayTime() ? itemKey == 0 ? `${weatherIcon}${currentData[0].WeatherIcon}.svg` : `${weatherIcon}${item.Day.Icon}.svg` : itemKey == 0 ? `${weatherIcon}${currentData[0].WeatherIcon}.svg` : `${weatherIcon}${item.Night.Icon}.svg`} alt="" />
                                            </div>
                                            <div class="tb_weather_day_temp">
                                                {
                                                    (itemKey == 0 && weatherData.DailyForecasts[0].Temperature.Maximum.Unit == currentData[0].Temperature.Imperial.Unit) ? <>{currentData[0].Temperature.Imperial.Value} <div class="tb_weather_day_deg"> {currentData[0].Temperature.Imperial.Unit}</div></> : (itemKey == 0 && weatherData.DailyForecasts[0].Temperature.Maximum.Unit == currentData[0].Temperature.Metric.Unit) ? <>
                                                        {currentData[0].Temperature.Metric.Value} <div class="tb_weather_day_deg"> {currentData[0].Temperature.Metric.Unit}</div></> : <>
                                                            {item.Temperature.Maximum.Value} <div class="tb_weather_day_deg"> {item.Temperature.Maximum.Unit}</div>
                                                        </>
                                                }
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                )

                            }
                        </div>
                    </div>
                </div>
            </div>


        </div>
            : null
    }
}


export default WeatherPreview;