import React from 'react';
import { CloudUrl } from '../../constants';
const urlImage = 'https://cloud.taggbox.com/';


const BannerLogo = ({ LogoImage, image_path }) => {
    //console.log(LogoImage,'LogoImageLogoImage')
    return <img src={(LogoImage.newName  === "logo_bnr-5.svg" || LogoImage.newName  === "logo_bnr-3.svg" || LogoImage.newName  === "logo_bnr-2.svg") ? 
     `${urlImage}react-app/media/banner/${LogoImage.newName}` : LogoImage.newName.includes('logo_bnr') ? LogoImage.newName : LogoImage.newName != '' ? `${urlImage}${LogoImage.path}${LogoImage.newName}` : ''} className="img-responsive" style={{ maxWidth: 240, width: "auto", paddingTop: 5, paddingBottom: 5, objectFit: "contain" }} />
}

export default BannerLogo;