
import axios from 'axios';
import { HOST_URL, IS_LIVE } from './api'
const requestInterceptor = (config) => {
    var headers = {}
    // headers = (IS_LIVE && localStorage.getItem("token")) ? { 'authorization': `Bearer ${localStorage.getItem("token")}` } : { 'authorization': 'Bearer ' + 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6MTUwLCJlbWFpbElkIjoicGljYXNvQG1haWxpbmF0b3IuY29tIiwiaWF0IjoxNjE1OTgxMTk2LCJleHAiOjE2MjM3NTcxOTZ9.z2Xe_r20tPgoSCN9_ZYy5RTOCwNvRdR3jGjSG8BDQIE', }

    return {
        ...config,
        headers: {
            ...config.headers,
            ...headers

        },
    }
};

class HttpClient {
    constructor() {
        const options = { baseURL: HOST_URL };
        const instance = axios.create(options);
        instance.interceptors.request.use(requestInterceptor);
        axios.interceptors.response.use(
            function (response) {
                return response;
            },
            function (error) {
                return Promise.reject(error);
            }
        );
        return instance;
    }
}
export default HttpClient;
