import React, { Component } from "react";
import { CloudUrl } from "../../../displayThemes/constants";

class ContentPlayLoader extends Component {

    componentDidMount() {
        this.fadeOutEffect("overlayLoader", 1000)
    }
    fadeOutEffect = (fadeID, duration) => {
        var fadeTarget = document.getElementById(fadeID);
        var fadeEffect = setInterval(() => {
            if (!fadeTarget.style.opacity) fadeTarget.style.opacity = 1;
            if (fadeTarget.style.opacity > 0) fadeTarget.style.opacity -= 0.1
            else {
                clearInterval(fadeEffect);
            }

        }, duration);
    }

    render() {
        return < div id="overlayLoader" >
            <div class="loaderBox">
                <div class="logo animated fadeInDown">
                    <img src={`${CloudUrl}/images/taggbox_white_logo.png`} alt="" />
                </div>
                <div class="progressDiv animated fadeInUp" style={{
                    '-webkit-animation-delay': '0.5s', animationDelay: '0.5s'
                }}>
                    {/* <div class='progressBlock' id="progress_div" >
                        <div className='bar' id='bar1' style={{ width: '100%' }}></div>
                    </div> */}
                </div>
            </div>
        </div >
    }
}
export default ContentPlayLoader; 