export const getConvertToDate = (date) => {
  if (navigator.userAgent.indexOf("Safari") !== -1) {
    let convertDate = moment(date).format('MM/DD/YYYY hh:mm:ss');
    var NewDate = new Date(convertDate);
    return NewDate;
  }
  else return NewDate;
}
