export const AnnouncementsUtils = (timeStamp, showAt, endAt, every, announcementKey) => {
  let timeIntervalRepeatOnce = null;
  let announcementStartDate = getConvertToDate(showAt)// Your timezone!
  let announcementTimeInterval = getTimeFromDate(announcementStartDate) / 1000.0;
  let announcementEndDate = getConvertToDate(endAt);
  let announcementEndTime = getTimeFromDate(announcementEndDate) / 1000.0;
  if (every != 0) timeIntervalRepeatOnce = (timeStamp - announcementTimeInterval + announcementKey * 10) % (every * 60); //repeat
  else timeIntervalRepeatOnce = (timeStamp - announcementTimeInterval - 23400 + announcementKey * 10);//once
  return { timeIntervalRepeatOnce, announcementTimeInterval, announcementEndTime };
}
