export const BannerSocialIcons = ({ wall }) => {
  // console.log("BannerSocialIcons",wall)
  // return null;

  if (wall.CustomBanner.social_icon_status == "1") {
    if (wall.CustomBanner.social_icons == "2,3") return <>
      <LogoSocialActionForTW wall={wall} />
      <LogoSocialActionForINSTA wall={wall} />
    </>
    else if (wall.CustomBanner.social_icons == "1,3") return <>
      <LogoSocialActionForFB wall={wall} />
      <LogoSocialActionForINSTA wall={wall} />
    </>
    else if (wall.CustomBanner.social_icons == "1,2") return <>
      <LogoSocialActionForFB wall={wall} />
      <LogoSocialActionForTW wall={wall} />
    </>
    else if (wall.CustomBanner.social_icons == "1") return <LogoSocialActionForFB wall={wall} />
    else if (wall.CustomBanner.social_icons == "2") return <LogoSocialActionForTW wall={wall} />
    else if (wall.CustomBanner.social_icons == "3") return <LogoSocialActionForINSTA wall={wall} />
    else if (wall.CustomBanner.social_icons == "1,2,3") return <>
      <LogoSocialActionForFB wall={wall} />
      <LogoSocialActionForTW wall={wall} />
      <LogoSocialActionForINSTA wall={wall} />
    </>
    else return null;
  }
  else return null;
}
const ADD_SCRIPT_ON_WINDOW_LOAD = url => {
  const script = document.createElement("script")
  script.src = url
  script.async = true
  document.body.appendChild(script)
}
export const WINDOW_ON_SCRIPT = () => {
  window.onload = () => {
    ADD_SCRIPT_ON_WINDOW_LOAD("https://widget.taggbox.com/common-assets/js/jquery.min.js")
    ADD_SCRIPT_ON_WINDOW_LOAD("https://widget.taggbox.com/common-assets/js/slackdown.js")
  }
}
