export const themeAutoScrolling = (scrollSpeed, wHeight) => {
  const marquee = document.querySelectorAll('#marqueecontainer');
  marquee.forEach((el) => {
    const container = el.querySelector('#wPosts');
    const content = el.querySelector('#wPosts');
    let elWidth = wHeight;
    const scrollH = content.scrollHeight;
    elWidth = scrollH;
    let progress = 1;
    function loop() {
      progress = progress - scrollSpeed;
      if (progress <= elWidth * -1) {
        progress = 0;
      }
      var i = container.style;
      i.webkitTransform = i.MozTransform = i.msTransform = i.OTransform = i.Transform = "translate(0, " + progress + "px)"
      // container.style.transform = 'translateY(' + (progress) + 'px)';
      // container.style.webkitTransform = 'translateY(' + (progress) + 'px)';
      // container.style.msTransform = 'translateY(' + (progress) + 'px)';
      // container.style.mozTransform = 'translateY(' + (progress) + 'px)';
      // container.style.oTransform = 'translateY(' + (progress) + 'px)';
      window.requestAnimationFrame(loop);
    }
    loop();
  });
}
