export const bytesToSize = (bytes) => {
  var sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
  if (bytes == 0) return '0 Byte';
  var i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)));
  return { size: Math.round(bytes / Math.pow(1024, i), 2), sizeText: sizes[i] }
}

export const loadImage = async src =>
  new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = () => resolve(img);
    img.onerror = (...args) => reject(args);
    img.src = src;
  });

export const getProductName = (productId) => {
  let productNames = []
  //var prodId = productId.split(",");
  if (productId.includes(1)) productNames.push('Free Trial')
  if (productId.includes(2)) productNames.push('Commerce')
  if (productId.includes(3)) productNames.push('UGC')
  if (productId.includes(4)) productNames.push('Display')
  if (productId.includes(6)) productNames.push('Widget')
  if (productId.includes(7)) productNames.push('Taggbox Smart Curation')
  if (productId.includes(8)) productNames.push('Expired')
  return productNames.join(',');
}

export const tb_onsite_btn_disabled = (action) => {
  const tb_onsite_btn_primary = document.getElementById("tb_onsite_btn_id")
  if (action == 1 && tb_onsite_btn_primary) {
    tb_onsite_btn_primary.classList.add("tb_onsite_btn_disabled")
  }
  else if (action == 2 && tb_onsite_btn_primary) {
    tb_onsite_btn_primary.classList.remove("tb_onsite_btn_disabled")
  }
}


export const customWorklength = (newItems, postWidth) => {
  var actualLength = newItems.length
  try {
    let row = newItems.length / parseInt(window.innerWidth / postWidth)
    let actu = parseInt(row) * parseInt(window.innerWidth / postWidth)
    actualLength = actu
  }
  catch (ex) {
    actualLength = newItems.length
  }
  return actualLength;
}
