import React, { memo } from 'react'
import NetworkCardComponent from './../networkCardComponent'
import { AuthorIconIMG, AuthorName, AuthorTimeHandle, AuthorHandle } from './../authorDetails'
import ErrorBoundary from '../../errorBoundary'
const AuthorDetails = ({ item, personalization, font, network, themeRule, signageTheme, backgroundColor }) => {
  return <ErrorBoundary>
    {(personalization.postAuthor === 1 && personalization.postTime === 1) && <div className="authorDetails" style={{ color: font.authorColor }}>

      {personalization.signageTheme === 57 ? <NetworkCardComponent network={network} font={font} themeRule={themeRule} /> : ''}
      <AuthorIconIMG picture={item.author.picture} name={item.author.name} color={font.fontColor} backgroundColor={backgroundColor} {...item.author} />
      <AuthorName styleCss={{ color: font.authorColor }} name={item.author.name === null ? '' : item.author.name}  {...item.author} />

      <div className="authrHandleTime">
        <AuthorHandle authrHandleStyle={{ color: font.authorColor }} username={item.author.username} color={font} {...item.author} />
        {/* seprateDotStyle={{ color: font.authorColor, paddingLeft: 2, paddingRight: 2 }} timePostStyle={{ color: font.authorColor }} */}
        <AuthorTimeHandle seprateDotStyle={{ color: font.authorColor, paddingLeft: 2, paddingRight: 2 }} timePostStyle={{ color: font.authorColor }} personalization={personalization} item={item} color={font} {...item.author} />
      </div>
    </div>

    }


    {

      (personalization.postAuthor === 1 && personalization.postTime !== 1) && <div className="authorDetails" border-radius="50%" style={{ color: font.authorColor }}>
        {personalization.signageTheme === 57 ? <NetworkCardComponent network={network} font={font} themeRule={themeRule} /> : ''}
        <AuthorIconIMG color={font.fontColor} backgroundColor={backgroundColor} picture={item.author.picture} name={item.author.name} {...item.author} />
        <AuthorName styleCss={{ color: font.authorColor }} name={item.author.name} {...item.author} />
        <div className="authrHandleTime">
          <AuthorHandle authrHandleStyle={{ color: font.authorColor }} username={item.author.username} color={font}  {...item.author} />
        </div>
      </div>

    }


    {

      (personalization.postAuthor !== 1 && personalization.postTime === 1) && <div className="authrHandleTime" style={{ color: font.authorColor }}>
        {personalization.signageTheme === 57 ? <NetworkCardComponent network={network} font={font} themeRule={themeRule} /> : ''}
        <div className="authrHandleTime">
          <AuthorTimeHandle seprateDotStyle={{ color: font.authorColor, paddingLeft: 2, paddingRight: 2 }} personalization={personalization} timePostStyle={{ color: font.authorColor }} item={item} color={font} {...item.author} />
        </div>
      </div>

    }


    {

      (personalization.postAuthor !== 1 && personalization.postTime !== 1) && <>
        {(personalization.signageTheme === 57) && <NetworkCardComponent network={network} font={font} themeRule={themeRule} />}
      </>

    }

  </ErrorBoundary>
}
export default memo(AuthorDetails)