import React, { Component } from "react"
import FontFamilyForPost from '../../sepcomponents/FontFamilyForPost'
import ErrorBoundary from '../../../errorBoundary'
import AuthorDetails from '../../sepcomponents/AuthorDetails'
import { CloudUrl } from "../../../constants";
class CardItem extends Component {
    render() {
        const { networkd, item, newApi, personalization, itemKey, postSize } = this.props;
        var textPost = false;
        var postImageWidth = 300;
        var postType = '';
        var postThemeClass = '';
        var file = "";
        if (item.type == 1) {
            file = item.author.picture;
            textPost = true;
            postImageWidth = 150;
            postThemeClass = 'tweetrPostSM';
        }
        else {
            if (item.carausel == 0 && (networkd.id == 2 || networkd.id == 18)) {

            }
            file = item.postFileNew;
        }
        var arr = [3, 5];
        if (arr.includes(item.type)) {
            postType = 'video';
        }
        if (!item.embed) {
            postType = '';
        }
        return <ErrorBoundary> <div key={`post_${item.id}`} data-id={item.id} id={`postId${item.id}`} data-tweet={postType} className={`tweet feedId${item.feedId} postItem item flatCard col-ms-6 col-xs-12 ${item.type == 1 ? `onlyTextCard` : ``}`} data-post-id={item.id} data-highlight={item.highlight} data-pin={item.pin} data-created={item.createdAt} style={{ height: postSize, width: postSize }}>
            {file ? <div className="thumb" key={`thumb_post_${item.id}`} ><img className="lazy" data-original={file} /></div> : <div className="thumb"><img className="lazy" data-original="img/blank.jpg" src="img/blank.jpg" /></div>
            }

            <div key={`selected-item${item.id}`} className={`selected-item ${postThemeClass}`} >
                <div class="tweet-post-img" >
                    {file ? <img data-original={file} alt="" className="image lazy" width={postImageWidth} height={postImageWidth} /> : <img data-original="img/blank.jpg" alt="" className="image lazy" width={postImageWidth} height={postImageWidth} />
                    }
                </div>

                {(postType == 'video') ? <>{(item.embed) ? <div class="tweet-post-img tweet-post-video" style={{ backgroundImage: `url(${item.postFileNew})` }}>
                </div> : <div className="tweet-post-img tweet-post-video" style={{ backgroundImage: `url(img/blank.jpg)` }}>
                    </div>
                }
                </> : null}

                <div class="content-outer">
                    <div class="content-inner" style={{ backgroundColor: newApi.wall.ThemeRule.cardColor }}>
                        <div class="header">

                            <div class="postNetwork top" data-network={networkd.name} data-network-color={networkd.color}>
                                <i style={{ color: newApi.wall.ThemeRule.iconType == 1 ? newApi.wall.ThemeRule.iconColor : '' }} class={networkd.id == 20 ? `tb_icon fa ${networkd.icon}` : `fa ${networkd.icon}`}></i>
                            </div>
                            <h4><AuthorDetails backgroundColor={networkd.color} item={item} newApi={newApi} personalization={newApi.wall.Personalization} font={newApi.wall.ThemeRule} network={networkd} /></h4>

                        </div>
                        {(item.rating && item.rating != 0) ? <div className="rating">
                            <img src={`${CloudUrl}/rating/${networkd.id}/${item.rating}.png`} />
                        </div> : null}
                        <div class="message-text">
                            <FontFamilyForPost item={item} newApi={newApi} personalization={newApi.wall.Personalization} themeRule={newApi.wall.ThemeRule} network={networkd} signageTheme={newApi.wall.Personalization.signageTheme} ownerID={newApi.wall.Wall.owner} />
                        </div>
                    </div>
                </div>
                <div class="clear"></div>
            </div>
        </div>
        </ErrorBoundary>

    }

}
export default CardItem