import React, { PureComponent, Fragment, Suspense, memo } from "react"
import moment from 'moment'
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, findNetworkDataFromWebFilters } from '../../../../utils'
import SocialNetwork from '../../../common/SocialNetwork'
import VideoMedia from '../../../common/VideoMedia'
import FontFamilyForPostSlackTheme from './../../../sepcomponents/FontFamilyForPostSlackTheme'
import AuthorAvatar from "../../../sepcomponents/AuthorAvatar"

export default class RightChatCard extends React.Component {
    state = {
        isAnimate: false,
        isAuthorImagevalid: true

    }
    componentDidMount() {
        const { isAnimate } = this.props;
        this.setState({ isAnimate }, () => this.onIsAnimation())
    }
    onIsAnimation = () => {
        setTimeout(() => this.setState({ isAnimate: false }, () => this.onHeightSet()), 700)
    }
    onHeightSet = () => {
        setTimeout(() => this.props.onScrollToBottom(), 500)
    }
    render() {
        const { item, newApi, webFilters } = this.props;
        const { isAnimate } = this.state;
        const netwrokd = findNetworkDataFromWebFilters(item.networkId, webFilters);
        return (
            <div id={`postId-${item.id}`} post-id={item.id} className={`tb_con_wrap tb_chat_box_right ${isAnimate ? `` : this.props.rightAnimation}`}>
                <div className="tb_con_author">
                    {newApi.Personalization && newApi.Personalization.postAuthor == 1 ?

                        <div className="tb_author_profile">
                            <>
                                {this.state.isAuthorImagevalid && < img className="img-fluid" style={{ display: ("" === 0 || item.author.isInstaUser) ? 'block' : 'block' }} src={item.author.picture}
                                    onError={(e) => {
                                        this.setState({
                                            isAuthorImagevalid: false
                                        });
                                        // console.clear()
                                    }}
                                    alt="" />}

                                {!this.state.isAuthorImagevalid && <AuthorAvatar style={{ display: (newApi.Personalization.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'flex' }} username={item.author.name} color={newApi.ThemeRule.fontColor} backgroundColor={netwrokd.color} />}

                            </>
                        </div> : <div className="tb_author_profile" style={{ background: `url(${String(item.author.picture).replace(/\s/g, "")})` }}></div>}
                </div>

                <div className="tb_con_post_content">
                    <div className="tb_con_post_content00" style={{ backgroundColor: newApi.ThemeRule.cardColor }}>
                        <div className="tb_con_post_cont01" style={item.type != 1 && newApi.ThemeRule.hideContent == 1 ? { minWidth: 400 } : {}}>
                            <div className="tb_con_post_cont001">
                                {((item.content || item.contentTitle)) ? <div className={`tb_con_post_cont002 sGFfonte-${newApi.ThemeRule.font_varient}`} style={{
                                    fontFamily: newApi.ThemeRule.css_font,
                                    color: (newApi.ThemeRule.cardColor === "#000000" && newApi.ThemeRule.fontColor === "#000000") ? "#ffffff"
                                        : newApi.ThemeRule.cardColor === "#ffffff" && newApi.ThemeRule.fontColor == "#ffffff" ? "#000000"
                                            : newApi.ThemeRule.fontColor,
                                    fontSize: item.fontSize
                                }}>
                                    {(item.contentTitle ? <div className="tb_con_title">{item.contentTitle}</div> : '')}

                                    <FontFamilyForPostSlackTheme item={item} newApi={newApi} personalization={newApi.Personalization} themeRule={newApi.ThemeRule} network={netwrokd} signageTheme={newApi.Personalization.signageTheme} ownerID={newApi.Wall.owner} />
                                </div> : ''
                                }
                                <div className="tb_con_post_media">
                                    <VideoMedia item={item} newApi={newApi} />


                                    {/* <VideoMedia className="tb_con_vid_media"
                                        mediaUrl={item.postFileNew}
                                        autoPlay={true}
                                        muted={true}
                                        loop={1}
                                        dataType="video"
                                        dataNetwork={item.networkId}
                                        dataLink={item.link}
                                        dataLoad={0} /> */}
                                </div>

                            </div>

                            <div className="tb_con_post_social">
                                <SocialNetwork networkID={item.networkId} iconColor={newApi.ThemeRule} />
                            </div>
                        </div>
                        <div className="animate-typing-col">
                            <div className="tb_dot" style={{ backgroundColor: newApi.ThemeRule.fontColor }}></div>
                            <div className="tb_dot" style={{ backgroundColor: newApi.ThemeRule.fontColor }}></div>
                            <div className="tb_dot" style={{ backgroundColor: newApi.ThemeRule.fontColor }}></div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

}
