import React, { PureComponent } from 'react';
import "../../comoncss/custom-modal.css"
import "../../comoncss/ugc-pro-modal.css";
import { CUSTOM_S_PATH } from '../../constants'
import { setIntervalFunction, AnnouncementsUtils } from '../../utils'


const styles = {
    wAnnouncementsOverLay: { position: "fixed", zIndex: 99999, top: 0, right: 0, bottom: 0, left: 0 },
    blurBg: { position: "absolute" },
    backgroundWhite: { backgroundColor: "white" },
    setFont: { fontSize: 24, color: "black" }
}

export default class AnnouncementsOverlayView extends PureComponent {


    state = {
        announcementData: null,
        announcementDataKeys: null,
        timeStamp: Math.floor(Date.now() / 1000),
        imageError: false
    }

    componentDidMount() {
        this.onLoadAnnouncements()
    }
    timeInterval = null
    onLoadAnnouncements = () => {
        const { newApi } = this.props;
        let announcementData = newApi.announcements;
        let announcementDataKeys = Object.keys(announcementData).map(itemKey => announcementData[itemKey])
        this.setState({ announcementData, announcementDataKeys, timeStamp: Math.floor(Date.now() / 1000), imageError: false }, () => this.announcementTimeIntervalData())
    }

    announcementTimeIntervalData = () => {
        if (this.timeInterval) {
            clearInterval(this.timeInterval)
        }
        const { announcementDataKeys, announcementData } = this.state;
        if (announcementDataKeys != null && announcementData != null) this.timeInterval = setIntervalFunction(() => this.onLoadAnnouncements(), 10000)
    }


    render() {
        const { announcementDataKeys, timeStamp, imageError } = this.state;
        return (
            <React.Fragment>{
                announcementDataKeys !== null ? announcementDataKeys.map((p, announcementKey) => {
                    const { timeIntervalRepeatOnce, announcementTimeInterval, announcementEndTime } = AnnouncementsUtils(timeStamp, p.showAt, p.endAt, p.every, announcementKey);
                    if (((timeIntervalRepeatOnce === 0) || (timeIntervalRepeatOnce < 10 && timeIntervalRepeatOnce >= 0)) && (timeStamp >= announcementTimeInterval)) {
                        return (
                            <div key={announcementKey} id="wAnnouncementsOverLay" style={{ display: isNaN(announcementEndTime) ? '' : timeStamp <= announcementEndTime ? '' : 'none', ...styles.wAnnouncementsOverLay }}>
                                <div className="blur-bg" style={styles.blurBg}></div>
                                <div className="wAnnouncementsOverLay" data-color="#000000" data-font="font26" data-fontsize="24px" data-cardcolor="#ffffff">
                                    <div className="cstmModalPopup taggModalShow anouncementModal disModalPopup modal-text-image fade in">
                                        <div className="taggModal" style={window.innerWidth > 768 ? { left: 0, right: 0, top: 0, margin: 0 } : {}}>
                                            <div className="taggModalContent">
                                                {!imageError && p.mediaFile != "" ? <div className={`taggImageHolder centerAlign ${window.innerWidth > 768 ? `instaMainImg` : ``}`} style={p.type === 2 ? { width: `100%` } : {}}>
                                                    <img src={`${CUSTOM_S_PATH}${p.mediaFile}`} onError={() => this.setState({ imageError: true })} className="img-responsive" alt="" />
                                                </div> : null}
                                                {(p.type === 1 || p.type === 4) ? <div className={`taggContentHolder social-icon-pos-bottom ${!imageError && p.mediaFile != "" ? '' : 'w-100'}`} style={{ ...styles.backgroundWhite, position: (p.type === 2 || p.type === 4) ? 'absolute' : 'inherit' }}>
                                                    <div className="setFont" style={styles.setFont}>{p.description}</div>
                                                </div> : null}
                                            </div>
                                        </div>

                                    </div>
                                </div>

                            </div>
                        )

                    }
                    else return null;
                }) : null}
            </React.Fragment>
        )
    }
}