import React, { PureComponent } from "react";
import Marquee from 'react-double-marquee';

export default class TopTicker extends PureComponent {
  render() {
    const { CustomTicker, newApi, UgcSettings } = this.props;
    const ugcStyle = (UgcSettings && UgcSettings.onsite_status) && UgcSettings.onsite_overlay_poisition == "1" ? { marginTop: `${UgcSettings.onsite_overlay_height}px` } : {}
    return (
      <div id="wtickerTop" className="headerTicker tickerTop" style={{ display: CustomTicker.status === 1 && CustomTicker.ticker_position === "1" ? '' : "none", ...ugcStyle }}>
        <div className={`tickerTitleView ${CustomTicker.tickerType === 3 ? 'ticker3titleView' : ''}`} style={{ display: CustomTicker.tickerType == 2 ? "none" : "flex", backgroundColor: CustomTicker.title_backgroud_color, height: CustomTicker.tickerType == 1 ? CustomTicker.ticker_height : (CustomTicker.ticker_height - 10) < 0 ? CustomTicker.ticker_height : (CustomTicker.ticker_height - 10) }}>
          <h1 className={`setFont sGFfonte-${CustomTicker.title_font_varient}`} style={{ fontFamily: CustomTicker.title_css_font, fontWeight: 500, fontSize: (CustomTicker.ticker_height < 40 && CustomTicker.title_font_size > 24 ) ? 20 : CustomTicker.title_font_size, color: CustomTicker.title_font_color }}>{CustomTicker.title}</h1>
        </div>

        <div className="tickerSubtitleView" style={{ backgroundColor: CustomTicker.subTitle_backgroud_color, height: CustomTicker.ticker_height, display: CustomTicker.status === 1 ? '' : "none" }}>
          <marquee scrollamount={CustomTicker.ticker_speed === 5 ? 5 : CustomTicker.ticker_speed === 10 ? 10 : 20}>
            <h2 className={`setFont sGFfonte-${CustomTicker.subtitle_font_varient}`} style={{ fontFamily: CustomTicker.subtitle_css_font, fontSize: (CustomTicker.ticker_height < 40 && CustomTicker.subTitle_font_size> 24 ) ? 20 : CustomTicker.subTitle_font_size, color: CustomTicker.subTitle_font_color }}>
              {CustomTicker.subTitle}
            </h2>
          </marquee>



          {/* <Marquee speed={CustomTicker.ticker_speed === 5 ? 5 : CustomTicker.ticker_speed === 10 ? 10 : 20}>
            <h2 className={`setFont`} style={{ fontSize: CustomTicker.subTitle_font_size, color: CustomTicker.subTitle_font_color }}>
              {CustomTicker.subTitle}
            </h2>
          </Marquee> */}
        </div>
      </div>
    )
  }
}