
import React, { memo, Component } from 'react'
import Iframe from 'react-iframe';
import reactImageSize from 'react-image-size';
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP } from '../../utils'
import ErrorBoundary from '../../errorBoundary'
import { CloudUrl } from '../../constants';

const loadImage = async src =>
    new Promise((resolve, reject) => {
        const img = new Image();
        img.onload = () => resolve(img);
        img.onerror = (...args) => reject(args);
        img.src = src;
    });
class ItemRender extends Component {
    constructor(props) {
        super(props);
        this.onLoad(props)
    }
    state = {
        newImage: "",
        height: '100',
        width: '100',
        error: ''
    }
    componentWillMount() {
        this.onLoad(this.props)
    }
    onLoad = async (props) => {
        const { item, newApi } = props;
        this.setState({ newImage: item.postFileNew })
        if (item && Object.keys(item).length > 0 && [2, 3, 4, 5].includes(item.type)) {
            if (item.mediaHeight != "" && item.mediaWidth != "") {
                this.setState({
                    height: (parseInt(item.mediaHeight) * 100) / parseInt(item.mediaWidth),
                    width: (parseInt(item.mediaWidth) * 100) / parseInt(item.mediaHeight)
                })
            }
            else {
                if (item.type && item.type == 5) {
                    const videoThumb = item.mediaUrl.includes("onsiteupload/video") ? '' : item.filename

                    reactImageSize(videoThumb).then(({ width, height }) => this.setState({
                        height: (height * 100) / width,
                        width: (width * 100) / height
                    })).catch((errorMessage) => this.setState({
                        height: 100,
                        width: 100,
                        error: errorMessage
                    }))
                }
                else if (item.type && [2, 3, 4, 5].includes(item.type)) {
                    const image = await loadImage(item.postFileNew);
                    this.setState({
                        height: (image.height * 100) / image.width,
                        width: (image.width * 100) / image.height
                    })
                }
            }
        }
    }
    componentDidMount() {

    }
    updateNewImage = e => {
        const { item } = this.props;
        this.setState({ newImage: e.target.src })
    }
    render() {
        const { network, item, itemKey, signageTheme, newApi } = this.props;
        const { newImage, height } = this.state;
        const videoStyle = {
            paddingBottom: height + '%', width: '100%'
        }
        // console.log("network, item, itemKey, signageTheme, newApi", network, item, itemKey, signageTheme, newApi);
        return <ErrorBoundary>
            {
                ((item.type === 2) || (item.type === 4)) ?
                <div className={`image media_holder${item.id}`} key={`img${itemKey}`} style={videoStyle} >
                        <div key={`bg${itemKey}`} className="blur-bg" style={{ backgroundImage: 'url(' + newImage + ')', display: signageTheme === 57 ? '' : 'none' }} > </div>
                        <img
                            data-src={item.postFileNew}
                            src={item.optmizedImg} data-is-optmized="0"
                            onLoad={this.updateNewImage}
                            onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)}
                            data-filter-id={item.filterId}
                            data-link={item.link} data-load="0" data-network={network.id} data-postid={item.id} data-wall-id={newApi.wall.Wall.id} data-owner-id={newApi.wall.Wall.owner} data-item-id={item.id}
                            data-stories={item.stories}
                            data-tb={newApi.wall.UserDetail.db_table}
                            alt="" className="img-responsive cropMeImg networkimage88009" />
                    </div> : ((item.type === 3) || (item.type === 5)) ? <div className="image" style={item.networkId === 7?{}:videoStyle} key={`video${itemKey}`} >

                        {
                            (item.link && (item.link.indexOf("vimeo") >= 0)) ?
                                <>
                                    <div className="videopost-img">
                                        <img key={`bg${itemKey}`} data-src={item.postFileNew} src={item.optmizedImg} data-is-optmized="0" className='imagelazy img-responsive img-hover-zoom--brightness' alt="" effect="blur"
                                            width="100%"
                                            onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)}
                                            data-link={item.link} data-load="0" data-network={network.id}
                                            data-tb={newApi.wall.UserDetail.db_table}
                                            data-filter-id={item.filterId} data-postid={item.id} data-wall-id={newApi.wall.Wall.id} data-item-id={item.id}
                                            data-owner-id={newApi.wall.Wall.owner}
                                        />
                                    </div>
                                    <div className="videoIcon"> < img src={item.networkId === 7 ? `${CloudUrl}/images/youtube.png` : `${CloudUrl}/images/play.png`}
                                        alt="play" /> </div>
                                </> :

                                (item.link && ((item.networkId && item.networkId == 30) && item.link.indexOf("vk") >= 0) || (item.networkId ==7 && (item.link.indexOf("youtube") >= 0 || item.link.indexOf("youtu.be") >= 0))) ?
                                    <Iframe
                                        className="tb-detail-image-iframe"
                                        frameBorder="0"
                                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                        src={`${item.mediaUrl}&autoplay=1&mute=1`}
                                        allowFullScreen="true"
                                        style={{ width: "100%!important" }}
                                        data-filter-id={item.filterId}
                                        data-stories={item.stories}
                                        data-type="video"
                                        data-network={item.networkId}
                                        data-link={item.link}
                                        data-item-id={item.id}
                                        data-load={0}
                                        height="1000"
                                        width="1000"
                                    ></Iframe> : <div className="video-auto__" style={videoStyle}>
                                        <video
                                            poster={item.filename}
                                            src={`${item.mediaUrl}`}
                                            autoPlay={true}
                                            muted={true}
                                            loop={1}
                                            onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e) }}
                                            data-type="video"
                                            data-network={item.networkId}
                                            data-filter-id={item.filterId}
                                            data-link={item.link}
                                            data-load={0}
                                            data-tb={newApi.wall.UserDetail.db_table}
                                            data-wall-id={newApi.wall.Wall.id} data-item-id={item.id}
                                            data-owner-id={newApi.wall.Wall.owner}
                                            height={'400'}
                                            width={'400'}
                                        ></video></div>
                        }

                        {
                            item.link && (item.link.indexOf("soundcloud") >= 0) && <></>

                        }

                        {
                            item.link && (item.link.indexOf("facebook") >= 0) && <></>
                        }

                    </div>
                        : null

                // (item.link.indexOf("linkedin") >= 0 || item.link.indexOf("twitter") >= 0 || item.link.indexOf("flickr") >= 0 || item.link.indexOf("tumblr") >= 0 || item.link.indexOf("instagram") >= 0) && <div className="video-auto__"><video
                //     src={`${item.mediaUrl}`}
                //     autoPlay={true}
                //     muted={true}
                //     loop={1}
                //     onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e) }}
                //     data-type="video"
                //     data-network={item.networkId}
                //     data-filter-id={item.filterId}
                //     data-link={item.link}
                //     data-load={0}
                //     data-wall-id={newApi.wall.Wall.id} data-item-id={item.id}
                // ></video></div>


            }
        </ErrorBoundary>
    }
}

export default memo(ItemRender);