import React, { Component } from "react";
import moment from 'moment'
import 'moment-timezone';
import FullCalendar, { formatDate } from '@fullcalendar/react'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import listPlugin from '@fullcalendar/list'
import interactionPlugin from '@fullcalendar/interaction'
//import './week.css'
class WeekCalendar extends Component {
    state = {
        weekendsVisible: true, upcomingEve: [], allEvents: []
    }

    render() {
        const { upcomingEve, allEvents, upcomingEvents } = this.props;
        return (
            <div className='cldtd_calendar_card_Week'>
                <div className='cldtd_calendar_card_main_Week'>
                    <FullCalendar
                        plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
                        headerToolbar={{
                            left: '',
                            center: 'title',
                            right: ''
                        }}
                        initialView='dayGridWeek'
                        displayEventTime={true}
                        events={allEvents}
                        eventColor="#D1A273"
                        //  eventDisplay='background'
                        timeFormat='H'
                        dayMaxEventRows={2}
                        slotDuration='01:00'
                    />
                </div>
                {this.renderSidebar()}
            </div>
        )
    }
    renderSidebar() {
        return (
            <div className='cldtd_calendar_card_sidebar_Week'>
                <div className='cldtd_calendar_card_sidebar_section'>
                    <div className="cldtd_calendar_card_sidebar_date_week">Upcoming Events</div>
                    <div className="cldtd_calendar_card_eventSection">
                        <FullCalendar
                            plugins={[listPlugin]}
                            headerToolbar={{
                                left: '',
                                center: '',
                                right: ''
                            }}
                            initialView='listWeek'
                            timeZone='local'
                            dayMaxEventRows={2}
                            events={this.props.upcomingEvents}
                        />
                    </div>

                </div>
            </div>
        )
    }
}


export default WeekCalendar

