import React, { Component } from 'react'
import ReactStars from "react-rating-stars-component";
class OnSitePost extends Component {

    render() {
        const { isEdit, upload_image, onClickToRemove, onClickToEdit } = this.props;
        //console.log("upload_imageupload_imageupload_image",upload_image)
        return <>
            {
                
                upload_image && upload_image.map((viewItem, viewIndex) => <>
                    <div className='fill-post-box'>
                        <div className='fill-post-info'>
                            {viewItem.thumb_file ? <div className='img-thumb'>
                                <img src={viewItem.thumb_file} alt="Thumbnail" />
                            </div> : null}
                            <div className='post-details'>
                                <p> {
                                    viewItem.caption && viewItem.caption.length > 0 ? viewItem.caption : viewItem.review && viewItem.review.length > 0 ? viewItem.review : null
                                }</p>
                                {(viewItem.caption == "" && viewItem.rating) || (viewItem.review && viewItem.review.length > 0) ? <ReactStars
                                    edit={false}
                                    count={5}
                                    size={22}
                                    value={viewItem.rating} /> : null}
                            </div>
                        </div>
                        <div className='post_edit'>
                            <button className='pencil' onClick={onClickToEdit(viewIndex)}>
                                <svg width="70" height="60" version="1.1" viewBox="180 60 350 450" xmlns="http://www.w3.org/2000/svg" fill="#AFAFAF">
                                    <g>
                                        <path d="m569.91 153.43-38.137 38.133c-2.1875 2.1914-5.1562 3.418-8.25 3.418s-6.0625-1.2266-8.25-3.418l-76.832-76.832c-2.1914-2.1875-3.418-5.1562-3.418-8.25s1.2266-6.0625 3.418-8.25l38.133-38.133c2.1875-2.1875 5.1562-3.418 8.25-3.418s6.0625 1.2305 8.25 3.418l76.832 76.832h0.003906c2.1875 2.1875 3.4141 5.1562 3.4141 8.25s-1.2266 6.0625-3.4141 8.25zm-439.28 256.52-6.9844 83.82c-0.28516 3.418 0.94922 6.7891 3.3789 9.2148 2.4258 2.4297 5.7969 3.6641 9.2148 3.3789l83.82-6.9844c2.75-0.23047 5.332-1.4258 7.2812-3.3789l263.19-263.18c2.1875-2.1875 3.4141-5.1562 3.4141-8.25s-1.2266-6.0625-3.4141-8.25l-76.836-76.836c-2.1875-2.1875-5.1562-3.4141-8.25-3.4141s-6.0625 1.2266-8.25 3.4141l-263.18 263.19c-1.9531 1.9492-3.1484 4.5312-3.3789 7.2812z" />
                                    </g>
                                </svg>
                            </button>
                            <button onClick={onClickToRemove(viewIndex)}>
                                <svg width="60" height="60" version="1.1" viewBox="180 60 350 450" xmlns="http://www.w3.org/2000/svg" fill='#AFAFAF'>
                                    <g>
                                        <path d="m423.45 123.37c0.75 3 3.8359 5.4336 6.918 5.4336h59.637c10.824 0 19.602 8.7734 19.602 19.598 0 8.9414-5.9844 16.48-14.164 18.836l-10.234 281.48c-0.83594 22.922-19.879 41.285-42.836 41.285h-184.73c-22.922 0-42.004-18.406-42.836-41.285l-10.234-281.48c-8.1797-2.3555-14.164-9.8945-14.164-18.836 0-10.824 8.7773-19.598 19.602-19.598h59.637c3.0703 0 6.1758-2.4609 6.918-5.4336l4.1953-16.777c5.1836-20.73 25.469-36.59 46.828-36.59h44.844c21.359 0 41.645 15.852 46.828 36.59zm-87.445 95.031v190.4c0 7.7305 6.2695 14 14 14s14-6.2695 14-14v-190.4c0-7.7305-6.2695-14-14-14s-14 6.2695-14 14zm-64.395 0.41406 5.6016 190.4c0.22656 7.7305 6.6758 13.809 14.406 13.582 7.7266-0.22656 13.809-6.6758 13.582-14.406l-5.6016-190.4c-0.22656-7.7266-6.6758-13.809-14.406-13.582-7.7266 0.22656-13.809 6.6758-13.582 14.406zm128.8-0.82422-5.6016 190.4c-0.22656 7.7305 5.8555 14.18 13.582 14.406 7.7305 0.22656 14.18-5.8516 14.406-13.582l5.6016-190.4c0.22656-7.7305-5.8555-14.18-13.582-14.406-7.7305-0.22656-14.18 5.8555-14.406 13.582zm-82.02-89.188h63.227c1.5312 0 2.4805-1.207 2.1094-2.6953l-2.5-10.008c-0.82422-3.2969-5.4336-6.8984-8.8008-6.8984h-44.844c-3.3594 0-7.9766 3.6094-8.8008 6.8984l-2.5 10.008c-0.38281 1.5234 0.57031 2.6953 2.1094 2.6953z" />
                                    </g>
                                </svg>
                            </button>
                        </div>
                    </div>
                </>)
            }
        </>
    }
}


export default OnSitePost;