import React, { Component } from 'react';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
const reactStringReplace = require("react-string-replace");


const convertCss = (hashtag_color, match, isHash) => {
    let style = `<span className="gridhashtag" style="color:${hashtag_color};font-weight:bold;">${isHash ? `#` : ``}${match}</span>`
    return style;
}
class FallingColumnForPost extends Component {
    unicodeyEmojiConvert = (data) => {

        return <>{ReactHtmlParser(data)}</>

    }
    textEmojiConvert = (data) => {
        return <>{data}</>

    }
    componentDidUpdate() {
        // window.dispatchEvent(new Event('resize'))
    }
    render() {

        const { item, wall, network } = this.props;
        var contentData = item.content;
        const slackData = item.content;
        return network.id === 20 && window.slackdown !==undefined ?
            <div> {
                wall.Personalization.hashtag_highlight !== 0 ?
                    wall.Personalization.hashtag_all === 1 ? this.unicodeyEmojiConvert(window.slackdown !==undefined ? window.slackdown.parse(reactStringReplace(slackData, /#(\w+)/g, (match, i) => convertCss(wall.Personalization.hashtag_color, match, true)), item.slackMember): '')
                        : wall.Personalization.hashtag_feed === 1 && item.hash.hashString != "" ? this.unicodeyEmojiConvert(window.slackdown.parse(reactStringReplace(slackData, item.hash.hashString, (match, i) => convertCss(wall.Personalization.hashtag_color, match, false)
                        ), item.slackMember)) : this.unicodeyEmojiConvert(window.slackdown.parse(slackData, item.slackMember))
                    : this.unicodeyEmojiConvert(window.slackdown.parse(slackData, item.slackMember))
            }</div>
            : <div>{
                wall.Personalization.hashtag_highlight !== 0 ?
                    wall.Personalization.hashtag_all === 1 ? this.textEmojiConvert(reactStringReplace(ReactHtmlParser(contentData), /#(\w+)/g, (match, i) => (
                        <span key={match + i} style={{ color: wall.Personalization.hashtag_color, fontWeight: "bold" }}>#{match}</span>
                    )))
                        : wall.Personalization.hashtag_feed === 1 && item.hash.hashString != "" ? this.textEmojiConvert(reactStringReplace(ReactHtmlParser(contentData), item.hash.hashString, (match, i) => (
                            <span key={match + i} style={{ color: wall.Personalization.hashtag_color, fontWeight: "bold" }}>{match}</span>
                        ))) : this.textEmojiConvert(ReactHtmlParser(contentData))
                    : this.textEmojiConvert(ReactHtmlParser(contentData))
            }</div>


    }
}
export default FallingColumnForPost;