
import React, { memo, Component } from 'react'
import ReactPlayer from 'react-player'
import { CloudUrl, ERROR_IMG, INSTAGRAM_IMAGE_BLANK, NO_CAMERA_IMAGE } from '../../constants'
import Iframe from 'react-iframe';
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP } from '../../utils'
import ErrorBoundary from '../../errorBoundary'

class AtmosItemRender extends Component {
    state = {
        newImage: ""
    }
    componentDidMount() {
        const { item } = this.props;
        this.setState({ newImage: item.postFileNew })
    }
    updateNewImage = e => {
        this.setState({ newImage: e.target.src })
    }

    render() {
        const { network, item, itemKey, signageTheme, newApi } = this.props;
        const { newImage } = this.state
        return <ErrorBoundary>{

            ((item.type === 2) || (item.type === 4)) ? <>
                <div className="tb_01_post_img_wrapper_001_blur_bg"
                    style={{ backgroundImage: 'url(' + newImage + ')' }} > </div>
                <div className="tb_01_post_img_wrapper_001"
                    style={{ backgroundImage: 'url(' + newImage + ')' }} > </div>


                <div className="tb_01_post_img_in_002">
                    <img src={newImage}
                        data-link={item.link} data-load="0" data-network={network.id}
                        data-postid={item.id} data-wall-id={newApi.wall.Wall.id} data-owner-id={newApi.wall.Wall.owner} data-item-id={item.id}
                        data-tb={newApi.wall.UserDetail.db_table}
                        onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)}
                        data-filter-id={item.filterId}
                        data-stories={item.stories}
                        onLoad={this.updateNewImage}
                        alt="" className="tb_01_no_post_001" />
                </div>
            </>
                : (item.type === 1) ? null
                    : ((item.type === 3) || (item.type === 5)) ? <div className="tb_01_post_vid_001__" key={`video${itemKey}`} >
                        <div className="tb_01_post_vid_001__"
                            style={{ backgroundImage: 'url(' + newImage + ')' }} >
                            <img className="tb_01_post_img_in_002_blur_bg" src={newImage}
                                data-link={item.link} data-load="0" data-network={network.id}
                                data-postid={item.id} data-wall-id={newApi.wall.Wall.id} data-owner-id={newApi.wall.Wall.owner} data-item-id={item.id}
                                data-tb={newApi.wall.UserDetail.db_table}
                                data-filter-id={item.filterId}
                                onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)}
                                data-stories={item.stories}
                                onLoad={this.updateNewImage}
                                alt="" />

                            {
                                item.link && (item.link.indexOf("youtube") >= 0 || item.link.indexOf("youtu.be") >= 0 || item.link.indexOf("vimeo") >= 0) &&
                                <>

                                    <div className="tb_01_vid_play_icon__">
                                        <img className="tb_01_play_icon001" src={item.networkId === 7 ? `${CloudUrl}/images/youtube-play.svg` : `${CloudUrl}/images/play.svg`}
                                            alt="icon" />
                                    </div>
                                </>
                            }
                            {
                                item.link && ((item.networkId && item.networkId == 30) && item.link.indexOf("vk") >= 0) &&
                                <Iframe
                                    className="tb-detail-image-iframe"
                                    frameBorder="0"
                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                    src={`${item.mediaUrl}&autoplay=1&mute=1`}
                                    allowFullScreen="true"
                                    style={{ width: "100%!important" }}
                                    data-filter-id={item.filterId}
                                    data-stories={item.stories}
                                    data-type="video"
                                    data-network={item.networkId}
                                    data-link={item.link}
                                    data-item-id={item.id}
                                    data-load={0}
                                ></Iframe>

                            }

                            {
                                item.link && (item.link.indexOf("soundcloud") >= 0) &&
                                <>

                                </>
                            }


                            {
                                item.link && (item.link.indexOf("facebook") >= 0) &&
                                <>

                                </>

                            }

                            {
                                item.link && (item.mediaUrl.indexOf("onsiteupload/video") >= 0 || item.link.indexOf("linkedin") >= 0 || item.link.indexOf("twitter") >= 0 || item.link.indexOf("flickr") >= 0 || item.link.indexOf("tumblr") >= 0 || item.link.indexOf("instagram") >= 0) && <video
                                    src={`${item.mediaUrl}`}
                                    className="tb_01_post_video__"
                                    autoPlay={true}
                                    muted={true}
                                    loop={1}
                                    onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e) }}
                                    data-type="video"
                                    data-stories={item.stories}
                                    data-network={item.networkId}
                                    data-filter-id={item.filterId}
                                    data-link={item.link}
                                    data-load={0}
                                    data-tb={newApi.wall.UserDetail.db_table}
                                    data-wall-id={newApi.wall.Wall.id} data-owner-id={newApi.wall.Wall.owner} data-item-id={item.id}
                                ></video>
                            }

                        </div>

                    </div>
                        : null
        }</ErrorBoundary>
    }
}
export default memo(AtmosItemRender);