import React, { useState, Component } from 'react';



const uploadImg = "https://images.pexels.com/photos/34153/pexels-photo.jpg?auto=compress&cs=tinysrgb&h=350";

class AddCaption extends Component {
    state = {
        uploadHide: true
    }

    handleHide = event => this.setState({ uploadHide: false })

    render() {
        const { uploadHide } = this.state;
        const { updateImageWithData, hasActive, onClickToUpdateStep, isActiveIndex, isAddMoreActive, addCaption, item, onClickToUpdateMoreStep } = this.props
        return (
            <div className='add_caption_tab'>
                <div className='add-text'>
                    <input type="text" className="input-fileld" placeholder='Add your caption here....' onChange={addCaption(isActiveIndex)} value={(item && item.caption) ? item.caption : ""} />
                    {item.error ? <div className='error-text'>* Please add caption.</div> : null}
                </div>
                <div className='next-step' style={item && isAddMoreActive ? { display: 'flex', justifyContent: 'space-between' } : null}>
                    {item && isAddMoreActive ? <button type='submit' className='btn-primary' onClick={onClickToUpdateMoreStep(2)}>Skip</button> : null}
                    <button type='submit' className='btn-primary' id="tb_onsite_btn_id" onClick={onClickToUpdateStep(2)}>Next</button>
                </div>
            </div>
        )
    }
}

export default AddCaption;