import queryString from 'query-string'
import HttpClient from "./http-client";
import { STUDIO_API_URL, STUDIO_QUOTES_DATA_URL, GET_WEATHER_CONTENT_INFO, GET_WEATHER_LOCATION_INFO, GET_LEADERBOARD } from './api'


export const getPreviewByWallID = (userID) => {
    let sceneID = ``;
    const parsed = queryString.parse(window.location.search);
    if (parsed.sceneID && parseInt(parsed.sceneID) > 0) sceneID = `/${parseInt(parsed.sceneID)}`
    return new HttpClient().get(`${STUDIO_API_URL}/${userID}${sceneID}`)
};

export const GET_QUOTES = (name) => {
    return new HttpClient().get(`${STUDIO_QUOTES_DATA_URL}/${name}`)
};

export const sceneDraggable = (scenes) => {
    let data = [];
    if (scenes && scenes.length > 0) {
        const filterData = scenes.filter((item) => item.status == 1);
        data = filterData.map((item) => {
            return { draggbleID: `dragg_${item.mixContentType}`, ...item, sceneContent: emptySceneReStructured(item.sceneContent, item.partitions) }
        })
    }
    return data;
}
const emptySceneReStructured = (obj, partitions) => {
    try {
        const getPart = convertObjectToArray(partitions);
        const getSceneContent = convertObjectToArray(obj);
        let data = [];
        if (getPart && getPart.length > 0) {
            getPart.map((item, index) => {
                if (getSceneContent && getSceneContent.length && getSceneContent[index] && Object.keys(getSceneContent[index]).length) data.push(getSceneContent[index])
                else data.push({ "name": item, "data": [] })
            })
        }
        return data;
    }
    catch (ex) {
        return convertObjectToArray(obj);
    }
};

const convertObjectToArray = (obj) => {
    let result = Object.keys(obj).map((key) => obj[key]);
    return result;
};

export const getWeatherForcasteByLocation = (locationID, language, unitType) => {
    return new HttpClient().get(`${GET_WEATHER_CONTENT_INFO}/${locationID}/${language}/${unitType == "17" ? true : false}`)
};

export const getWeatherSpecificByLocation = (locationID) => {
    return new HttpClient().get(`${GET_WEATHER_LOCATION_INFO}/${locationID}`)
};

export const getLeaderboard = (data) => {
    const { wallId, leader, rows, startDate, endDate, feedList } = data
    var queryFeedList = [];
    if (feedList && feedList.length > 0) feedList.map((item) => queryFeedList.push(item.id))
    return new HttpClient().get(`${GET_LEADERBOARD}/${wallId}/${queryFeedList.join()}?leader=${leader}&rows=${rows}&startDate=${startDate}&endDate=${endDate}`)

}