import React, { PureComponent } from "react";
import Marquee from 'react-double-marquee';

export default class BottomTicker extends PureComponent {

  render() {
    const { newApi, UgcSettings } = this.props;


    const ugcStyle = (UgcSettings && UgcSettings.onsite_status) && UgcSettings.onsite_overlay_poisition == "2" ? { marginBottom: `${UgcSettings.onsite_overlay_height}px` } : {}

    return <div id="wtickerBottom" className="headerTicker tickerBottom" style={{ display: newApi.wall.CustomTicker.status === 1 && newApi.wall.CustomTicker.ticker_position === "2" ? '' : "none", ...ugcStyle }}>
      <div className={`tickerTitleView ${newApi.wall.CustomTicker.tickerType === 3 ? 'ticker3titleView' : ''}`} style={{ display: newApi.wall.CustomTicker.tickerType == 2 ? "none" : "flex", backgroundColor: newApi.wall.CustomTicker.title_backgroud_color, height: newApi.wall.CustomTicker.tickerType == 1 ? newApi.wall.CustomTicker.ticker_height : (newApi.wall.CustomTicker.ticker_height - 10) < 0 ? newApi.wall.CustomTicker.ticker_height : (newApi.wall.CustomTicker.ticker_height - 10) }}>
        <h1 className={`setFont  sGFfonte-${newApi.wall.CustomTicker.title_font_varient}`} style={{ fontFamily: newApi.wall.CustomTicker.title_css_font, fontWeight: 500, fontSize: (newApi.wall.CustomTicker.ticker_height < 40 && newApi.wall.CustomTicker.title_font_size > 24 ) ? 20 : newApi.wall.CustomTicker.title_font_size , color: newApi.wall.CustomTicker.title_font_color }}>{newApi.wall.CustomTicker.title}</h1>
      </div>
      <div className="tickerSubtitleView" style={{ backgroundColor: newApi.wall.CustomTicker.subTitle_backgroud_color, height: newApi.wall.CustomTicker.ticker_height, display: newApi.wall.CustomTicker.status === 1 ? '' : "none" }}>


        <marquee scrollamount={newApi.wall.CustomTicker.ticker_speed === 5 ? 5 : newApi.wall.CustomTicker.ticker_speed === 10 ? 10 : 20}>
          <h2 className={`setFont  sGFfonte-${newApi.wall.CustomTicker.subtitle_font_varient}`} style={{ fontFamily: newApi.wall.CustomTicker.subtitle_css_font, display: 'inline-block', fontSize: (newApi.wall.CustomTicker.ticker_height < 40 && newApi.wall.CustomTicker.subTitle_font_size> 24 ) ? 20 :  newApi.wall.CustomTicker.subTitle_font_size, color: newApi.wall.CustomTicker.subTitle_font_color }}>
            {newApi.wall.CustomTicker.subTitle}
          </h2>
        </marquee>

        {/* <Marquee  Marquee speed={newApi.wall.CustomTicker.ticker_speed}>
            <h2 className={`setFont `} style={{ display: 'inline-block', fontSize: newApi.wall.CustomTicker.subTitle_font_size, color: newApi.wall.CustomTicker.subTitle_font_color }}>
              {newApi.wall.CustomTicker.subTitle}
            </h2>
          </Marquee> */}


      </div>
    </div>


  }
}