import React, { useState, Component } from 'react';
import OnSitePost from './elements/onsitePost'




class SubmitPostField extends Component {

    render() {
        const { submitError, submitErrorMessage, wall, onClickToaddMore, onSubmitPostData, upload_image, onClickToRemove, isEdit, onTextName, onTextEmail, email, name, receiveMail, tandc, onUpdatereceiveMail, nameError, emailError, onClickToEdit, apiRequest } = this.props
        //console.log("upload_imageupload_image?upload_image", upload_image)
        return <>

            <div className='box_title'>
                <h2 className='title'>Fill out the following information</h2>
                <p className='sub_heading'><sup>*</sup> Indicates required field</p>
            </div>
            <div className='box_body'>
                <div className='submit-post-main'>
                    {
                        (upload_image && upload_image.length > 0) ? <OnSitePost key={1} upload_image={upload_image} onClickToRemove={onClickToRemove} isEdit={isEdit} onClickToEdit={onClickToEdit} /> : null
                    }
                    <div className='following_information'>
                        <div>
                            <div className='form-group'>
                                <label>YOUR NAME<span>*</span></label>
                                <input type="text" className='input-fileld' placeholder='Enter Your Name' onChange={onTextName} value={name} />
                                {nameError ? <div className='error-text'>* Please enter correct your name.</div> : null}
                            </div>
                            <div className='form-group'>
                                <label>EMAIL ADDRESS<span>*</span></label>
                                <input type="text" className='input-fileld' placeholder='Enter Your Email' onChange={onTextEmail} value={email} />
                                {emailError ? <div className='error-text'>* Please enter correct email.</div> : null}
                            </div>
                            {/* <div className='form-group'>
                                <label className='checkbox'><input type="checkbox" checked={receiveMail} onClick={onUpdatereceiveMail} /><span></span> I want to receive email updates.</label>
                            </div> */}
                            <p className='terms_condition'>By clicking on Submit, you are agreeing to our <a href={wall.UgcSettings.onsite_terms_url} target="_blank">terms and conditions *</a></p>
                            {submitError ? <div className='error-text submit-error'>* {submitErrorMessage ? submitErrorMessage : upload_image && upload_image.length == 0 ? `Please include atleast one post.` : `Please check complete details.`}</div> : null}
                            <div className='form_footer'>
                                <div className='add_more'>
                                    <button className='btn-addmore' onClick={onClickToaddMore}>
                                        + Add More Post
                                    </button>
                                    <span><span>*</span> Max 20 Posts</span>
                                </div>
                                <div className='submit_btn'>

                                    <button className={`btn-primary ${apiRequest ? `submit_pic_spinner` : ``}`} onClick={onSubmitPostData}>SUBMIT POST</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>

    }
}

export default SubmitPostField;