import React, { PureComponent, Fragment, Suspense, memo } from "react"
import moment from 'moment'
import Iframe from 'react-iframe';
import ReactPlayer from "react-player"
import FallingColumnForPost from '../../../sepcomponents/FallingColumnForPost'
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP } from '../../../../utils'
import ErrorBoundary from '../../../../errorBoundary'
import { CloudUrl } from "../../../../constants";
import AuthorAvatar from '../../../sepcomponents/AuthorAvatar'

export default class FallingCard extends React.Component {
    componentDidUpdate() {
        //window.dispatchEvent(new Event('resize'))
    }
    state = {
        isAuthorImagevalid: true
    }
    render() {
        const { item, newApi, networkData, activeIndex, columnsId } = this.props;
        return <ErrorBoundary> <div id={`tb_wall_card_wrap__${columnsId}_${parseInt(activeIndex) + 1}`} className="tb_wall_card_wrap__" data-id={activeIndex} data-active={activeIndex == 0 ? 1 : 0} style={{ padding: newApi.wall.Personalization.padding / 2 }}>
            <div id={`post_id-${item.id}`} className="tb_wall_card_in__" style={{ backgroundColor: newApi.wall.ThemeRule.cardColor }}>
            <div className={`tb_wall_card_media___ media_holder${item.id}`}>
                    {
                        (item.type === 2 || item.type === 4) ? <div className="card_img__01">
                            <img className="card_img_02" width="400" height="400"
                                src={item.postFileNew}
                                alt=""
                                onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)}
                                data-filter-id={item.filterId}
                                data-tb={newApi.wall.UserDetail.db_table}
                                data-link={item.link} data-load="0" data-network={networkData.id} data-postid={item.id} data-wall-id={newApi.wall.Wall.id}  data-owner-id={newApi.wall.Wall.owner} data-item-id={item.id} data-stories={item.stories} /></div> :
                            (item.type == 1) ? <></> :
                                (item.type === 3 || item.type === 5) ?

                                    item.link && (item.link.indexOf("youtube") >= 0 || item.link.indexOf("vimeo") >= 0) ?
                                        <div className="card_img__01">
                                            <img className="card_img_02" width="400" height="400"
                                                data-stories={item.stories}
                                                src={item.postFileNew}
                                                alt=""
                                                onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)}
                                                data-filter-id={item.filterId}
                                                data-tb={newApi.wall.UserDetail.db_table}
                                                data-link={item.link} data-load="0" data-network={networkData.id} data-postid={item.id} data-wall-id={newApi.wall.Wall.id} data-owner-id={newApi.wall.Wall.owner} data-item-id={item.id} />
                                            <div className="tb_fc_play_icon">
                                                <img className="tb_fc_play_icon001" src={item.networkId === 7 ? `${CloudUrl}/images/youtube-play.svg` : `${CloudUrl}/images/play.svg`}
                                                    alt="icon" />
                                            </div>
                                        </div>
                                        // : 
                                        // (item.link.indexOf("tumblr") >= 0 || item.link.indexOf("soundcloud") >= 0 || item.link.indexOf("linkedin") >= 0) ?
                                        // <div className="card_video__01">
                                        //     <Iframe
                                        //         className="tb_post_vid_005"
                                        //         frameBorder="0"
                                        //         allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                        //         src={`${item.mediaUrl}&mute=1&loop=1&autoplay=1`}
                                        //         allowFullScreen="true"
                                        //         style={{ width: "100%!important" }}
                                        //     ></Iframe>
                                        // </div>
                                        : item.link && (item.link.indexOf("facebook") >= 0) ?
                                            <div className="card_video__01">
                                                <ReactPlayer poster={item.postFileNew} style={{ display: 'contents', width: `${0.3 * 16}vw`, height: `${0.3 * 9}vw` }} className="tb_post_vid_005" url={item.mediaUrl} playing loop muted />
                                            </div>
                                            :
                                            <div className="card_video__01">
                                                {item.link && (item.networkId && item.networkId == 30) && item.link.indexOf("vk") >= 0 ?
                                                    <Iframe
                                                        className="tb-detail-image-iframe"
                                                        frameBorder="0"
                                                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                        src={`${item.mediaUrl}&autoplay=1&mute=1`}
                                                        allowFullScreen="true"
                                                        style={{ width: "100%!important" }}
                                                        data-filter-id={item.filterId}
                                                        data-stories={item.stories}
                                                        data-type="video"
                                                        data-network={item.networkId}
                                                        data-link={item.link}
                                                        data-item-id={item.id}
                                                        data-load={0}
                                                    ></Iframe> :
                                                    <video src={item.mediaUrl} width="500" height="500" className="tb_post_vid_005" poster={item.postFileNew} autoPlay loop muted onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e) }} data-type="video" data-stories={item.stories}
                                                        data-network={networkData.id}
                                                        data-filter-id={item.filterId}
                                                        data-link={item.link}
                                                        data-postid={item.id}
                                                        data-load={0}
                                                        data-tb={newApi.wall.UserDetail.db_table}
                                                        data-wall-id={newApi.wall.Wall.id}
                                                        data-owner-id={newApi.wall.Wall.owner} data-item-id={item.id} />}
                                            </div>
                                    : null

                    }
                </div> 
                <div className="tb_wall_card_content___">
                    <div className="tb_wall_card_conent__001">
                        <div className="tb_post_author__">
                            <div className="tb_post_author_001_" style={{ backgroundImage: `url(${item.author.picture})`, display: (newApi.wall.Personalization.postAuthor == 0 || item.author.isInstaUser) ? 'none' : '' }}>

                                {this.state.isAuthorImagevalid && < img className="img-fluid" style={{ display: ("" === 0 || item.author.isInstaUser) ? 'block' : 'block' }} src={item.author.picture}
                                    onError={(e) => {
                                        this.setState({
                                            isAuthorImagevalid: false
                                        });
                                        //console.clear()
                                    }}
                                    alt="" />}

                                {!this.state.isAuthorImagevalid && <AuthorAvatar style={{ display: (newApi.wall.Personalization.postAuthor === 0 || item.author.isInstaUser) ? 'none' : 'flex' }} username={item.author.name} backgroundColor={networkData.color} color={newApi.wall.ThemeRule.fontColor} />}



                            </div>
                            <div className="tb_post_userinfo__">
                                <div className={`tb_post_username__  sGFfonte-${newApi.wall.ThemeRule.font_varient}`} style={{ fontFamily: newApi.wall.ThemeRule.css_font, display: (newApi.wall.Personalization.postAuthor == 0 || item.author.isInstaUser) ? 'none' : '', color: newApi.wall.ThemeRule.authorColor }}>{item.author.name ? item.author.name : ''}</div>
                                <div className="tb_post_002">
                                    <div className={`tb_post_user_003   sGFfonte-${newApi.wall.ThemeRule.font_varient}`} style={{ fontFamily: newApi.wall.ThemeRule.css_font, display: (newApi.wall.Personalization.postAuthor == 0 || item.author.isInstaUser) ? 'none' : '', color: newApi.wall.ThemeRule.authorColor }} >{item.author.username}</div>
                                    <div className="tb_seprate_dot__" style={{ display: newApi.wall.Personalization.postAuthor == 0 || newApi.wall.Personalization.postTime == 0 || item.author.isInstaUser ? 'none' : '', color: newApi.wall.ThemeRule.authorColor }}></div>
                                    <div className={`tb_post_time_001  sGFfonte-${newApi.wall.ThemeRule.font_varient}`} style={{ fontFamily: newApi.wall.ThemeRule.css_font, display: newApi.wall.Personalization.postTime == 0 || item.author.isInstaUser ? 'none' : '', color: newApi.wall.ThemeRule.authorColor }} data-livestamp={item.createdAt}>{moment(new Date(item.createdAt * 1000)).fromNow()}</div>
                                </div>
                            </div>
                            <div className="tb_card_social_001">
                                <div className={networkData.id == 20 ? `tb_icon fa ${networkData.icon}` : `fa ${networkData.icon}`}
                                    style={{ color: newApi.wall.ThemeRule.iconType === 1 ? newApi.wall.ThemeRule.iconColor : null }}
                                ></div>
                            </div>
                        </div>
                        <div className="tb_post_content__05">
                            {item.rating && item.rating != 0 ?
                                <div className="tb_post_cont_rating">
                                    <img src={`${CloudUrl}/rating/${item.networkId}/${item.rating}.png`} alt="rating" />
                                </div>
                                : null}
                            <div className={`tb_post_cont_in__001 ${newApi.wall.Personalization.textDecorate === 1 && item.type == 1 ? 'decorateTxt' : ''}  sGFfonte-${newApi.wall.ThemeRule.font_varient}`} style={{ fontFamily: newApi.wall.ThemeRule.css_font, display: newApi.wall.ThemeRule.hideContent == 1 && item.type != 1 ? 'none' : '', fontSize: item.fontSize, color: newApi.wall.ThemeRule.fontColor }}>
                                <FallingColumnForPost item={item} network={networkData} wall={newApi.wall} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </ErrorBoundary>
    }

}
