import React, { PureComponent } from "react";
//import 'font-awesome/css/font-awesome.min.css';
import './css/web-font.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import WaterFall from './WaterFall'
import { getShowLoadingScreen } from './actions/action'
import { GET_LOADER_API_PATH } from './constants'
import { WINDOW_ON_SCRIPT, WINDOW_PATH_URL, IS_EDITOR, IS_CHECK_WALL_KEY, getWallKey } from './utils'
import queryString from "query-string"
import ErrorComponent from './components/errors/error'
class App extends PureComponent {
    state = {
        wallID: null,
        themeIdwall: null,
        isLoad: { loader_type: null, loader_color1: null, loader_color2: null, newLoaderActive: false },
        isActiveSocket: null,
        isError: false,
        message: "Some Error Occurred."
    }
    componentDidMount() {
        WINDOW_ON_SCRIPT();
        this.onLoadUrl();
    }
    onLoadUrl = () => {
        if (window.wallID) {
            let isActiveSocket = this.onTrackSocketWithEditor();
            this.setState({ wallID: window.wallID, isActiveSocket }, () => this.onGetLoader())
        }
        else {
            const windowHref = window.location.href;
            if (windowHref.includes('display.taggbox.com')) {
                let wallid = window.location.href.split("https://display.taggbox.com/")[1];
                if (wallid.includes('/editor')) {
                    wallid = wallid.replace("/editor", "");
                }
                if (wallid.includes("?")) {
                    wallid = wallid.split("?")[0]
                }
                let themeid = queryString.parse(window.location.search);
                let isActiveSocket = this.onTrackSocketWithEditor();
                this.setState({ wallID: wallid, themeIdwall: themeid, isActiveSocket }, () => this.onGetLoader())
            }
            else if (windowHref.includes('display-demo.taggbox.com')) {
                let wallid = window.location.href.split("https://display-demo.taggbox.com/")[1];
                if (wallid.includes('/editor')) {
                    wallid = wallid.replace("/editor", "");
                }
                let themeid = queryString.parse(window.location.search);
                let isActiveSocket = this.onTrackSocketWithEditor();
                this.setState({ wallID: wallid, themeIdwall: themeid, isActiveSocket }, () => this.onGetLoader())
            }

            else {
                let checkIndex = WINDOW_PATH_URL.split('/index')
                let wallID = null;
                if (checkIndex.length > 1) {
                    wallID = checkIndex[1].split('/')[1];
                    let isActiveSocket = this.onTrackSocketWithEditor();
                    this.setState({ wallID: wallID, isActiveSocket }, () => this.onGetLoader())
                }
                else {
                    let wPath = WINDOW_PATH_URL.split('/s/')
                    let sSplit = wPath[1].split('/');
                    if (sSplit.length > 1) {
                        if (sSplit[1] === "editor") wallID = sSplit[0];
                        else wallID = sSplit[1];
                    }
                    else if (sSplit.length === 1) wallID = sSplit[0];
                    let isActiveSocket = this.onTrackSocketWithEditor();
                    let themeid = queryString.parse(window.location.search)
                    this.setState({ wallID: wallID, themeIdwall: themeid, isActiveSocket }, () => this.onGetLoader())
                }
            }
        }

    }
    onTrackSocketWithEditor = () => {
        if (IS_EDITOR) {
            let trackEditor = WINDOW_PATH_URL.split('/editor/')
            if (trackEditor.length > 1) {
                let trackSocketOnOff = trackEditor[1].split('/');
                if (trackSocketOnOff.length > 1) return trackSocketOnOff[1];
                else if (trackSocketOnOff.length === 1) return trackSocketOnOff[0];
                else return null;
            }
            else return null;
        }
        else return null;
    }

    onGetLoader = () => {
        const { wallID, themeIdwall, isLoad } = this.state;

        let tbPostsCount = {}
        if (themeIdwall && themeIdwall.tb_posts_count && themeIdwall.tb_posts_count != null) {
            let tbPostCount = themeIdwall.tb_posts_count;
            if (tbPostCount.includes("?digitalsignage=1")) {
                tbPostCount = tbPostCount.replace("?digitalsignage=1", "")
            }
            tbPostsCount = { "tb-post-count": tbPostCount }
        }
        let wallResuestKey = {}
        if (!IS_EDITOR && IS_CHECK_WALL_KEY) {
            wallResuestKey = { wallKey: getWallKey() }
        }
        else if (IS_EDITOR) {
            wallResuestKey = { editor: 1 }
        }
        const options = (themeIdwall && themeIdwall.themeId && themeIdwall.themeId != null) ? {
            headers: {
                'Access-Control-Allow-Origin': '*',
                themeId: themeIdwall.themeId.substr(0, 2),
                ...tbPostsCount,
                ...wallResuestKey
            }
        } : IS_CHECK_WALL_KEY ? {
            headers: {
                'Access-Control-Allow-Origin': '*',
                ...wallResuestKey
            }
        } : {}

        getShowLoadingScreen(`${GET_LOADER_API_PATH}${wallID}`, options)
            .then(res => {
                const { loader_type, loader_color1, loader_color2 } = res.data;
                let isResLoad = { loader_type: loader_type, loader_color1: loader_color1, loader_color2: loader_color2, newLoaderActive: (loader_type === 0 && isLoad.newLoaderActive === false) ? true : false }
                this.setState({ isLoad: isResLoad })
            }).catch((error) => {
                const { data } = error.response;
                if (data && data.message) {
                    this.setState({ isError: true, message: data.message })
                }
                else {
                    this.setState({ isError: true })
                }


            })
    }

    render() {
        const { wallID, isLoad, isActiveSocket, isError, message } = this.state;
        const { wallErrorCode } = this.props
        return wallID != null && isLoad.loader_type != null ? <WaterFall mainUrl={WINDOW_PATH_URL} {...this.props} socialWallId={wallID} isLoad={isLoad} isActiveSocket={isActiveSocket} /> : isError ? <ErrorComponent message={message} wallErrorCode={wallErrorCode} /> : null
    }
}

export default App