import React, { Component } from "react";
import StandardClockPreview from '../standard'
import ClassicClockPreview from '../classic'

class ClockPreview extends Component {

    render() {
        const { app_data,iframeID } = this.props;
      
        return app_data && Object.keys(app_data).length > 0 ? <>{app_data.timeLayout == 0 ? <ClassicClockPreview  {...app_data} iframeID={iframeID}/> : app_data.timeLayout == 1 ? <StandardClockPreview {...app_data} iframeID={iframeID}/> : null}</> : null
       
    }
}

export default ClockPreview
