
import React, { Component } from "react";
import ModernWaterFall from "./components/themes/ModernWaterFall";
import HueCardWaterFall from './components/themes/HueCardWaterfall';
import PolaroidWaterFall from './components/themes/PolaroidWaterfall'
import SlideShow from "./components/themes/SlideShow";
import RoundEdge from './components/themes/RoundEdge'
import TickerWall from './components/themes/TickerWall'
import FallingColumns from './components/themes/FallingColumns/swiper'
import Atmosphere from './components/themes/Atmosphere'
import UpgradeScreenPopupTicker from './components/popUp/UpgradeScreenPopupTicker'
import TaggboxBrandingTicker from './components/taggboxBrandingTicker'
import GridTheme from './components/themes/grid'
import Mashup from './components/themes/Mashup'
import Conversation from "./components/themes/Conversation";

import { SPEED_UP_PIXEL, customWorklength } from './utils';


export default class ThemeRoutes extends Component {
    constructor(props) {
        super(props)
        this.state = {
            dataItems: [],
            getHeight: 0,
            wHeight: window.innerHeight
        }
    }
    componentDidMount() {
        window.addEventListener("resize", () => {
            this.setState({ wHeight: window.innerHeight })
        })
        const { newItems } = this.props;
        this.setState({ dataItems: newItems }, () => this.onLoad())
    }

    onLoad = () => {
        const { newItems, newApi, BannerImageHeight } = this.props;
        let newHeight = newApi.wall.CustomBanner && Object.keys(newApi.wall.CustomBanner).length > 0 ? (newApi.wall.CustomBanner.status === 1 ? newApi.wall.CustomBanner.banner_height : 0) + (newApi.wall.CustomTicker.status === 1 ? newApi.wall.CustomTicker.ticker_height : 0) : 0;
        if (newApi.wall.BannerImage && newApi.wall.BannerImage.status === 1) {
            newHeight += BannerImageHeight;
        }
        this.setState({ dataItems: newItems, getHeight: newHeight })
    }
    componentWillReceiveProps(nextProps) {
        const { newItems } = nextProps;
        this.setState({ dataItems: newItems }, () => this.onLoad())
    }

    // shouldComponentUpdate(nextProps, nextState) {
    //     if (nextProps.newApi.wall.Personalization.signageTheme === 26) {
    //         return nextState.dataItems !== nextProps.newItems;
    //     }
    //     else {
    //         if (nextState.dataItems = nextProps.newItems) return true;
    //         else if (nextState.wHeight !== this.state.wHeight) return true;
    //         else return true;
    //     }

    // }

    dataSortingForSlackTheme = (newData) => {
        const data = newData.sort((a, b) => {
            return a.createdAt - b.createdAt;
        });
        return data
    }
    render() {
        const { newApi, width, postWidth, newItems, itemCount, see, dismissPopUp, poweredByTag, socketData, onSocketRemoveData, isSocketAction, isSocketRemoveIDs } = this.props;
        const { getHeight, wHeight } = this.state;
        // if (newApi.wall.Personalization.wallId == 122464) {
        //     let row = newItems.length / parseInt(window.innerWidth / postWidth)
        //     let actu = parseInt(row) * parseInt(window.innerWidth / postWidth)
        //     // let remainingPost = (newItems.length - actu)
        //     // //console.log("rowrowrow", row, newItems.length, parseInt(window.innerWidth / postWidth), actu, newItems.length - actu, remainingPost)
        //     newItems.length = actu
        // }
        if (newApi.wall.Personalization.wallId == 122464) {
            newItems.length = customWorklength(newItems, postWidth)
        }

        return <>{

            (newApi.wall.Personalization.signageTheme === 26 || newApi.wall.Personalization.signageTheme === 58) ? <div className="themeModrnWatrFall">
                <ModernWaterFall newItems={newItems} newApi={newApi} width={width} postWidth={postWidth} scrollSpeed={SPEED_UP_PIXEL(newApi.wall.Personalization.scrollDelay)} />
            </div>

                : (newApi.wall.Personalization.signageTheme === 59) ? <Conversation newItems={this.dataSortingForSlackTheme(newItems)} newApi={newApi} width={width} postWidth={postWidth} itemCount={itemCount} />

                    : (newApi.wall.Personalization.signageTheme === 5 || newApi.wall.Personalization.signageTheme === 56) ? <div className="themeClassicWatrFall">
                        <HueCardWaterFall newItems={newItems} newApi={newApi} width={width} postWidth={postWidth} itemCount={itemCount} />
                    </div>
                        : newApi.wall.Personalization.signageTheme === 29 ? <GridTheme newItems={newItems} newApi={newApi} width={width} postWidth={postWidth} itemCount={itemCount} />
                            : (newApi.wall.Personalization.signageTheme === 38 || newApi.wall.Personalization.signageTheme === 57) ? <div className="themePolaroid">
                                <PolaroidWaterFall newItems={newItems} newApi={newApi} width={width} postWidth={postWidth} />
                            </div>

                                : newApi.wall.Personalization.signageTheme === 46 ? <div className="">
                                    <RoundEdge newItems={newItems} newApi={newApi} width={width} postWidth={postWidth} />
                                </div>

                                    : newApi.wall.Personalization.signageTheme === 21 || newApi.wall.Personalization.signageTheme === 35 || newApi.wall.Personalization.signageTheme === 37 ? <div className="themeSlidShows zoomSlidShow" style={{ height: (wHeight) - getHeight }}>
                                        <SlideShow
                                            newItems={newItems} newApi={newApi} width={width}
                                            height={(wHeight) - getHeight}
                                            postWidth={postWidth}
                                            timer={(newApi.wall.Personalization.slideDelay * 1000)}
                                        />
                                    </div>

                                        : newApi.wall.Personalization.signageTheme === 51 ? <div className="themeTickerWall">

                                            <TickerWall newItems={newItems} newApi={newApi} width={width} postWidth={postWidth} />
                                            <UpgradeScreenPopupTicker {...this.props} />
                                            <TaggboxBrandingTicker see={see} newApi={newApi} dismissPopUp={dismissPopUp} poweredByTag={poweredByTag} />
                                        </div>

                                            :
                                            newApi.wall.Personalization.signageTheme === 40 ? <div className="themeModrnWatrFall">
                                                <FallingColumns newItems={newItems} newApi={newApi} socketData={socketData} onSocketRemoveData={onSocketRemoveData} />
                                            </div>
                                                :

                                                newApi.wall.Personalization.signageTheme === 44 ? <div className="">
                                                    <Atmosphere newItems={newItems} newApi={newApi} socketData={socketData} isSocketAction={isSocketAction} isSocketRemoveIDs={isSocketRemoveIDs} />
                                                </div>

                                                    : newApi.wall.Personalization.signageTheme === 36 ? <div className="">
                                                        <Mashup newItems={newItems} newApi={newApi} timer={(newApi.wall.Personalization.slideDelay * 1000)} />
                                                    </div>


                                                        :

                                                        < div >
                                                            error</div>

        }</>
    }
}
