import React, { PureComponent, Fragment, Suspense, memo } from "react"
import $ from 'jquery'
import { v4 as uuidv4 } from 'uuid';
import Masonry from "react-masonry-component";
import '../../../css/styleGrid.css'
import { findNetworkDataFromWebFilters } from './../../../utils'
import CardItem from './cardItem'


const domain = 'https://taggbox.com';
let iData = 0;
var $tiw = 0;
var $tih = 0;
const baseUrl = "https://app.taggbox.com/display/";
class GridTheme extends PureComponent {
    state = {
        rootWidth: document.getElementById('root').clientWidth,
        wHeight: window.innerHeight,
        height: 0,
        switcher: 0,
        aiy: 0,
        newPersons: [],
        count: 0,
        loading: true,
        scrollheight: 0,
        postWidth: 0,
        cardNumber: 0,
        newItems: []
    }
    onChangeOverlayBG = () => {
        var overClass = ["bg1", "bg2", "bg3", "bg4"];
        var count = 0;
        $(".grid_overlay").removeClass("bg1 bg2 bg3 bg4").addClass(overClass[count]);
        setInterval(() => {
            $(".grid_overlay").removeClass("bg1 bg2 bg3 bg4").addClass(overClass[count]);
            count >= 3 ? count = 0 : count += 1;
        }, 5000)

    }
    componentDidMount() {
        /* FOR POST RESPONSIVE */
        this.onResponsiveTheme();
        window.addEventListener("resize", () => {
            this.setState({ rootWidth: document.getElementById('root').clientWidth, wHeight: window.innerHeight }, () => this.onResponsiveTheme())
        })
        /* ==============FOR MOUSE SCROLL STOP============== */
        window.addEventListener("wheel", e => e.preventDefault(), { passive: false })
        this.onLoadTheme();
    }
    onLoadTheme = () => {
        const { newItems, newApi } = this.props;
        if (newApi && newApi.postData && newApi.postData.length > 0) {
            this.setState({ newItems: newApi.postData }, () => {
                $("body").addClass("gridThemeBody");
                /// this.onChangeOverlayBG();
                this.onSetShowPosition();
                this.onImageFlip();
            })

        }
    }

    componentDidUpdate(prevProps, prevState) {
        const { newItems } = this.props;
        if (prevState.newItems != newItems) {
            this.setState({ newItems: newItems }, () => {
                $("body").addClass("gridThemeBody");
                this.onChangeOverlayBG();
                this.onSetShowPosition();
                this.onImageFlip();
            })
        }
    }
    removeHighLightPost = () => {
        var twt = $("#sPosts").find('.tweet.show');
        $("#sPosts").find('.tweet').find('video').remove();
        $("#sPosts").find('.tweet').find('iframe').remove();
        twt.removeClass('show');
        return;
        twt.find('.selected-item').css({
            "margin-top": 0,
            "margin-left": 0,
            "top": 0,
            "left": 0,
            "height": '100%',
            "width": '100%',
        });
        twt.find('.content-outer').css("left", 0);
    }

    applyBoldnessGrid = (e) => {
        e.find(".tweet").not(".autoBold").each(function () {
            if (void 0 !== $(this).find(".dataDiv").html()) {
                var e = $(this).find(".dataDiv").html();
                var t = $.parseJSON(e)
                var a = $(this).find(".message-text a").html();
                if (null != t && 1 == t.personalization.hashtag_highlight && void 0 !== a) {
                    var i = t.personalization.hashtag_color;
                    if (Number(t.personalization.hashtag_feed)) {
                        var o = new RegExp(t.hash.hashString, "ig");
                        a = a.replace(o, '<span class="feed_hashtag" style="color:' + i + ';font-weight:bold;">' + t.hash.hashString + "</span>")
                    }
                    if (Number(t.personalization.hashtag_all)) {
                        a = a.replace(/(^|\s)(#[a-z\d-]+)/gi, "$1<span class='gridhashtag' style='color:" + i + ";font-weight:bold;'>$2</span>")
                        $(this).find(".message-text a").html(a)
                        $(this).addClass("autoBold")
                    }
                }
            }
        })
    }

    mouseEnterNow = (elem) => {
        var hideTime = 20000;
        this.removeHighLightPost();
        iData++;
        const { newItems } = this.state;
        var newData = newItems && newItems.length > 0 ? newItems.find(element => element.id == parseInt($(elem).attr('data-id'))) : null
        if ($(elem).attr('data-tweet') == 'video') {
            // var data = $.parseJSON($(elem).find('.dataDiv').html());
            // data.embed = $('<textarea />').html(data.embed).text();

            $(elem).find('.tweet-post-img.tweet-post-video').html(newData.embed);
            var dataVideo = $(elem).find('.tweet-post-img.tweet-post-video').find('video');
            if (dataVideo.length != 0) {
                dataVideo.attr('src', dataVideo.attr('src'));
                dataVideo.attr('muted', true);
                dataVideo.attr('controls', true);
                // if (domain == config.HostName) {
                //     dataVideo.prop({ muted: true, preload: "metadata" });
                // }
                dataVideo.get(0).load();

                dataVideo.attr('autoplay', true);
                $(elem).find('.tweet-post-img.tweet-post-video').find('video')[0].autoplay = true;
            }
            var dataiframe = $(elem).find('.tweet-post-img.tweet-post-video').find('iframe');
            if (dataiframe.length != 0) {
                dataiframe.attr('allow', 'autoplay');
                var videoUrl = dataiframe.attr('src');
                // if (domain == config.HostName) {
                //     videoUrl = videoUrl + '&mute=1';
                // }
                dataiframe.attr('src', videoUrl);
            }
            $(elem).find('.tweet-post-img.tweet-post-video').find("iframe.soundcloudVideo").each(function () {
                // if (domain == config.HostName) {
                //     var widget = new SC.Widget($(this)[0]);
                //     widget.bind(SC.Widget.Events.READY, function () {
                //         widget.setVolume(0);
                //     })
                // }
            })

            $tih = 0;
            $tiw = 0;
        }

        setTimeout(() => $(elem).addClass('show'), 1000)

        var setPosition = function (elem) {
            var popElem = $(elem).find('.selected-item');
            var imageContainerWidth = popElem.find('.tweet-post-img').width();
            var contentContainerWidth = popElem.find('.content-outer').width();
            var popElemWidth = imageContainerWidth + contentContainerWidth;
            var popElemHeight = $(elem).find('.selected-item').height();

            var popElemPosition = popElem.offset();

            var windowWidth = document.getElementById('root').clientWidth;
            var windowHeight = document.getElementById('root').clientHeight;
            var headerHeight = $(".tb_wall_header__").height();
            if (headerHeight) {
                if (windowHeight) {
                    windowHeight = parseInt(windowHeight) - parseInt(headerHeight);
                }
            }
            if (typeof popElemPosition !== "undefined") {
                if ((elem.offsetLeft + popElemWidth) > windowWidth && elem.offsetLeft != 0) {
                    var restWidth = (elem.offsetLeft + popElemWidth) - windowWidth;
                    var extraWidth = windowWidth - elem.offsetLeft + popElemWidth;
                    popElem.css('left', -restWidth);
                }
                if (popElemPosition.top + popElemHeight > windowHeight && popElemPosition.top != 0) {
                    var restHeight = popElemPosition.top + popElemHeight - windowHeight;

                    let selectedItemHeight = popElemHeight;
                    let selectedContainerOffset = document.querySelector(`#${elem.id}`).offsetTop;
                    let completeContainer = document.querySelector("#sPosts").clientHeight;
                    if (selectedContainerOffset > 0) {
                        let checkData = parseInt(completeContainer) - parseInt(selectedContainerOffset);
                        restHeight = parseInt(checkData) - parseInt(selectedItemHeight);


                    }
                    popElem.css('top', -restHeight);
                }
                setTimeout(function () {
                    var popElemPosition = popElem.offset();
                    if (popElemPosition.top < 0) {
                        popElem.css('top', 0);
                    }
                    if (popElemPosition.left < 0) {
                        popElem.css('left', 0);
                    }
                }, 1000);
            }


        }
        var e = $(elem).find(".postAuthor");

        $(elem).find('.selected-item').find('.tweet-post-img img').one('load', function () {
            setPosition(elem);
        });
        setTimeout(function () {
            setPosition(elem);
        }, 2000);

        var timeoutHandle = window.setTimeout(() => {
            this.removeHighLightPost();
            //console.log('hidetimeout');   
        }, hideTime);
    }

    showData = (ik, r) => {
        var tweetLength = $("#sPosts").find('.tweet').not('.tweet-overlay').length;
        var $ti = Math.floor((Math.random() * tweetLength) + 1);
        if (r == 1) {
            this.mouseEnterNow($('#tweet-list').find('.tweet:nth-child(' + $ti + ')').get(0));
        } else {
            this.mouseEnterNow($('#tweet-list').find('.tweet:nth-child(' + ik + ')').get(0));
        }
    }


    onLoadMasonryData = () => {
        var Seconds_from_T1_to_T2, Seconds_Between_Dates; var randomizePost = 0;
        var ik = 1; var t1 = new Date();
        var t2 = new Date(); var dif = 0; var loadedImages = 0;
        var imgLength = $("img.lazy").not('.lazyLoaded').length;
        var allimagesLoadInterval = setInterval(() => {
            t2 = new Date();
            dif = t1.getTime() - t2.getTime();
            Seconds_from_T1_to_T2 = dif / 1000;
            Seconds_Between_Dates = Math.abs(Seconds_from_T1_to_T2);
            if (loadedImages == imgLength || Seconds_Between_Dates > 10) {
                clearInterval(allimagesLoadInterval);
                allimagesLoadInterval = null;
                /*Auot Show Tweet*/
                this.showData(ik, randomizePost);
                var totalTweet29 = $('#tweet-list').find('.tweet').length;
                if (ik > totalTweet29) ik = 1;
                else ik = ik + 1;
                var interval = 10000, value = 0;
                var gridModal = setInterval(() => {
                    this.showData(ik, randomizePost);
                    if (ik > totalTweet29) ik = 1;
                    else ik = ik + 1;
                    //console.log('autoShow');  
                }, interval);
            }
        }, 4000);
    }
    /* ==============FOR Vertical Scroll Loop ============== */
    onResponsiveTheme = () => {
        const { newApi } = this.props;
        const { rootWidth } = this.state;
        let cardNumber = null; let adjustSpacing = null; let spacing = null; let adjustWidth = null;
        const postWidth = newApi.wall.Personalization.minimumPostWidth;
        cardNumber = (rootWidth) / (postWidth);
        let rowCard = Math.trunc(cardNumber);
        spacing = (rootWidth) % postWidth;
        adjustSpacing = spacing / rowCard;
        if (cardNumber >= 1) adjustWidth = postWidth + adjustSpacing;
        else adjustWidth = postWidth - adjustSpacing;
        this.setState({ postWidth: adjustWidth, cardNumber, wHeight: window.innerHeight })
    }
    onSetShowPosition = () => {
        var wh = document.getElementById('root').clientWidth;
        var ww = document.getElementById('root').clientHeight;
        var imgLength = $("img.lazy").not('.lazyLoaded').length;
        var loadedImages = 0;
        var t1 = new Date();
        var t2 = new Date();
        var dif = t1.getTime() - t2.getTime();
        setTimeout(function () {
            $("img.lazy").not('.lazyLoaded').each(function () {
                $(this).hide();
                $(this).attr('src', $(this).attr('data-original'));
                $(this).one("load", function () {
                    $(this).fadeIn();
                    $(this).addClass('lazyLoaded');
                    loadedImages++;
                })
            })
        }, 1000);



        var $tb = $('#tweet-list .tweet').not('.tweet.tweet-overlay');
        var $th = 0;
        var $tw = 0;


        $tb.each(function (index) {
            $(this).attr('class', "tweet");
            $th = $(this).height();
            $tw = $(this).width();
            $tih = $(this).find('.selected-item .image').attr('height');
            $tiw = $(this).find('.selected-item .image').attr('width');
            $(this).attr('i', index);
            var $p = $(this);
            var $position = $p.position();
            if ($position.left < ($tiw - $tw) / 2) {
                $(this).addClass('lt');
            }
            ;
            if ($position.top < ($tih - $th) / 2) {
                $(this).addClass('tt');
            }
            ;
            if ($position.left > (ww - (parseInt($tiw) + 400))) {
                $(this).addClass('rt');
                $(this).attr('data-left', $position.left);
            }
            ;
            //console.log(index, $position.top, (wh - ($tih - $th)/2)); 
            if ($position.top > (wh - ($tih - $th) / 2)) {
                $(this).addClass('bt');
            }
            ;
            if ((wh - $position.top) < $th) {
                $(this).addClass('bbt');
                $(this).attr('data-top', $position.top);
            }
            ;
        });
        this.onLoadMasonryData();
        $('#sPosts #tweet-list').css({ 'min-height': '100vh' });
    }
    onImageFlip = () => {
        var interval = 3000, value = 0;
        let flipti = 0;

        var gridModalFlip = setInterval(() => {
            //var $ti = Math.floor((Math.random() * 50) + 1);   
            flipti = flipti + 1;
            $('#tweet-list .tweet').removeClass("imageFlip");
            $('#tweet-list .tweet:nth-child(' + flipti + ')').addClass("imageFlip");
        }, interval);
    }
    postSizeFun = (itemKey, perPostSize) => {
        var postSize = ''
        const postSizeS = [2, 3, 4, 6, 7, 8, 9, 10, 11, 12, 13, 15, 16, 17, 18, 19, 22, 24, 25, 27, 28, 29, 30, 34, 36, 37, 38, 41, 42, 43, 44, 45, 47, 48, 52, 53, 54, 55, 59, 60, 61, 62, 63, 64, 69, 70, 71, 72, 76, 77, 78, 79, 80, 83, 84, 87, 88, 89, 90, 96, 97, 98, 99, 100, 102, 103, 104, 110, 111, 112, 114, 115, 116, 118, 119, 121, 122, 123, 124, 129, 130, 131, 132, 133, 134, 137, 138, 139, 140, 144, 145, 146, 147, 148, 149, 150];
        const postSizeM = [1, 4, 5, 14, 21, 23, 31, 32, 33, 39, 40, 56, 57, 49, 50, 66, 67, 68, 74, 75, 81, 82, 85, 86, 91, 92, 93, 94, 95, 101, 106, 107, 109, 113, 120, 125, 126, 127, 135, 136, 142, 143];
        const postSizeL = [0, 20, 26, 35, 46, 51, 58, 65, 73, 105, 108, 117, 128, 141];
        if (postSizeS.includes(itemKey)) {
            postSize = perPostSize / 2
        } else if (postSizeM.includes(itemKey)) {
            postSize = perPostSize * 1
        } else if (postSizeL.includes(itemKey)) {
            postSize = perPostSize * 2
        }
        return postSize
    }
    render() {
        const { loading, postWidth, newItems } = this.state;
        let { newApi, rootHeight, rootWidth } = this.props;

        const area = rootHeight * rootWidth;
        const postCount = newApi.postData.length;
        const postSizes = area / postCount;
        const perPostSize = Math.sqrt(postSizes);
        const postSize = perPostSize;
        // console.log("grid theme", newItems.length, newItems)
        return (
            <div class="">
                <div id="sPosts" class={`theme${newApi.wall.Personalization.signageTheme}`} style={{ height: 'inherit', position: 'relative' }}>
                    <Masonry id="tweet-list"
                        className="clearfix" data-nopost="0" style={{ position: 'relative', height: '1020px', minHeight: '100vh' }}
                        elementType={"div"}
                        options={{
                            transitionDuration: 0,
                            itemSelector: '.tweet',

                        }}
                        disableImagesLoaded={false} // default false
                        updateOnEachImageLoad={true} // default false and works only if disableImagesLoaded is false
                    >
                        {newApi.wall.Personalization.animateBackground == 1 ? <div className="tweet tweet-overlay" style={{ position: 'absolute', left: '0px', top: '0px' }}><div className="grid_overlay bg1"></div></div> : null}
                        {
                            newApi && newItems && newItems.map((item, itemKey) => {
                                const networkd = findNetworkDataFromWebFilters(item.networkId, newApi.webFilters);
                                const dataId = uuidv4();
                                return <CardItem key={dataId} itemKey={itemKey} networkd={networkd} item={item} newApi={newApi} personalization={newApi.wall.Personalization} postSize={this.postSizeFun(itemKey, postSize)} />
                            })
                        }
                    </Masonry>
                </div>
            </div>
        )
    }

}

export default memo(GridTheme)